<template>
  <!-- 修改申请状态 -->
  <div>
    <!-- <h3 style="margin: 16px 0">跟进记录</h3> -->
    <div v-if="applyChangedRecord.length">
      <Divider />
      <h3 style="margin: 16px 0" >跟进记录</h3>
      <Scroll height="200">
        <Row style="padding-left: 24px;" v-for="(item, index) in applyChangedRecord" :key="index">
          <Col>状态: {{ applyStatusMap[item.currentApplyStatus] }}</Col>
          <Col>备注: {{ item.remark }}</Col>
          <Col span="8">操作人: {{ item.modifyEmpName }}</Col>
          <Col span="16">操作时间: {{ item.createTime }}</Col>
          <Col span="24"><Divider /></Col>
        </Row>
      </Scroll>
    </div>
    <Divider />
    <h3 style="marginBottom: 16px">跟进维护</h3>
    <Form :label-width="100">
      <FormItem label="状态" required>
        <Cascader
          size="small"
          style="width: 200px;display: inline-block;"
          :data="cascaderList"
          v-model="cascaderValue">
        </Cascader>
      </FormItem>
      <FormItem label="下次跟进时间" required v-if="nextTimeShow">
        <DatePicker
          v-model="formData.nextTime"
          @on-change="timeChange"
          :options="options"
          type="datetime"
          placement="top"
          size="small"
          format="yyyy-MM-dd HH:mm"
          placeholder="请选择"
          style="width: 200px">
        </DatePicker>
      </FormItem>
      <FormItem label="备注" required>
        <Input v-model="formData.remark" placeholder="请输入备注" type="textarea" :minlength="5" style="width: 80%;" />
      </FormItem>
    </Form>
    <Row type="flex" justify="center" :gutter="20">
      <Col><Button type="primary" @click="submit">确定</Button></Col>
      <!-- <Col><Button type="default" @click="cancel">取消</Button></Col> -->
    </Row>
  </div>
</template>

<script>
import { modifyApplyStatus } from '@/api/appointmentApply'
export default {
  name: 'changeApplyStatus',
  props: {
    applyChangedRecord: {
      type: Array
    },
    applyInfo: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data () {
    return {
      options: {
        disabledDate (date) {
          return date && date.valueOf() < Date.now() - 86400000
        }
      },
      nextTime: '',
      nextTimeShow: false, // 是否显示下次跟进时间
      formData: {
        remark: '', // 备注
        applyType: 1, // 1: 试听申请
        preApplyStatus: 0, // 之前状态
        nextFollowUpTime: '' // 下次跟进时间
      },
      applyStatusMap: {
        0: '未跟进',
        1: '已预约',
        2: '跟进结束',
        3: '跟进中'
      },
      applySecondStatusMap: {
        1: '继续跟进',
        2: '未接通',
        3: '无意向',
        4: '已用其他账号预约',
        5: '已转化无需预约',
        6: '无匹配课程'
      },
      cascaderValue: [], // 跟进状态
      // 跟进状态级联列表
      cascaderList: [
        {
          value: 3,
          label: '跟进中',
          children: [
            {
              value: 1,
              label: '继续跟进'
            },
            {
              value: 2,
              label: '未接通'
            }
          ]
        },
        {
          value: 2,
          label: '跟进结束',
          children: [
            {
              value: 3,
              label: '无意向'
            },
            {
              value: 4,
              label: '已用其他账号预约'
            },
            {
              value: 5,
              label: '已转化无需预约'
            },
            {
              value: 6,
              label: '无匹配课程',
              children: [
                {
                  value: 7,
                  label: '无时间匹配'
                },
                {
                  value: 8,
                  label: '无课程匹配'
                }
              ]
            }
          ]
        }
      ]
    }
  },
  watch: {
    cascaderValue (val) {
      if (val && val.length && val[0] === 3) { // 如果是跟进中
        this.nextTimeShow = true
      } else {
        this.nextTimeShow = false
        this.nextTime = ''
        this.formData.nextFollowUpTime = ''
      }
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.formData.preApplyStatus = this.applyInfo.applyStatus
      this.formData.taskIds = this.applyInfo.taskIds

      this.cascaderValue = [
        this.applyChangedRecord.applyStatus,
        this.applyChangedRecord.applySecondStatus,
        this.applyChangedRecord.applyThirdStatus
      ]
      this.formData.applyId = this.applyInfo.id
    })
  },
  methods: {
    timeChange (time) {
      this.formData.nextFollowUpTime = time
    },
    // cancel () {
    //   this.cascaderValue[0] = ''
    //   this.formData.remark = ''
    // },
    async submit () {
      if (!this.cascaderValue[0]) {
        return this.$Message.warning('请选择状态')
      }
      if (!this.formData.remark.trim()) {
        return this.$Message.warning('请填写备注')
      }

      const currentApplyStatus = this.cascaderValue[0] // 一级状态
      const applySecondStatus = this.cascaderValue[1] || null // 二级状态
      const applyThirdStatus = this.cascaderValue[2] || null // 三级状态
      if (currentApplyStatus === 3 && !this.formData.nextFollowUpTime) { // 如果是跟进中
        return this.$Message.warning('请选择下次跟进时间')
      }
      const params = {
        ...this.formData,
        currentApplyStatus,
        applySecondStatus,
        applyThirdStatus
      }

      try {
        await modifyApplyStatus(params)
        this.$Message.success({ content: `修改申请状态成功` })
        this.$emit('success')
      } catch (error) {
        this.$Message.error({
          content: `修改申请状态失败: ${error.message}`,
          duration: 5
        })
      }
    }
  }
}
</script>

<style lang="less" scoped>
/deep/ .ivu-col {
  line-height: 2;
}
</style>
