<template>
  <Form
    :model="formData"
    inline
    :rules="rules"
    class="detail_form"
    ref="form"
  >
    <div class="dist_detail_item" >
      <span style="margin-right: 16px;">接收方</span>
      <FormItem prop="cityId">
        <Select v-model="formData.cityId" filterable @on-change="getAreaList" placeholder="城市：">
          <Option v-for="(item, key) in cityList" :key="key" :label="item.deptName" :value="item.id" />
        </Select>
      </FormItem>
      <FormItem prop="areaId" v-if="acceptType !== 1">
        <Select v-model="formData.areaId" filterable @on-change="getCenterList" placeholder="区域：">
          <Option v-for="(item, key) in areaList" :key="key" :label="item.deptName" :value="item.id" />
        </Select>
      </FormItem>
      <FormItem v-else></FormItem>
      <FormItem prop="centerId" v-if="acceptType !== 1">
        <Select v-model="formData.centerId" filterable @on-change="getAdviserList" placeholder="服务中心：">
          <Option v-for="(item, key) in serverList" :key="key" :label="item.deptName" :value="item.id" />
        </Select>
      </FormItem>
      <FormItem v-else></FormItem>
      <FormItem prop="adviserId" v-if="acceptType === 3">
        <Select v-model="formData.adviserId" filterable placeholder="顾问：">
          <Option v-for="(item, key) in adviserList" :key="key" :label="item.userName" :value="item.identityId" />
        </Select>
      </FormItem>
      <FormItem v-else style="width: 110px;"></FormItem>
      <span style="margin-right: 16px;margin-left: 40px;">{{ distributeType === 1 ? '资源数量':'资源百分比' }}</span>
      <FormItem prop="num" style="width: 80px;">
        <InputNumber v-if="distributeType === 0"
          :precision="0"
          style="width: 80px;" :placeholder="leftNum+''"
          v-model="formData.num" @input="$emit('change')" :max="100" />
        <InputNumber v-else
          :precision="0"
          style="width: 80px;" :placeholder="leftNum+''"
          v-model="formData.num" @input="$emit('change')" :max="leftNum" />
      </FormItem>
      <span style="margin-right: 30px;">{{ distributeType === 0 ? '%' : '人'}}</span>
      <Icon class="icon_delete" type="ios-trash-outline" @click="$emit('delete')" />
    </div>
  </Form>
</template>

<script>
import { queryCrmChildDepartList, getUsersLevelInfo } from '@/api/crm'

export default {
  inject: ['parent'],
  props: {
    distributeType: {
      type: Number, // 1资源百分比 2数量分配
      default: 1
    },
    acceptType: {
      type: Number, // 接受方类型 1城市 2服务中心 3顾问
      default: 0
    },
    isLast: Boolean,
    leftNum: Number // 剩余分配量
  },
  computed: {
    cityName () {
      let filter = this.cityList.filter(item => item.id === this.formData.cityId)[0]
      if (filter) {
        return filter.deptName
      }
      return ''
    },
    centerName () {
      let filter = this.serverList.filter(item => item.id === this.formData.centerId)[0]
      if (filter) {
        return filter.deptName
      }
      return ''
    },
    adviserName () {
      let filter = this.adviserList.filter(item => item.identityId === this.formData.adviserId)[0]
      if (filter) {
        return filter.userName
      }
      return ''
    }
  },
  data () {
    return {
      formData: {
        cityId: null,
        areaId: null,
        centerId: null,
        adviserId: null,
        num: null
      },
      rules: {
        cityId: [{ required: true, message: '内容不能为空' }],
        areaId: [{ required: true, message: '内容不能为空' }],
        centerId: [{ required: true, message: '内容不能为空' }],
        adviserId: [{ required: true, message: '内容不能为空' }],
        num: [
          { required: true, message: '内容不能为空', trigger: 'blur', type: 'number' },
          { validator: this.leftNumValidator, trigger: 'blur' }
        ]
      },
      cityList: [],
      areaList: [],
      serverList: [],
      adviserList: []
    }
  },
  mounted () {
    this.getCityList()
  },
  methods: {
    // 是否超出限制校验
    leftNumValidator (rule, value, cb) {
      const leftNum = this.parent.getLeftNum(this.distributeType)
      if (value > leftNum && this.isLast) {
        cb(new Error('数量超出限额'))
      } else if (value < leftNum && this.isLast) {
        cb(new Error('资源未分配完'))
      }
      return true
    },
    // 最后一个值校验
    validateField () {
      if (this.formData.num > 0) {
        this.$refs.form.validateField('num')
      }
    },
    // 表单校验
    async validate () {
      const valid = await this.$refs.form.validate()
      return valid
    },
    getAreaCodeByCityCode (cityId) {
      const city = this.cityList.filter((item) => item.id === cityId)[0]
      if (city) {
        return city.deptCode
      }
    },
    // 城市
    getCityList () {
      queryCrmChildDepartList({
        departId: Number(10),
        departType: ''
      })
        .then((data) => {
          this.cityList = data.filter((item) => {
            return item.deptType !== 10
          })
        })
        .catch((err) => {
          this.$Message.error({
            content: '获取数据失败：' + err.message
          })
        })
    },
    // 区域
    getAreaList (val) {
      queryCrmChildDepartList({
        departId: val,
        departType: ''
      })
        .then((data) => {
          this.areaList = data
          this.adviserList = []
          this.serverList = []
        })
        .catch((err) => {
          this.$Message.error({
            content: '获取数据失败：' + err.message
          })
        })
    },
    // 顾问
    getAdviserList () {
      getUsersLevelInfo({
        areaCode: this.getAreaCodeByCityCode(this.formData.cityId),
        roleId: 5,
        deptId: this.formData.centerId
      })
        .then((data) => {
          this.adviserList = data
        })
        .catch((err) => {
          this.$Message.error({
            content: '获取数据失败：' + err.message
          })
        })
    },
    // 服务中心
    getCenterList (val) {
      queryCrmChildDepartList({
        departId: val,
        departType: ''
      })
        .then((data) => {
          this.serverList = data
          this.adviserList = []
        })
        .catch((err) => {
          this.$Message.error({
            content: '获取数据失败：' + err.message
          })
        })
    }
  }
}
</script>

<style lang="less" scoped>
.detail_form {
  display: flex;
  border-bottom: 1px solid #f0f0f0;
  padding: 0px 0 25px 0;
  margin-bottom: 16px;
  div.dist_detail_item {
    /deep/.ivu-form-item-content {
      width: 110px;
      flex: 1;
    }
  }
}

.icon_delete {
  font-size: 22px;
  cursor: pointer;
  display: inline-block;
  margin-top: -4px;
}
</style>
