<template>
  <div>
    <el-dialog
      v-if="dialogVisible"
      :visible="dialogVisible"
      :before-close="handleClose"
      title="资源分配"
      width="60%"
      @close="closeDialog"
    >
      <el-tabs
        v-model="activeName"
        @tab-click="handleClick"
      >
        <el-tab-pane
          label="资源分配"
          name="first"
        >
          <p class="selected-info">
            <i class="icon el-icon-info" />
            已选择<span class="stress">{{ selectedNum }}人</span>
          </p>
          <el-form
            :model="addForm"
            class="form"
            label-width="110px"
          >
            <el-form-item label="城市：">
              <el-select
                v-model="addForm.cityId"
                filterable
                @change="getAreaList"
              >
                <el-option
                  v-for="(item, key) in cityList"
                  :key="key"
                  :label="item.deptName"
                  :value="String(item.id)"
                />
              </el-select>
            </el-form-item>

            <el-form-item label="区域：">
              <el-select
                v-model="addForm.areaId"
                filterable
                @change="getCenterList"
              >
                <el-option
                  label="不限"
                  value=""
                />
                <el-option
                  v-for="(item, key) in areaList"
                  :key="key"
                  :label="item.deptName"
                  :value="String(item.id)"
                />
              </el-select>
            </el-form-item>

            <el-form-item label="服务中心：">
              <el-select
                v-model="addForm.centerId"
                filterable
                @change="getAdviserList"
              >
                <el-option
                  label="不限"
                  value=""
                />
                <el-option
                  v-for="(item, key) in serverList"
                  :key="key"
                  :label="item.deptName"
                  :value="String(item.id)"
                />
              </el-select>
            </el-form-item>

            <el-form-item label="顾问：">
              <el-select
                v-model="addForm.adviserId"
                filterable
              >
                <el-option
                  label="不限"
                  value=""
                />
                <el-option
                  v-for="(item, key) in adviserList"
                  :key="key"
                  :label="item.userName"
                  :value="String(item.identityId)"
                />
              </el-select>
            </el-form-item>
          </el-form>
          <div style="text-align: right">
            <el-button @click="closeDialog">取 消</el-button>
            <el-button
              :disabled="submitSuccess"
              type="primary"
              @click="alloc"
            >确 定</el-button>
          </div>
        </el-tab-pane>
        <el-tab-pane
          label="分配记录"
          name="second"
        >
          <el-table
            border
            :data="tableData.records"
            style="width: 100%"
          >
            <el-table-column
              prop="createDate"
              label="操作时间"
              align="center"
            >
              <template slot-scope="{ row }">
                <span>
                  {{ `${row.createDate ? row.createDate : '--'}` }}
                </span>
              </template>
            </el-table-column>
            <el-table-column
              prop="typeName"
              label="接收方"
              align="center"
            />
            <el-table-column
              label="分配状态"
              align="center"
            >
              <template slot-scope="scope">
                <div>
                  <span style="margin-right: 10px">{{
                    scope.row.status === 2 ? '处理完成' : '处理中'
                  }}</span>
                  <a @click="getDispatchDetail(scope.row.id)">查看明细</a>
                </div>
                <div>
                  {{
                    `（成功${scope.row.successNum}人，失败${scope.row.failNum}人）`
                  }}
                </div>
              </template>
            </el-table-column>
          </el-table>
          <el-pagination
            :current-page="tableData.pageNo"
            :page-sizes="[10, 20]"
            :page-size="tableData.pageSize"
            :total="tableData.totalCount"
            layout="total, sizes, prev, pager, next, jumper"
            @size-change="handleSizeChangeOpr"
            @current-change="handleCurrentChangeOpr"
          />
          <el-dialog
            title="分配明细"
            :visible.sync="detailModal"
            append-to-body
            :before-close="closeDia"
            width="60%"
          >
            <div v-if="
                detailList &&
                detailList.records &&
                detailList.records.length > 0
              ">
              <el-timeline :reverse="true">
                <el-timeline-item
                  v-for="(detail, index) in detailList.records"
                  :key="index"
                  :timestamp="detail.createDate"
                >
                  <div>{{ statusMap[detail.status] }}</div>
                  <div>{{ detail.studentName }}</div>
                </el-timeline-item>
              </el-timeline>
              <el-pagination
                :current-page="detailList.pageNo"
                :page-sizes="[10, 20]"
                :page-size="detailList.pageSize"
                :total="detailList.totalCount"
                layout="total, sizes, prev, pager, next, jumper"
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
              />
            </div>
            <div
              v-else
              class="noText"
            >暂无数据</div>
          </el-dialog>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
  </div>
</template>

<script>
import {
  queryCrmChildDepartList,
  batchDispatchStudent,
  getUsersLevelInfo,
  getBatchDispatchLog,
  getBatchDispatchDetail
} from '@/api/crm'
import { mapState } from 'vuex'
export default {
  props: {
    deptid: {
      type: String,
      default: () => {
        return ''
      }
    },
    formData: Object,
    totalNum: Number
  },
  data () {
    return {
      statusMap: {
        1: '处理中',
        2: '分配成功',
        3: '分配失败'
      },
      addForm: {
        cityId: '',
        areaId: '',
        centerId: '',
        adviserId: ''
      },
      areaList: [],
      cityList: [],
      serverList: [],
      submitSuccess: false,
      adviserList: [],
      activeName: 'first',
      tableData: {},
      detailModal: false,
      detailList: [],
      searchData: {
        pageNo: 1,
        pageSize: 10
      },
      operateSearch: {
        pageNo: 1,
        pageSize: 10
      }
    }
  },
  computed: {
    ...mapState('roleRelativeData', ['userInfo']),
    dialogVisible () {
      return this.$store.state.resource.identity === 'consult'
    },
    multipleSelection () {
      return this.$store.state.resource.multipleSelection
    },
    selectedNum () {
      return this.$store.state.resource.multipleSelection.length || this.totalNum
    },
    areaCode () {
      const city = this.cityList.find(c => String(c.id) === this.addForm.cityId)
      if (city) {
        return city.deptCode
      }
      return ''
    },
    empNo () {
      return this.$store.state.user.userInfo.empNo
    },
    confirmHtml () {
      const arr = []
      const city = this.cityList.find(c => String(c.id) === this.addForm.cityId)
      if (city) {
        arr.push(city.deptName)
      }
      const area = this.areaList.find(c => String(c.id) === this.addForm.areaId)
      if (area) {
        arr.push(area.deptName)
      }
      const server = this.serverList.find(c => String(c.id) === this.addForm.centerId)
      if (server) {
        arr.push(server.deptName)
      }
      const adviser = this.adviserList.find(c => String(c.identityId) === this.addForm.adviserId)
      if (adviser) {
        arr.push(adviser.userName)
      }
      return `<p><span>已勾选：</span><span style="color: #2D8cF0"> ${this.selectedNum}人</span></p>
              <p>
                <span>接收方：</span>
                <span style="color: #2D8cF0">
                    ${arr.join(' - ')}
                 </span>
              </p>
              <p>确认分配后将无法撤销，请谨慎处理！</p>`
    }
  },
  watch: {
    dialogVisible: function () {
      this.activeName = 'first'
      queryCrmChildDepartList({
        departId: Number(this.deptid),
        departType: ''
      }).then(data => {
        this.cityList = data.filter(item => {
          return item.deptType !== 10
        })
      }).catch(err => {
        this.$Message.error({
          content: '获取数据失败：' + err.message
        })
      })
      this.submitSuccess = false
    }
  },
  methods: {
    handleClick (tab) {
      if (tab.name === 'second') {
        this.queryLogs()
      }
    },
    queryLogs (params = null) {
      getBatchDispatchLog({
        empNo: this.empNo,
        areaCode: this.userInfo.cityCode,
        cityCode: this.userInfo.cityCode,
        ...this.operateSearch
      }).then(data => {
        this.tableData = data
      }).catch(err => {
        this.$Message.error({
          content: '获取数据失败：' + err.message
        })
      })
    },
    getDispatchDetail (dispatchId) {
      this.currentDispatchId = dispatchId
      this.queryDetailInfo()
    },
    queryDetailInfo () {
      getBatchDispatchDetail({
        dispatchId: this.currentDispatchId,
        ...this.searchData,
        cityCode: this.cityCode
      }).then(data => {
        this.detailList = data
        this.detailModal = true
      }).catch(err => {
        this.$Message.error({
          content: '获取数据失败：' + err.message
        })
      })
    },
    async alloc () {
      if (this.selectedNum === 0) {
        this.$message({
          type: 'warning',
          message: '当前条件下未找到需要分配的学员'
        })
        return
      }
      if (this.addForm.cityId === '') {
        this.$message({
          type: 'warning',
          message: '请选择所要分配的城市'
        })
        return
      }
      if (this.addForm.areaId !== '' && this.addForm.centerId === '') {
        this.$message({
          type: 'warning',
          message: '不能分配到区域，请选择所要分配的服务中心'
        })
        return
      }
      this.$confirm(this.confirmHtml, '重要提示', {
        confirmButtonText: '确认执行分配',
        cancelButtonText: '修改后再提交',
        type: 'warning',
        dangerouslyUseHTMLString: true,
        showClose: false
      }).then(async () => {
        this.submitSuccess = true
        let targetId = this.addForm.cityId
        let type = 1
        if (this.addForm.adviserId) {
          targetId = this.addForm.adviserId
          type = 3
        } else if (this.addForm.centerId) {
          targetId = this.addForm.centerId
          type = 2
        }
        const params = Object.assign({}, this.formData, {
          areaCode: this.areaCode,
          type,
          targetId,
          pyUidList: this.multipleSelection,
          operateEmpNo: this.empNo,
          cityCode: this.userInfo.cityCode,
          // operateIdentityId: this.userInfo.authIdentityId
          belongIdentityId: this.formData.belongIdentityIdByPerson || this.formData.belongIdentityIdByStop,
          targetList: [
            {
              targetId,
              type,
              dispatchCount: this.multipleSelection.length || this.selectedNum
            }
          ]

        })
        delete params.pageNo
        delete params.pageSize
        console.log('params', params)
        try {
          await batchDispatchStudent(params)
          this.$message({
            type: 'success',
            message: '成功分配'
          })
          this.closeDialog()
          this.$emit('refresh')
        } catch (err) {
          this.$Message.error({
            content: '分配失败：' + err.message
          })
        } finally {
          this.submitSuccess = false
        }
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消'
        })
      })
    },
    getAreaList () {
      queryCrmChildDepartList({
        departId: this.addForm.cityId,
        departType: ''
      }).then(data => {
        this.areaList = data
        this.addForm.areaId = ''
        this.addForm.centerId = ''
      }).catch(err => {
        this.$Message.error({
          content: '获取数据失败：' + err.message
        })
      })
    },
    getCenterList () {
      queryCrmChildDepartList({
        departId: this.addForm.areaId,
        departType: ''
      }).then(data => {
        this.serverList = data
        this.addForm.centerId = ''
      }).catch(err => {
        this.$Message.error({
          content: '获取数据失败：' + err.message
        })
      })
    },
    getAdviserList () {
      getUsersLevelInfo({
        areaCode: this.areaCode,
        roleId: 5,
        deptId: this.addForm.centerId
      }).then(data => {
        this.adviserList = data
        this.addForm.adviserId = ''
      }).catch(err => {
        this.$Message.error({
          content: '获取数据失败：' + err.message
        })
      })
    },
    closeDialog () {
      this.clearForm()
      this.$store.commit('resource/setIdentity', '')
      this.$store.commit('resource/setDialogType', '')
      this.$store.commit('resource/setmultipleSelection', [])
    },
    clearForm () {
      this.addForm = {
        cityId: '',
        areaId: '',
        centerId: '',
        adviserId: ''
      }
      this.belongDeptId = ''
    },
    handleClose (done) {
      this.closeDialog()
      done()
    },
    handleSizeChange (size) {
      this.searchData['pageSize'] = size
      this.queryDetailInfo()
    },
    handleCurrentChange (num) {
      this.searchData['pageNo'] = num
      this.queryDetailInfo()
    },
    handleSizeChangeOpr (size) {
      this.operateSearch['pageSize'] = size
      this.queryLogs()
    },
    handleCurrentChangeOpr (num) {
      this.operateSearch['pageNo'] = num
      this.queryLogs()
    },
    closeDia (done) {
      this.searchData = {
        pageNo: 1,
        pageSize: 10
      }
      this.operateSearch = {
        pageNo: 1,
        pageSize: 10
      }
      done()
    }
  }
}
</script>

<style scoped lang="less">
.icon {
  color: #10b8ff;
  margin: 13px 10px auto 10px;
}
.form {
  margin-left: 10%;
}
.selected-info {
  background: rgba(16, 184, 255, 0.1);
  border-radius: 2px 2px 2px 0px 2px;
  font-family: MicrosoftYaHei;
  font-size: 12px;
  color: #2b3257;
  letter-spacing: 0;
  line-height: 15px;
  height: 34px;
  width: 90%;
  margin: 0px auto 15px;
}
.stress {
  color: #10b8ff;
}
.noText {
  text-align: center;
  font-size: 18px;
}
</style>
