<template>
  <div>
    <el-table
      v-loading="loading"
      :data="tableData"
      style="width: 100%">
      <el-table-column
        prop="stuLoginName"
        label="学员编号"
        width="120"
        align="center"/>
      <el-table-column
        label="姓名"
        align="center">
        <template slot-scope="scope">
          <el-button
            type="text"
            @click="goCustomerDetail(scope.row)">{{ scope.row.stuRealName }}</el-button>
          <el-button
            v-if="Number(scope.row.isRelateAccount) === 1"
            size="mini"
            @click="openRelatedStu(scope.row)">其他关联账号</el-button>
          <el-tag v-if="Number(scope.row.activityStatus) === 1" type="warning" size="small">老带新</el-tag>
        </template>
      </el-table-column>
      <el-table-column
        label="渠道"
        align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.stuRegistName }} </span>
          <span v-if="scope.row.sceneMessage">{{ scope.row.sceneMessage.sceneType }} / {{ scope.row.sceneMessage.secondaryType }} / {{ scope.row.sceneMessage.activityName }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="stuGradeName" label="操作" width="120" align="center">
        <template slot-scope="scope">
          <a href="javascript:void(0)" v-if="scope.row.diagnosisTaskContains === 1" @click="emitClickHandle('follow-diagnosis',scope.row)">散诊任务跟进</a>
          <a href="javascript:void(0)" v-if="scope.row.auditionsTaskContains === 1" @click="emitClickHandle('follow-audition',scope.row)">试听任务跟进</a>
          <a href="javascript:void(0)" v-if="scope.row.isStudentDiagnosis === 1" @click="emitClickHandle('diagnosis',scope.row.taskList)">诊断报告</a>
          <a href="javascript:void(0)" v-if="scope.row.isStuHasReg === 1"  @click="emitClickHandle('class-info', scope.row)">报班信息</a>
          <a href="javascript:void(0)" v-if="scope.row.isStuHasWait === 1" @click="emitClickHandle('backup-record',scope.row)">候补记录</a>
          <a href="javascript:void(0)" v-if="scope.row.isStuFollowUp === 1" @click="showKeyAction(scope.row)">关键行为</a>
          <a href="javascript:void(0)" v-if="scope.row.auditionApplyFollow === 1" @click="emitClickHandle('apply',scope.row)">试听申请跟进</a>
        </template>
      </el-table-column>
      <el-table-column
        prop="stuGradeName"
        label="当前年级"
        width="100"
        align="center"/>
      <el-table-column
        prop="registCity"
        label="注册城市"
        width="100"
        align="center"/>
      <el-table-column
        prop="allTask"
        label="任务类型"
        width="230"
        align="center">
      </el-table-column>
      <!--<el-table-column-->
      <!--prop="planContent"-->
      <!--label="维护内容"-->
      <!--width="120"-->
      <!--align="center"/>-->
      <el-table-column
        prop="taskNum"
        label="待维护任务数"
        width="120"
        align="center"/>
      <el-table-column
        prop="followNum"
        label="维护次数"
        width="100"
        align="center"/>
      <el-table-column
        prop="earlistPopTime"
        label="最早任务弹出时间"
        align="center"/>
      <el-table-column
        prop="reactionStartTime"
        label="任务开始计时时间"
        align="center"/>
    </el-table>
    <Modal
      v-model="keyActionModal"
      title="关键信息"
      width="80%"
      :mask-closable="false"
      :footer-hide="true"
    >
      <div v-if="actionData.activityClassList && actionData.activityClassList.length">
        <h3 style="margin-bottom: 10px">活动类</h3>
        <class-table :student-detail="studentDetail" :table-data="actionData.activityClassList"></class-table>
      </div>
      <div v-if="actionData.shortTermClassList && actionData.shortTermClassList.length">
        <h3 style="margin: 10px 0">短期班</h3>
        <class-table :student-detail="studentDetail" :table-data="actionData.shortTermClassList"></class-table>
      </div>
      <div v-if="actionData.mofangActivityList && actionData.mofangActivityList.length">
        <h3 style="margin: 10px 0">魔方活动</h3>
        <Table :columns="cubeCols" :data="actionData.mofangActivityList">
          <template slot-scope="{ row }" slot="url">
            <a :href="row.url" target="_blank">{{row.url}}</a>
          </template>
        </Table>
      </div>
    </Modal>
    <Modal
      :mask-closable="false"
      title="与用户注册手机号关联的其他账号"
      width="560"
      v-model="showRelatedStu"
      :footer-hide="true"
    >
      <RelatedAccount v-if="showRelatedStu"
                      :city-code="row.areaCode"
                      :uid="row.stuUid"
                      :student-id="row.studentId"
                      @close="showRelatedStu = false"></RelatedAccount>
    </Modal>
  </div>
</template>

<script>
import { queryStudentTaskActivity } from '@/api/crm'
import CustomerMixin from '../../mixin/customerDetail'
import ClassTable from './classTable'
import RelatedAccount from '../../archives/relatedStu'
export default {
  mixins: [CustomerMixin],
  components: {
    ClassTable,
    RelatedAccount
  },
  props: {
    tableData: {
      type: Array,
      default: function () {
        return []
      }
    },
    loading: {
      type: Boolean,
      default: function () {
        return false
      }
    }
  },
  data () {
    return {
      studentDetail: {},
      keyActionModal: false,
      actionData: {},
      cubeCols: [
        { title: '活动名称', align: 'center', key: 'activityName' },
        { title: '活动类型', align: 'center', key: 'type' },
        { title: '活动URL', align: 'center', slot: 'url' }
      ],
      cubeData: [],
      row: {},
      showRelatedStu: false
    }
  },
  methods: {
    showKeyAction (row) {
      this.studentDetail.cityCode = row.areaCode
      this.studentDetail.studentId = row.studentId
      queryStudentTaskActivity({
        studentId: row.studentId,
        pyUid: row.stuUid,
        uid: row.stuUid,
        mode: 4,
        cityCode: row.areaCode
      }).then(data => {
        this.actionData = data
        this.keyActionModal = true
      }).catch(err => {
        this.$Message.error('查询关键行为错误：' + err.message)
      })
    },
    openRelatedStu (row) {
      this.row = row
      this.showRelatedStu = true
    }
  }
}
</script>
