<template>
  <!-- 快速搜索、手机号、学员姓名、学员编号 -->
  <div @click.stop="">
    <Input class="fast-search" v-model="inputValue" style="width: 240px;" placeholder="请输入关键词搜索" clearable
      @on-clear="handleHistorySelect({name: ''})"
      @on-keyup.enter="handleValueConfirm" @on-focus="showHistory = true">
      <Select v-model="selectValue" slot="prepend" style="width: 86px" @on-change="handleSelectValueChange">
        <Option value="stuMobile">手机号码</Option>
        <Option value="stuRealName">学员姓名</Option>
        <Option value="stuLoginName">学员编号</Option>
      </Select>
    </Input>
    <div class="search-history-list" v-if="showHistory && historyList && historyList.length">
      <div class="search-history-item" v-for="(item, index) in historyList" :key="index"
        @click="handleHistorySelect(item)" @mouseover="item.isHover = true" @mouseleave="item.isHover = false">
        <span>{{ item.name }}</span>
        <span v-if="!item.isHover" class="gray">历史</span>
        <a v-else href="javascript:void(0)" @click.stop="handleHistoryDel(item)">删除</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PhoneCardNameSearch',
  props: {
    value: {
      type: Array,
      default: () => []
    }
  },
  watch: {
    // 回显
    value: {
      deep: true,
      immediate: true,
      handler (newValue) {
        this.inputValue = newValue[1] || ''
        this.selectValue = newValue[0] || 'stuMobile'
      }
    },
    inputValue () {
      this.$emit('input', [this.selectValue, this.inputValue])
      this.$emit('on-change', [this.selectValue, this.inputValue])
    },
    selectValue () {
      this.$emit('input', [this.selectValue, this.inputValue])
      this.$emit('on-change', [this.selectValue, this.inputValue])
    }
  },
  data () {
    return {
      inputValue: '',
      selectValue: 'stuMobile',
      localHistoryList: localStorage['localHistoryList']
        ? JSON.parse(localStorage['localHistoryList'])
        : {},
      showHistory: false,
      historyList: []
    }
  },
  mounted () {
    window.addEventListener('click', this.historyListVisibleEvent)
    this.setHistoryListShow()
  },
  beforeDestroy () {
    window.removeEventListener('click', this.historyListVisibleEvent)
  },
  methods: {
    // 恢复默认值
    // 显示当前类型的历史列表
    setHistoryListShow () {
      this.historyList = this.localHistoryList[this.selectValue] || []
    },
    // 学员编号、姓名、手机号类型变更
    handleSelectValueChange () {
      this.setHistoryListShow()
    },
    // 历史展示、隐藏
    historyListVisibleEvent () {
      this.showHistory = false
    },
    // 选中历史
    handleHistorySelect (data) {
      this.inputValue = data.name
      this.showHistory = false
      data.isHover = false
      this.$emit('input', [this.selectValue, this.inputValue])
      this.$emit('on-change', [this.selectValue, this.inputValue])
    },
    // 删除历史
    handleHistoryDel (data) {
      this.localHistoryList[this.selectValue] = this.localHistoryList[this.selectValue].filter((item) => item.id !== data.id)
      localStorage['localHistoryList'] = JSON.stringify(this.localHistoryList)
      this.setHistoryListShow()
    },
    // 确认输入值
    handleValueConfirm () {
      if (!this.localHistoryList[this.selectValue]) {
        this.localHistoryList[this.selectValue] = []
      }
      // 排重
      this.localHistoryList[this.selectValue] = this.historyList.filter(
        (item) => item.name !== this.inputValue.trim()
      )
      if (this.inputValue.trim()) {
        if (this.historyList.length === 10) {
          this.localHistoryList[this.selectValue].pop()
        }
        this.localHistoryList[this.selectValue].unshift({
          name: this.inputValue,
          isHover: false,
          id: Date.now()
        })
        localStorage['localHistoryList'] = JSON.stringify(this.localHistoryList)
        this.setHistoryListShow()
      }
      // 隐藏历史列表
      this.historyList.forEach((item) => (item.isHover = false))
    }
  }
}
</script>

<style lang="less" scoped>
.search-history-list {
  position: absolute;
  width: 153px;
  padding: 7px 0;
  left: 87px;
  top: 34px;
  background: #fff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 2px;
  max-height: 150px;
  overflow: auto;
  z-index: 10001;
  .search-history-item {
    display: flex;
    justify-content: space-between;
    cursor: pointer;
    padding: 0 7px;
    &:hover {
      background-color: #edf4ff;
    }
  }
  .gray {
    color: rgba(0, 0, 0, 0.5);
  }
}

.fast-search {
  /deep/ .ivu-select-selection > div {
    background-color: #fff;
  }
}
</style>
