<template>
  <div style="border: 1px solid rgba(0, 0, 0, 0.08);height: 100%;">
    <div class="all-grade">
      <div style="width:20%;padding-left: 37px;">全部班级</div>
      <div style="width:80%;padding-left: 53px;">
        <span style="color:red">*</span>
        <span>班级个数</span>
        <span v-if="classList.length">{{classList.length}}</span>
        <InputNumber v-else :value="0" :min="0" :max="50" @on-blur="classCountBlur"></InputNumber>
      </div>
    </div>
    <div style="height:100%;display: flex">
      <TalTab style="width: 20%">
        <TalTabItem v-for="grade in gradeList" :key="grade.gradeId" @click="setCurrentGradeId(grade.gradeId)" :active="grade.gradeId === gradeId">
          <span class="grade-name">{{grade.gradeName}}</span>
          <span class="grade-count">共{{grade.classCount ? grade.classCount : 0}}个</span>
        </TalTabItem>
      </TalTab>
      <div class="clazz-wrapper">
        <Row v-for="(clazz, index) in classList" :key="clazz.id" class="grade">
          <Col :span="6" class="clazz-name">
            <div style="margin-right: 5px">{{ clazz.className }}</div>
            <Input v-if="clazz.editing" style="width: 100px" :value="clazz.className" @on-blur="classBlur($event, index, 'name')"></Input>
            <div v-else class="edit-icon" @click="editClassName(clazz.id)"></div>
          </Col>
          <Col :span="6" class="clazz-count">
            <span style="margin-right: 20px">人数</span>
            <InputNumber :value="Number(clazz.studentCount)" :min="0" :max="200" @on-blur="classBlur($event, index, 'count')"></InputNumber>
          </Col>
          <Col :span="12" class="clazz-log">
            <div class="log-text">
              <div>{{ clazz.log.message }}</div>
              <div>{{ clazz.log.createTime }}</div>
            </div>
            <div v-if="(index + 1) === classList.length" class="delete" @click="deleteClass(clazz.id)">
            </div>
            <div v-else style="width: 14px">
            </div>
          </Col>
        </Row>
        <Row class="grade" v-if="classList.length">
          <Col :span="6" class="clazz-name" @click.native="addNewClass" style="cursor: pointer"><span style="margin-right: 5px">+</span><span>新建班级</span></Col>
        </Row>
      </div>
    </div>
  </div>
</template>

<script>
import TalTab from '_c/tab/tal-tab'
import TalTabItem from '_c/tab/tal-tab-item'
import { addSchoolClass, editSchoolClass, deleteSchoolClass, queryOperateLog } from '@/api/schoolDic'
import RecordMixin from './record-mixin'
const NUM_REG = /^[+]?(\d+)$/
export default {
  name: 'RecordTab',
  mixins: [ RecordMixin ],
  components: {
    TalTab,
    TalTabItem
  },
  props: {
    schoolId: String,
    cityCode: String
  },
  data () {
    return {
      source: 'edit',
      adding: false
    }
  },
  methods: {
    editClassName (id) {
      const clazz = this.classList.find(c => c.id === id)
      this.$set(clazz, 'editing', true)
    },
    async classBlur (event, index, type) {
      try {
        const clazz = this.classList[index]
        const val = event.target.value
        if (type === 'name') {
          if (clazz.className === val) {
            clazz.editing = false
            return
          }
          clazz.className = val
        } else if (type === 'count') {
          if (!NUM_REG.test(val)) {
            this.$Message.error({
              content: '请输入正整数',
              duration: 5
            })
            return
          }
          if (Number(clazz.studentCount) === Number(val)) {
            return
          }
          clazz.studentCount = val
        }
        clazz.cityCode = this.cityCode
        await editSchoolClass(clazz)
        this.$Message.success('更新班级成功')
        clazz.editing = false
        const logs = await queryOperateLog({
          cityCode: this.cityCode,
          schoolIds: clazz.id
        })
        if (logs.records && logs.records.length) {
          clazz.log = logs.records[0]
        } else {
          clazz.log = {}
        }
      } catch (err) {
        this.$Message.error({
          content: '更新班级失败：' + err.message,
          duration: 5
        })
      }
    },
    async classCountBlur (event) {
      try {
        this.adding = true
        const val = event.target.value
        if (NUM_REG.test(val)) {
          for (let i = 1; i <= Number(val); i++) {
            const clazz = {
              cityCode: this.cityCode,
              schoolId: this.schoolId,
              gradeId: this.gradeId,
              classOrder: i,
              className: `${i}班`,
              studentCount: 0
            }
            await this.addClass(clazz)
          }
        } else {
          this.$Message.error({
            content: '请输入正整数',
            duration: 5
          })
        }
      } catch (err) {
        this.$Message.error({
          content: '批量新增班级失败：' + err.message,
          duration: 5
        })
      } finally {
        this.adding = false
      }
    },
    addNewClass () {
      if (this.adding) {
        this.$Message.warning({
          content: '新增班级中，新稍后重试...',
          duration: 5
        })
        return
      }
      const classOrder = this.classList.length + 1
      const clazz = {
        cityCode: this.cityCode,
        schoolId: this.schoolId,
        gradeId: this.gradeId,
        classOrder,
        className: `${classOrder}班`,
        studentCount: 0
      }
      this.addClass(clazz)
    },
    async addClass (clazz) {
      try {
        clazz = await addSchoolClass(clazz)
        const logs = await queryOperateLog({
          cityCode: this.cityCode,
          schoolIds: clazz.id
        })
        if (logs.records && logs.records.length) {
          clazz.log = logs.records[0]
        } else {
          clazz.log = {}
        }
        this.classList.push(clazz)
        this.gradeList.find(g => clazz.gradeId === g.gradeId).classCount = this.classList.length
      } catch (err) {
        this.$Message.error({
          content: '新增班级失败：' + err.message,
          duration: 5
        })
      }
    },
    deleteClass (id) {
      this.$Modal.confirm({
        title: '删除确认',
        content: '是否确认删除此班级？',
        onOk: () => {
          deleteSchoolClass({
            cityCode: this.cityCode,
            id
          }).then(() => {
            this.$Message.success('删除班级成功')
            this.classList = this.classList.filter(c => c.id !== id)
            this.gradeList.find(g => g.gradeId === this.gradeId).classCount = this.classList.length
          }).catch(err => {
            this.$Message.error({
              content: '删除班级失败：' + err.message,
              duration: 5
            })
          })
        }
      })
    }
  }
}
</script>

