
<template>
  <div ref="card" class="task-type-card"
       :class="{'active': data.checked}"
       @mouseenter="hover = true" @mouseleave="hover = false" @click="$emit('card-click',data)">
    <p class="name">{{ data.taskName }}</p>
    <div class="num">{{ data.taskCount }}
      <template v-if="data.taskName !== '全部任务'">
        <span class="top" v-if="!data.isTop && hover" style="font-size: 12px;" @click.stop="$emit('top', data)">置顶</span>
        <div v-if="data.isTop && hover" @mouseenter="hover2 = true" @mouseleave="hover2 = false" class="top" style="font-size: 12px;" >
          置顶
          <div class="tooltips" v-if="hover2" @click.stop="$emit('cancel-top', data)">取消置顶</div>
          <div class="del"></div>
        </div>
      </template>
    </div>
    <div class="overflow" v-if="data.isTop">
      <div class="corner"></div>
    </div>
  </div>
</template>

<script>
export default {
  inject: ['ScrollInstance'],
  props: {
    data: {
      type: Object,
      default: () => {}
    }
  },
  data () {
    return {
      hover: false,
      hover2: false
    }
  },
  mounted () {
    this.$nextTick(() => {
      const cardWidth = this.$refs.card.clientWidth
      // 2是border宽度
      if (this.ScrollInstance) {
        this.ScrollInstance.updateView(cardWidth + 2)
      }
    })
  },
  methods: {
    mouseOver () {}
  }
}
</script>

<style lang="less" scoped>
.task-type-card {
  padding: 8px 16px;
  border-radius: 4px;
  border: 1px solid #ebedf0;
  color: rgba(0, 0, 0, 0.9);
  display: inline-block;
  font-weight: bold;
  position: relative;
  min-width: 100px;
  cursor: pointer;

  &:hover{
    box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.06);
  }

  &.active {
    border-color: #3384ff;
    color: #3384ff;
  }

  .name {
    font-size: 14px;
  }

  .num {
    font-size: 20px;
    font-family: DIN Alternate;
    display: flex;
    align-items: baseline;
    justify-content: space-between;
  }

  .top {
    font-size: 12px;
    color: rgba(0, 0, 0, 0.85);
    position: relative;

    .del {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 30px;
      height: 1px;
      transform: translate(-50%, -50%) rotate(-30deg);
      background-color: rgba(0, 0, 0, 0.85);
    }
  }

  .overflow {
    width: 20px;
    height: 20px;
    position: absolute;
    right: 0;
    top: 0;
    overflow: hidden;
    border-radius: 3px;
  }

  .corner {
    width: 20px;
    height: 20px;
    position: absolute;
    right: 0;
    top: 0;
    transform: translate(50%, -50%) rotate(45deg);
    background-color: #3384ff;
  }

  .tooltips {
    position: absolute;
    bottom: -35px;
    left: 50%;
    transform: translate(-50%);
    background: rgba(0, 0, 0, 0.85);
    color: #fff;
    font-size: 14px;
    padding: 5px;
    border-radius: 4px;

    &::after {
      content: '';
      position: absolute;
      border: 4px solid rgba(0, 0, 0, 0);
      top: 0;
      left: 50%;
      transform: translate(-50%, -100%);
      border-bottom-color: rgba(0, 0, 0, 0.85);
    }
  }
}
</style>
