<template>
  <div class="new-data-card">
    <h1 class="title">{{name}}</h1>
    <div class="num" style="margin-top: 16px;margin-bottom: 4px;">
      <span class="done">{{ done }}</span> / {{ target }}
    </div>
    <span class="desc" v-if="target !== 0">完成率 {{percent}}%</span>
    <span class="desc" v-else>-</span>
    <Progress style="margin-top: 6px;" :percent="percent" hide-info :stroke-color="name !== '个人招新人次' ? '#FF9F19' : '#2d8cf0'" />
  </div>
</template>

<script>
export default {
  props: {
    name: String,
    target: Number,
    done: Number
  },
  computed: {
    percent () {
      if (this.done === 0 || this.target === 0) {
        return 0
      }
      return +((this.done / this.target) * 100).toFixed(1)
    }
  }
}
</script>

