<template>
  <div>
    <el-dialog
      v-if="dialogVisible"
      :visible="dialogVisible"
      :before-close="handleClose"
      title="资源分配记录查询"
      width="60%"
      @close="closeDialog"
    >
      <el-form
        :model="addForm"
        class="form"
        label-width="70px"
        ref="addFormref"
      >
        <el-form-item label="操作人：">
          <el-select
            size="small"
            v-model="addForm.operateEmpNo"
            filterable
            @change="(val)=>getAreaList(val,1)"
            @visible-change="queryOperation"
          >
            <el-option
              :value="null"
              label="全部"
            ></el-option>
            <el-option
              v-for="(item, key) in operateUserList"
              :key="key"
              :label="item.operateName"
              :value="String(item.operateEmpNo)"
            />
          </el-select>
        </el-form-item>
      </el-form>
      <el-table
        border
        v-loading="tableLoading"
        :data="tableData.records"
        style="width: 100%"
      >
        <el-table-column
          prop="createDate"
          label="发起时间"
          align="center"
        >
          <template slot-scope="{ row }">
            <span>
              {{ `${row.createDate ? row.createDate : '--'}` }}
            </span>
          </template>
        </el-table-column>
        <el-table-column
          label="操作人"
          align="center"
        >
          <template slot-scope="{ row }">
            <span>
              {{ `${row.operateName ? row.operateName : '--'}` }}
            </span>
            <p>{{ `(${getRole(row)})` }}</p>
          </template>
        </el-table-column>
        <el-table-column
          prop="typeName"
          width="180"
          label="接收方"
          align="center"
        />
        <el-table-column
          width="180"
          label="分配状态"
          align="center"
        >
          <template slot-scope="scope">
            <div>
              <span style="margin-right: 10px">{{
                scope.row.status === 2 ? '处理完成' : '处理中'
              }}</span>
              <a @click="getDispatchDetail(scope.row.id)">查看明细</a>
            </div>
            <div>
              {{
                `（成功${scope.row.successNum}人，失败${scope.row.failNum}人）`
              }}
            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="completeTime"
          label="完成时间"
          align="center"
        >
          <template slot-scope="{ row }">
            <span>
              {{ `${row.completeTime ? row.completeTime : '--'}` }}
            </span>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        :current-page="tableData.pageNo"
        :page-sizes="[10, 20]"
        :page-size="tableData.pageSize"
        :total="tableData.totalCount"
        layout="total, sizes, prev, pager, next, jumper"
        @size-change="handleSizeChangeOpr"
        @current-change="handleCurrentChangeOpr"
      />
      <el-dialog
        title="分配明细"
        :visible.sync="detailModal"
        append-to-body
        :before-close="closeDia"
        width="60%"
      >
        <el-form
          :inline="true"
          :model="detailForm"
          label-width="100px"
          size="small"
          ref="detailFormref"
        >
          <el-form-item label="分配状态：">
            <el-select v-model="detailForm.status">
              <el-option
                :value="null"
                label="全部"
              > </el-option>
              <el-option
                v-for="(item, key) in Object.keys(splitStatus)"
                :key="key"
                :label="splitStatus[item]"
                :value="String(item)"
              />
            </el-select>
          </el-form-item>
          <el-form-item label="用户名：">
            <el-input
              v-model="detailForm.name"
              placeholder="请输入"
            >
            </el-input>
          </el-form-item>
          <el-button
            size="small"
            type="primary"
            @click="searchDetail"
          >查询</el-button>
        </el-form>
        <br />
        <div v-if="
            detailList && detailList.records && detailList.records.length > 0
          ">
          <el-timeline :reverse="true">
            <el-timeline-item
              v-for="(detail, index) in detailList.records"
              :key="index"
              :timestamp="detail.createDate"
            >
              <div>{{ statusMap[detail.status] }}</div>
              <div>{{ detail.studentName }}</div>
            </el-timeline-item>
          </el-timeline>
          <el-pagination
            :current-page="detailList.pageNo"
            :page-sizes="[10, 20]"
            :page-size="detailList.pageSize"
            :total="detailList.totalCount"
            layout="total, sizes, prev, pager, next, jumper"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
          />
        </div>

        <div
          v-else
          class="noText"
        >暂无数据</div>
      </el-dialog>
    </el-dialog>
  </div>
</template>

<script>
import {
  getBatchDispatchLog,
  getBatchDispatchDetail,
  getOperateUserList
} from '@/api/crm'
import { mapState } from 'vuex'
export default {
  props: {
    deptid: {
      type: String,
      default: () => {
        return ''
      }
    },
    formData: Object,
    totalNum: Number
  },
  data () {
    return {
      statusMap: {
        1: '处理中',
        2: '分配成功',
        3: '分配失败'
      },
      addForm: {
        operateEmpNo: null
      },
      tableData: [],
      detailModal: false,
      detailList: [],
      searchData: {
        pageNo: 1,
        pageSize: 10
      },
      operateSearch: {
        pageNo: 1,
        pageSize: 10
      },
      detailForm: {
        status: null,
        name: ''
      },
      operateUserList: [],
      tableLoading: false,
      splitStatus: {
        '1': '处理中',
        '2': '处理完成',
        '3': '处理失败'
      }
    }
  },
  computed: {
    ...mapState('roleRelativeData', ['userInfo']),
    dialogVisible () {
      return this.$store.state.resource.identity === 'records'
    },
    empNo () {
      return this.$store.state.user.userInfo.empNo
    },
    getRole () {
      return function (row) {
        let operateCityName = row.operateCityName ? row.operateCityName + '-' : ''
        let operateAreaName = row.operateAreaName ? row.operateAreaName : ''
        let operateServiceCenterName = row.operateServiceCenterName ? row.operateServiceCenterName + '-' : ''
        let operateRoleName = row.operateRoleName ? row.operateRoleName : ''
        return operateCityName + operateAreaName + operateServiceCenterName + operateRoleName
      }
    }

  },
  watch: {
    dialogVisible: function () {
      this.getAreaList(null, 1)
    }
  },
  methods: {
    // 查询操作人
    queryOperation (status) {
      if (status) {
        let params = {
          cityCode: this.userInfo.cityCode
        }
        getOperateUserList(params).then(res => {
          this.operateUserList = res
        }).catch(err => {
          this.$Message.error(err.message)
        })
      }
    },
    // 点击明细查询列表
    getDispatchDetail (dispatchId) {
      this.currentDispatchId = dispatchId
      this.queryDetailInfo()
    },
    // 查询明细列表
    queryDetailInfo () {
      getBatchDispatchDetail({
        ...this.detailForm,
        ...this.searchData,
        dispatchId: this.currentDispatchId,
        cityCode: this.userInfo.cityCode
      }).then(data => {
        this.detailList = data
        this.detailModal = true
      }).catch(err => {
        this.$Message.error({
          content: '获取数据失败：' + err.message
        })
      })
    },
    // 点击资源分配分配记录
    getAreaList (val, type) {
      if (type === 1) {
        this.operateSearch = {
          pageNo: 1,
          pageSize: 10
        }
      }
      let params = {
        empNo: this.empNo,
        operateEmpNo: val,
        areaCode: this.userInfo.cityCode,
        cityCode: this.userInfo.cityCode,
        ...this.operateSearch
      }
      this.tableLoading = true
      getBatchDispatchLog(params).then(data => {
        this.tableData = data
        this.tableLoading = false
      }).catch(err => {
        this.tableLoading = false
        this.$Message.error({
          content: '获取数据失败：' + err.message
        })
      })
    },
    closeDialog () {
      this.clearForm()
      this.$store.commit('resource/setIdentity', '')
      this.$store.commit('resource/setDialogType', '')
      this.$store.commit('resource/setmultipleSelection', [])
    },
    clearForm () {
      this.addForm = {
        operateEmpNo: null
      }
    },
    handleClose (done) {
      this.closeDialog()
      done()
    },
    handleSizeChange (size) {
      this.searchData['pageSize'] = size
      this.queryDetailInfo()
    },
    handleCurrentChange (num) {
      this.searchData['pageNo'] = num
      this.queryDetailInfo()
    },
    handleSizeChangeOpr (size) {
      this.operateSearch['pageSize'] = size
      this.getAreaList(this.addForm.operateEmpNo)
    },
    handleCurrentChangeOpr (num) {
      this.operateSearch['pageNo'] = num
      this.getAreaList(this.addForm.operateEmpNo)
    },
    closeDia (done) {
      this.searchData = {
        pageNo: 1,
        pageSize: 10
      }
      this.operateSearch = {
        pageNo: 1,
        pageSize: 10
      }
      this.$refs.detailFormref.resetFields()
      this.$refs.addFormref.resetFields()
      this.detailForm.status = null
      this.addForm.operateEmpNo = null
      done()
    },
    searchDetail () {
      this.queryDetailInfo()
    }
  }
}
</script>

<style scoped lang="less">
.noText {
  text-align: center;
  font-size: 18px;
}
</style>
