<template>
  <div style="padding: 20px;width: 100%;">
    <Button style="float:right;" @click="quiteFollow">放弃跟进</Button>
    <div class="ivu-table ivu-table-default ivu-table-border relative" style="margin-top: 20px;">
      <Spin size="large" fix v-if="tableLoading"></Spin>
      <CheckboxGroup
        v-model="checkAllGroup"
        style="font-size: 12px"
        @on-change="handleCheckGroupChange"
      >
        <table cellspacing="0" cellpadding="0" border="0" style="width: 100%">
          <thead>
            <tr class="ivu-table-row">
              <th
                v-for="(item,index) in tableColumns"
                :key="index"
                class="ivu-table-column-center"
                :width="item.width"
              >
                <template v-if="item.isCheckBox">
                  <label class="tal-ipt-checkbox" style="margin-left: 0">
                    <input
                      type="checkbox"
                      class="ivu-checkbox-inner"
                      style="margin-left: -9px;"
                      @click="checkAllHandler"
                      :checked="checkAll"
                    />
                    <span></span>
                  </label>
                </template>
                <div class="ivu-table-cell" v-else>{{item.title}}</div>
              </th>
            </tr>
          </thead>
          <tbody class="ivu-table-tbody">
            <tr class="ivu-table-row" v-for="(item, index) in tableData" :key="index">
              <td class="ivu-table-column-center">
                <div class="ivu-table-cell">
                  <span
                    style="display: inline-block;width: 90px;"
                  >{{ item.isFullPhone ? item.fullPhone : item.phone }}</span>
                  <Icon
                    style="margin-left: 20px;"
                    v-if="!item.isFullPhone"
                    @click="toggleMobile(item)"
                    type="md-eye"
                  />
                  <Icon
                    style="margin-left: 20px;"
                    v-else
                    type="md-eye-off"
                    @click="toggleMobile(item)"
                  />
                </div>
              </td>
              <td class="ivu-table-column-center">
                <div class="ivu-table-cell">{{ item.addTime }}</div>
              </td>
              <td class="ivu-table-column-center">
                <div class="ivu-table-cell">{{ item.createTime }}</div>
              </td>
              <td class="ivu-table-column-center">
                <div class="ivu-table-cell">
                  <a
                    href="javascript:void(0);"
                    v-if="getOperateByStatus(item.status).complete"
                    @click="updateStudentInfo(item.uid)"
                  >完善信息</a>
                </div>
              </td>
              <td class="ivu-table-column-center">
                <div class="ivu-table-cell">
                  <Checkbox :label="item.label" v-if="getOperateByStatus(item.status).select">{{''}}</Checkbox>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </CheckboxGroup>

      <p style="text-align: center;" v-if="!tableData.length">暂无数据</p>
      <div
        style="margin: 10px;"
        :class="{ 'page-fix': tableData.length < 3 }"
        v-show="!tableLoading"
      >
        <div style="float: right;">
          <Page
            :total="pageTotal"
            :page-size="currentPageSize"
            :current="currentPageNum"
            :page-size-opts="[10,20]"
            show-elevator
            show-sizer
            show-total
            @on-page-size-change="pageSizeChange"
            @on-change="changePage"
          ></Page>
        </div>
      </div>
    </div>
    <!-- 完善学员信息 -->
    <Modal
      :mask-closable="false"
      :footer-hide="true"
      title="完善信息"
      width="960px"
      class="modal-height1"
      v-model="addStudentModal"
    >
      <tal-student-complete
        v-if="addStudentModal"
        :formData="addStudentForm"
        @close="addStudentModal = false"
        @success="onCompleteSuccess"
      ></tal-student-complete>
    </Modal>
  </div>
</template>

<script>
import { decodePhone } from 'talApi/student'
import { queryCompleteStudentList, giveUpFollowUp } from '../../../api/crmStudent'
import { getUserInfo } from '../../../api/student'
export default {
  name: 'incompleteInfoUsers',
  data () {
    return {
      tableColumns: Object.freeze([
        {
          title: '电话号码',
          width: '25%'
        },
        {
          title: '注册时间',
          width: 200
        },
        {
          title: '分配时间',
          width: 200
        },
        {
          title: '操作',
          width: 200
        },
        {
          title: 'checkbox',
          isCheckBox: true,
          width: 150
        }
      ]),
      tableData: [],
      checkAllGroup: [], // checkboxGroup选中项
      allItem: [], // 所有可选项
      checkAll: false, // 是否全选
      pageTotal: 0,
      currentPageSize: 10,
      currentPageNum: 1,
      status: 1, // 0未分配 1未完善 2已完善 3放弃跟进
      tableLoading: false,
      addStudentModal: false,
      addStudentForm: {}
    }
  },
  mounted () {
    this.allItem = [123, 456, 789]
    this.getTableData()
  },
  methods: {
    // 分页
    changePage (num) {
      this.currentPageNum = num
      this.getTableData()
    },
    pageSizeChange (size) {
      this.currentPageNum = 1
      this.currentPageSize = size
      this.getTableData()
    },
    // 获取未完善信息用户列表
    async getTableData () {
      this.tableLoading = true
      this.tableData = []
      this.checkAllGroup = []
      this.checkAll = false
      const params = {
        pageNo: this.currentPageNum,
        pageSize: this.currentPageSize,
        status: this.status
      }
      try {
        const data = await queryCompleteStudentList(params)

        const allItem = []
        data.records.forEach(item => {
          if (this.getOperateByStatus(item.status).select) {
            allItem.push(item.uid)
          }
          item.label = item.uid
          item.isFullPhone = false
          item.fullPhone = ''
        })
        this.tableData = data.records
        this.allItem = Object.freeze(allItem)
        this.pageTotal = data.totalCount
      } catch (error) {
        this.tableData = []
        this.$Message.error({ content: '获取数据失败:' + error.message, duration: 5 })
      }
      this.tableLoading = false
    },
    // 显示隐藏手机号
    async toggleMobile (item) {
      item.isFullPhone = !item.isFullPhone
      if (item.fullPhone) return
      const params = {
        uid: item.uid,
        encodePhone: item.phone
      }
      try {
        const res = await decodePhone(params)
        item.fullPhone = res
      } catch (error) {
        this.$Message.error({ content: '手机号解码失败:' + error.message, duration: 5 })
      }
    },
    // 获取新增学员初始表单值
    getStudentForm () {
      let object = {
        realName: '测试宁宁', // 姓名(必填)
        callCenterEmpNo: '', // 操作人员工编号(必填)
        name: '', // 操作人姓名(必填)
        sex: 1, // 性别(必填)
        gradeId: '', // 年级(必填)
        gradeTypeId: '', // 年部(必填)
        areaCode: '010', // 所在城市ID(必填)
        birthday: '', // 生日
        provinceId: '110000', // 所在城市省份ID(必填)
        provinceName: '北京市',
        cityId: '110100',
        cityName: '北京',
        cityCode: '010',
        uid: '2347796',
        // 联系人
        attentionReqList: [
          {
            'tel': '15631778927',
            'typeId': 7,
            'typeName': '本人'
          }
        ],
        // 邮寄地址列表
        recipientReqList: [
          //   {
          //   'address': '', // 详细地址
          //   'name': '', // 收件人
          //   'tel': '', // 联系电话
          //   'isDefault': 1, // 是否默认地址
          //   'provinceId': '', // 省Id
          //   'cityId': '', // 城市Id
          //   'districtId': '' // 区/县Id
          // }
        ] // 邮寄地址
      }
      return object
    },
    onCompleteSuccess () {
      this.addStudentModal = false
      this.$Message.success({ content: '完善信息成功', duration: 5 })
      this.getTableData()
    },
    async updateStudentInfo (uid) {
      // 1.获取学员信息 getStudentInfo params:card
      // 2.获取该学员详情 getStudentDetail parmas:cityCode studentId uid
      // 3.学员信息解码 decodeAll params:studentDetail

      try {
        const res = await getUserInfo({ uid })
        const { isComplete } = res
        if (!isComplete) {
          return this.$Message.warning({ content: '该学员已完善信息', duration: 5 })
        }

        // const res2 = await getStudentDetail({uid, studentId: uid, cityCode: res.cityId})

        this.addStudentForm = Object.assign({}, res, { cityCode: res.cityId })
        this.addStudentModal = true
      } catch (error) {
        this.$Message.error({ content: '获取学员信息失败:' + error.message, duration: 5 })
      }
    },
    // 全选
    checkAllHandler () {
      this.checkAll = !this.checkAll
      if (!this.checkAll) {
        this.checkAllGroup = []
      } else {
        this.checkAllGroup = [...this.allItem]
      }
    },
    // checkGroup on-change
    handleCheckGroupChange () {
      if (this.checkAllGroup.length === this.allItem.length) {
        this.checkAll = true
      } else {
        this.checkAll = false
      }
    },
    // 放弃跟进
    quiteFollow () {
      if (!this.checkAllGroup.length) {
        return this.$Message.warning({ content: '请先选择数据', duration: 5 })
      }

      this.$Modal.confirm({
        title: '确认放弃跟进已选学员？',
        content: '',
        onOk: () => {
          this.giveUpFollowUp()
        }
      })
    },
    async giveUpFollowUp () {
      const pyUids = this.checkAllGroup.join(',')
      try {
        await giveUpFollowUp({ pyUids })
        this.$Message.success({ content: '已放弃跟进', duration: 5 })
        this.getTableData()
      } catch (error) {
        this.$Message.error({ content: '放弃跟进失败：' + error.message, duration: 5 })
      }
    },
    getOperateByStatus (status) {
      switch (status) {
        case 0:
          return {
            complete: false,
            select: false
          }
        case 1:
          return {
            complete: true,
            select: true
          }
        case 2:
          return {
            complete: true,
            select: true
          }
        case 3:
          return {
            complete: true,
            select: false
          }
        default:
          return {
            complete: true,
            select: true
          }
      }
    }

  }
}
</script>

<style lang="less" scoped>
 /deep/ .ivu-table {
  overflow-y: auto;
  padding-bottom: 50px;
  &::before {
    background-color: rgba(0, 0, 0, 0);
  }
}

 /deep/ .ivu-select{
   position: relative;
 }
</style>
