<template>
  <div class="layout">
    <Tabs class="custom-tabs" :animated="false" name="resource" v-model="activeTabName" @on-click="handleTabClick">
        <TabPane v-for="item in tabs" :label="labelRender[item.id]" :name="String(item.id)" :key="item.id"></TabPane>
        <TabPane v-if="resourceWarningName" :label="resourceWarningName" :name="'6'"></TabPane>
    </Tabs>
    <Form class="form-container" :model="formData" :label-width="64" inline>
      <FormItem label="注册日期" v-if="getFormItemVisible('注册日期')">
        <DatePicker type="daterange" :options="registDateOptions" v-model="formData.registDate"
          placeholder="   开始日期 -> 结束日期" style="width: 200px" @on-change="handleRegistDateChange" clearable></DatePicker>
      </FormItem>
      <FormItem label="维护时间"  v-if="getFormItemVisible('维护时间')">
        <DatePicker type="daterange" :options="registDateOptions"
          placeholder="   开始日期 -> 结束日期" style="width: 200px" @on-change="handleFollowDateChange" v-model="formData.followDate" clearable></DatePicker>
      </FormItem>
      <FormItem label="分配时间"  v-if="getFormItemVisible('分配时间')">
        <DatePicker type="daterange" :options="registDateOptions" v-model="formData.disDate"
                    placeholder="   开始日期 -> 结束日期" style="width: 200px" @on-change="handleAllocateDateChange" clearable></DatePicker>
      </FormItem>
      <FormItem label="绑定时间"  v-if="getFormItemVisible('绑定时间')">
        <DatePicker type="daterange" :options="registDateOptions" v-model="formData.bindDate"
                    placeholder="   开始日期 -> 结束日期" style="width: 200px" @on-change="handleBindDateChange" clearable></DatePicker>
      </FormItem>
      <FormItem label="绑定状态" :label-width="64"  v-if="getFormItemVisible('绑定状态')">
        <Select v-model="formData.bindStatus" style="width:120px" clearable>
          <Option v-for="item in condition.belongStatusList" :value="item.id" :key="item.id">{{ item.name }}</Option>
        </Select>
      </FormItem>
      <FormItem label="绑定人" :label-width="50"  v-if="getFormItemVisible('绑定人')">
        <Select v-model="formData.belongIdentityIdByPerson" style="width:120px" :disabled="formData.bindStatus === 1 || !!formData.belongIdentityIdByStop" clearable filterable>
          <Option v-for="item in condition.consultantList" :value="item.identityId" :key="item.identityId">{{ item.userName }}</Option>
        </Select>
      </FormItem>
      <FormItem label="停用/离职顾问" :label-width="99"  v-if="getFormItemVisible('停用/离职顾问')">
        <Select v-model="formData.belongIdentityIdByStop" style="width:120px" :disabled="formData.bindStatus === 1 || !!formData.belongIdentityIdByPerson" clearable filterable>
          <Option v-for="item in condition.resignIdentity" :value="item.identityId" :key="item.identityId">{{ item.userName }}</Option>
        </Select>
      </FormItem>
      <FormItem label="归属类型" :label-width="64"  v-if="getFormItemVisible('归属类型')">
        <Select v-model="formData.belongDeptType" style="width:120px" @on-change="queryDeptList" clearable>
          <Option v-for="item in condition.belongTypeList" :value="item.id" :key="item.id">{{ item.name }}</Option>
        </Select>
      </FormItem>
      <FormItem label="归属" :label-width="36"  v-if="getFormItemVisible('归属')">
        <Select v-model="formData.belongDeptId" style="width:120px" :disabled="!formData.belongDeptType" clearable filterable>
          <Option v-for="item in condition.deptList" :value="item.id" :key="item.id">{{ item.deptName }}</Option>
        </Select>
      </FormItem>
      <FormItem label="最后沟通内容" :label-width="92"  v-if="getFormItemVisible('最后沟通内容')">
        <Dropdown @on-click="handleClickItem" placement="bottom-start" >
          <Input type="text" placeholder="请输入内容" v-model="formData.content"  @on-change="_followContentQuery" clearable />
          <DropdownMenu slot="list" v-if="followContentList.length > 0">
            <template>
              <DropdownItem v-for="(item,index) in followContentList" :key="index" :name="item.name" :selected="formData.content === item.name">
                <p class="class-item" v-html="item.highlightName" />
              </DropdownItem>
            </template>
          </DropdownMenu>
        </Dropdown>
      </FormItem>
      <FormItem label="快速搜索"  v-if="getFormItemVisible('分配时间')">
        <PhoneCardNameSearch ref="fastSearch" v-model="fastSearchArray" @on-change="handleFastSearchChange" />
      </FormItem>
      <FormItem label="加微状态"  v-if="getFormItemVisible('加微状态')">
        <Select v-model="formData.weixinAddStatus" style="width:100px">
          <Option v-for="item in condition.weixinAddStatus" :value="item.id" :key="item.id">{{ item.name }}</Option>
        </Select>
      </FormItem>
      <FormItem label="近30日体验行为"  v-if="getFormItemVisible('近30日体检行为')" :label-width="109">
        <Select v-model="formData.expBehaviorThirty" style="width:100px">
          <Option v-for="item in condition.expBehaviorThirty" :value="item.name" :key="item.id">{{ item.name }}</Option>
        </Select>
      </FormItem>
      <!-- <FormItem label="近7日活跃行为"  v-if="getFormItemVisible('近7日活跃行为')" :label-width="100">
        <DatePicker type="date" v-model="formData.activeBehaviorSeven"   placeholder="   开始日期 -> 结束日期" style="width: 200px"></DatePicker>
      </FormItem> -->
      <FormItem label="首次净缴学期"  v-if="getFormItemVisible('首次净缴学期')" :label-width="92">
        <Select v-model="formData.payFirstTermName" style="width:100px">
          <Option v-for="item in condition.payFirstTermName" :value="item.name" :key="item.name">{{ item.name }}</Option>
        </Select>
      </FormItem>
      <FormItem label="首次净缴日期"  v-if="getFormItemVisible('首次净缴日期')" :label-width="92">
        <DatePicker type="date" v-model="formData.payFirstDate" placeholder="请选择" style="width: 150px"></DatePicker>
      </FormItem>
      <div class="place"></div>
      <div class="btns">
        <Button type="primary" style="margin-right: 8px;" @click="handleSearch">查 询</Button>
        <Button @click="handleReset">重 置</Button>
      </div>
    </Form>
    <ResourceTable  ref="resourceTable" :tab-index="Number(activeTabName)" :resource-condition="resourceCondition" :search-params="formData"></ResourceTable>
  </div>
</template>

<script>
import PhoneCardNameSearch from '@/components/phone-card-name-search'
import ResourceTable from './resource-table'
import ResourceDistributeModal from './resource-distribute-modal'
import {
  queryResourceCondition,
  queryResignConsultantsByIdentity,
  queryStuLevelCount,
  queryConsultantsByIdentity,
  queryDeptListByDeptType,
  followContentQuery
} from '@/api/crm'

import { mapState } from 'vuex'
export default {
  name: 'resourceList2',
  components: {
    PhoneCardNameSearch,
    ResourceTable,
    ResourceDistributeModal
  },
  data () {
    return {
      levelDiscription: [
        '全部资源',
        '仅注册，未参与任何转化产品，并且从未净缴费长期班学员',
        '参与过诊断、试听课、活动类、短期班、体验课，但是从未转化为长期班付费用户的学员',
        '历史曾经在读过，但是所有在读科目均已结课',
        '首次在学而思上课的在读学员',
        '非首次在学而思上课的在读学员'
      ],
      labelRender: [
        (h) => {
          const item = this.tabs[0]
          return h('span', item.name + '（' + (item.count || 0) + '）')
        },
        (h) => {
          const item = this.tabs[1]
          return h(
            'Tooltip',
            {
              props: {
                content: this.levelDiscription[item.id],
                placement: 'bottom',
                maxWidth: '200',
                transfer: true
              }
            },
            item.name + '（' + (item.count || 0) + '）'
          )
        },
        (h) => {
          const item = this.tabs[2]
          return h(
            'Tooltip',
            {
              props: {
                content: this.levelDiscription[item.id],
                placement: 'bottom',
                maxWidth: '200',
                transfer: true
              }
            },
            item.name + '（' + (item.count || 0) + '）'
          )
        },
        (h) => {
          const item = this.tabs[3]
          return h(
            'Tooltip',
            {
              props: {
                content: this.levelDiscription[item.id],
                placement: 'bottom',
                maxWidth: '200',
                transfer: true
              }
            },
            item.name + '（' + (item.count || 0) + '）'
          )
        },
        (h) => {
          const item = this.tabs[4]
          return h(
            'Tooltip',
            {
              props: {
                content: this.levelDiscription[item.id],
                placement: 'bottom',
                maxWidth: '200',
                transfer: true
              }
            },
            item.name + '（' + (item.count || 0) + '）'
          )
        },
        (h) => {
          const item = this.tabs[5]
          return h(
            'Tooltip',
            {
              props: {
                content: this.levelDiscription[item.id],
                placement: 'bottom',
                maxWidth: '200',
                transfer: true
              }
            },
            item.name + '（' + (item.count || 0) + '）'
          )
        }
      ],
      resourceWarningName: '', // 资源预警类型
      activeTabName: '0', // 当前激活tab
      tabs: [], // tabs
      formData: {
        registDate: '',
        followDate: '',
        disDate: '',
        bindDate: '', // 绑定时间
        bindStartTime: '', // 绑定开始时间
        bindEndTime: '', // 绑定结束时间
        startTime: '', // 注册日期-开始时间
        endTime: '', // 注册日期-结束时间
        belongIdentityIdByStop: '', // 停用/离职顾问
        stuMobile: '', // 手机号
        stuRealName: '', // 学员姓名
        stuLoginName: '', // 学员编号
        belongStartTime: '', // 分配开始时间
        belongEndTime: '', // 分配结束时间
        followStartTime: '', // 分配结束时间
        followEndTime: '', // 分配结束时间
        content: '', // 最后沟通内容
        weixinAddStatus: '', // 加微状态
        expBehaviorThirty: '', // 近30日体检行为
        // activeBehaviorSeven: '', // 近7日活跃行为
        payFirstTermName: '', // 首次缴费学期id
        payFirstDate: '', // 首次缴费日期
        resourceWarningType: '', // 资源预警类型
        resourceWarningId: '', // 资源预警id
        stuLevel: '', // 学员类型  1纯新2体验3历史在读4首缴费5多期在读
        bindStatus: '', // 绑定状态
        belongIdentityIdByPerson: '', // 绑定人
        belongDeptType: '', // 归属类型
        belongDeptId: '' // 归属
      },
      form: {
        startTime: '',
        endTime: '',
        activeStatus: '',
        allotStatus: '',
        followStatus: '',
        belongIdentityId: '',
        readStatus: '',
        areaCode: '',
        gradeId: '',
        tagId: '',
        belongStatus: '',
        bindStatus: '',
        belongDeptType: '',
        belongDeptId: '',
        userTag: '',
        pageNo: 1,
        pageSize: 20,
        belongIdentityIdByStop: '',
        belongIdentityIdByPerson: '',
        resourceWarning: ''
      },
      fastSearchArray: ['stuMobile', ''], // 快速搜索 [类型，值]
      // 注册日期禁用条件
      registDateOptions: {
        disabledDate (date) {
          return date && date.valueOf() >= Date.now()
        },
        shortcuts: [
          {
            text: '今日',
            value () {
              const end = new Date()
              return [end, end]
            }
          },
          {
            text: '昨日',
            value () {
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24)
              return [start, start]
            }
          },
          {
            text: '近7日',
            value () {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              return [start, end]
            }
          },
          {
            text: '近30日',
            value () {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              return [start, end]
            }
          }
        ]
      },
      resourceCondition: {},
      // 筛选条件
      condition: {
        resignIdentity: [], // 停用/离职顾问
        expBehaviorThirty: [], // 近30日活动行为
        payFirstTermName: [], // 首期净缴学期
        weixinAddStatus: [], // 加微状态
        belongStatusList: [], // 绑定状态
        consultantList: [], // 绑定人
        belongTypeList: [], // 归属类型
        deptList: [] // 归属列表
      },
      followContentList: []
    }
  },
  computed: {
    ...mapState('roleRelativeData', ['userInfo'])
  },
  watch: {
    userInfo (val) {
      if (val.crmRoleId) {
        this._queryTabInfo()
      }
    },
    activeTabName () {
      // 过滤资源预警信息
      if (this.activeTabName === '6') {
        this.formData.resourceWarningType = this.resourceWarning.split('-')[0]
        this.formData.resourceWarningId = this.resourceWarning.split('-')[1]
      } else {
        this.formData.resourceWarningType = null
        this.formData.resourceWarningId = null
      }
    }
  },
  created () {
    const { query } = this.$route
    this.setResourceWarningInfo(query)
  },
  mounted () {
    this._queryResourceCondition()
    this._queryConsultantsByIdentity()
    this._queryResignIdentity()
    // 获取顶部tab资源分层
    if (this.userInfo.crmRoleId) {
      this._queryTabInfo()
    }
  },
  methods: {
    // 获取最后沟通内容
    _followContentQuery ({ target }) {
      followContentQuery({
        crmDeptId: this.userInfo.crmDeptId,
        crmRoleId: this.userInfo.crmRoleId,
        baseDataId: this.userInfo.baseDataId,
        highlightPreTag: '<span style="color: red">',
        highlightPostTag: '</span>',
        pageNo: 1,
        pageSize: 10,
        content: target.value
      }).then((data) => {
        this.followContentList = data.classNameSuggestDTOList || []
      })
    },
    setResourceWarningInfo (query) {
      if (query.type) {
        this.resourceWarningName = query.typeName + '-' + query.name
        this.resourceWarning = `${query.type}-${query.id}`
        this.activeTabName = '6'
        this.formData.resourceWarningType = this.resourceWarning.split('-')[0]
        this.formData.resourceWarningId = this.resourceWarning.split('-')[1]
      }
    },
    // 重置默认值
    handleReset () {
      Object.assign(this.formData, this.$options.data().formData)
      this.fastSearchArray = ['stuMobile', '']
    },
    // 搜索
    handleSearch () {
      this.$refs.resourceTable.search()
      // 快速搜索内容存储本地
      this.$refs.fastSearch.handleValueConfirm()
    },
    // 快速搜索值变更
    handleFastSearchChange (value) {
      this.formData.stuLoginName = ''
      this.formData.stuRealName = ''
      this.formData.stuMobile = ''
      this.formData[value[0]] = value[1]
    },
    async _queryTabInfo () {
      queryStuLevelCount({
        crmDeptId: this.userInfo.crmDeptId,
        crmRoleId: this.userInfo.crmRoleId,
        baseDataId: this.userInfo.baseDataId
      })
        .then((data) => {
          this.tabs = data
        })
        .catch((err) => {
          this.$Message.error({
            content: '获取用户分层失败：' + err.message
          })
        })
    },
    // 获取停用/离职顾问
    async _queryResignIdentity () {
      try {
        this.resignIdentitying = true
        const data = await queryResignConsultantsByIdentity()
        this.condition.resignIdentity =
          Object.freeze(
            data.map((item) => {
              return { ...item, userName: item.userName + '(已停用)' }
            })
          ) || []
      } catch (err) {
        this.$Message.error({
          content: '获取停用/离职顾问失败：' + err.message
        })
      }
    },
    // 根据归属类型获取归属列表
    async queryDeptList () {
      this.formData.belongDeptId = ''
      if (
        this.formData.belongDeptType === undefined ||
        this.formData.belongDeptType === 0
      ) {
        return
      }
      try {
        const data = await queryDeptListByDeptType({
          type: this.formData.belongDeptType
        })
        let deptAllList =
          data &&
          data.map((item) => {
            return item.status === 2
              ? { ...item, deptName: item.deptName + ' (已停用)' }
              : item
          }) // 停用数据
        this.condition.deptList = deptAllList
      } catch (err) {
        this.$Message.error({
          content: '获取归属失败：' + err.message
        })
      }
    },
    // 获取筛选条件-客户状态、
    async _queryResourceCondition () {
      try {
        const data = (this.resourceCondition = await queryResourceCondition())
        this.condition.cityList = Object.freeze(data.cityList) || []
        this.condition.expBehaviorThirty =
          Object.freeze(data.expBehaviorThirty) || []
        this.condition.payFirstTermName =
          Object.freeze(data.payFirstTermName) || []
        this.condition.weixinAddStatus =
          Object.freeze(data.weixinAddStatus) || []
        this.condition.belongStatusList =
          Object.freeze(data.belongStatusList) || []
        this.condition.belongTypeList = Object.freeze(data.belongTypeList) || []
        // this.tabs =
        //   data.stuLevel.map((item) => {
        //     return { id: item.id + '', name: item.name }
        //   }) || []
      } catch (err) {
        this.$Message.error({
          content: '获取筛选条件失败：' + err.message
        })
      }
    },
    // 查询绑定人
    async _queryConsultantsByIdentity () {
      try {
        const data = await queryConsultantsByIdentity()
        this.condition.consultantList = Object.freeze(data) || []
      } catch (err) {
        this.$Message.error({
          content: '获取绑定人失败：' + err.message
        })
      }
    },
    // 维护时间变更
    handleFollowDateChange (data) {
      this.formData.followStartTime = new Date(data[0] + ' 00:00:00').getTime() / 1000
      this.formData.followEndTime = new Date(data[1] + ' 00:00:00').getTime() / 1000 + 86399
    },
    // 分配日期变更
    handleAllocateDateChange (data) {
      this.formData.belongStartTime = new Date(data[0] + ' 00:00:00').getTime() / 1000
      this.formData.belongEndTime = new Date(data[1] + ' 00:00:00').getTime() / 1000 + 86399
    },
    // 绑定日期变更
    handleBindDateChange (data) {
      this.formData.bindStartTime = new Date(data[0] + ' 00:00:00').getTime() / 1000
      this.formData.bindEndTime = new Date(data[1] + ' 00:00:00').getTime() / 1000 + 86399
    },
    // 注册日期变更
    handleRegistDateChange (data) {
      this.formData.startTime = new Date(data[0] + ' 00:00:00').getTime() / 1000
      this.formData.endTime = new Date(data[1] + ' 00:00:00').getTime() / 1000 + 86399
    },
    // // 绑定状态变更
    // belongStatusChange () {
    //   if (val === 1) {
    //     this.formData.belongIdentityIdByPerson = ''
    //     this.formData.belongIdentityIdByStop = ''
    //   }
    // },
    // 根据选择模块，调整筛选项显示和隐藏
    getFormItemVisible (name) {
      // 1纯新2体验3历史在读4首缴费5多期在读
      switch (name) {
        case '停用/离职顾问':
        case '分配时间':
        case '绑定时间':
        case '快速搜索':
        case '绑定状态':
        case '绑定人':
        case '归属类型':
        case '归属':
          if (this.activeTabName === '0' || this.activeTabName === '6') {
            return true
          }
          break
        case '注册日期':
          if (
            this.activeTabName === '0' ||
            this.activeTabName === '6' ||
            this.activeTabName === '1' ||
            this.activeTabName === '2'
          ) {
            return true
          }
          break
        case '维护时间':
          if (
            this.activeTabName === '0' ||
            this.activeTabName === '6' ||
            this.activeTabName === '1' ||
            this.activeTabName === '3' ||
            this.activeTabName === '4' ||
            this.activeTabName === '2' ||
            this.activeTabName === '5'
          ) {
            return true
          }
          break
        case '最后沟通内容':
          if (
            this.activeTabName === '0' ||
            this.activeTabName === '6' ||
            this.activeTabName === '1' ||
            this.activeTabName === '2'
          ) {
            return true
          }
          break
        case '加微状态':
          if (this.activeTabName === '1' || this.activeTabName === '2') {
            return true
          }
          break
        case '近30日体检行为':
          if (this.activeTabName === '2') {
            return true
          }
          break
        case '近7日活跃行为':
          if (this.activeTabName === '3') {
            return true
          }
          break
        case '首次净缴学期':
        case '首次净缴日期':
          if (this.activeTabName === '4') {
            return true
          }
      }
    },
    handleTabClick (tabName) {
      // this.activeTabName = tabName
      this.handleReset()
    },
    handleClickItem (name) {
      this.formData.content = name
    }
  }
}
</script>

