<template>
  <div class="dic-wrapper">
    <div class="title">
      <div class="text">校内字典</div>
      <div class="btn-group">
        <Button type="primary" @click="recordShow">信息录入</Button>
        <Button @click="onExport">下载表格</Button>
      </div>
    </div>
    <div class="search-wrapper">
      <Form ref="searchForm" :model="formData" inline :label-width="80">
        <FormItem label="服务中心" prop="servicecenterId">
          <Select
            :disabled="userInfo.crmRoleId !== 2"
            class="select_w"
            v-model="formData.servicecenterId"
            @on-change="changeServicecenterId"
          >
            <Option
              v-for="(item, index) in scList"
              :value="item.deptCode"
              :key="item.deptCode + index"
              :label="item.deptName"
            >
            </Option>
          </Select>
        </FormItem>
        <FormItem label="学校" prop="schoolIdList">
          <Select class="select_minW" v-model="formData.schoolIdList" multiple>
            <Option
              v-for="item in allSchool"
              :value="item.schoolId"
              :key="item.schoolId"
              :label="item.schoolName"
            >
            </Option>
          </Select>
        </FormItem>
      </Form>
      <div>
        <Button type="primary" @click="onSearch">查询</Button>
        <Button @click="onReset">重置</Button>
      </div>
    </div>
    <SchoolTable></SchoolTable>
    <Modal v-model="recordModal" title="信息录入" footer-hide width="80%">
      <Record
        v-if="recordModal"
        :school-list="allSchool"
        :city-code="userInfo.cityCode"
      ></Record>
    </Modal>
  </div>
</template>

<script>
import { getDeptByAreaCode } from '@/api/crm'
import { queryExportDateDicInfo, exportTable } from '@/api/schoolDic'
import SchoolTable from './school-table'
import Record from './record'
import { mapState, mapActions } from 'vuex'
export default {
  name: 'schoolDic',
  components: {
    SchoolTable,
    Record
  },
  data () {
    return {
      recordModal: false,
      formData: {
        servicecenterId: '',
        schoolIdList: []
      },
      scList: [],
      initSearchParams: {
        pageSize: 10,
        pageNo: 1
      },
      queryId: 1361
    }
  },
  computed: {
    ...mapState('resource', ['allSchool', 'schoolDataSearch']),
    ...mapState('roleRelativeData', ['userInfo'])
  },
  methods: {
    ...mapActions('resource', ['queryAllSchool', 'querySchoolData']),
    recordShow () {
      this.recordModal = true
    },
    onSearch () {
      if (this.userInfo.crmRoleId !== 2) {
        this.formData.servicecenterId = this.userInfo.baseDataId
      }
      let params = {
        ...this.schoolDataSearch,
        ...this.formData
      }
      this.querySchoolData(params)
    },
    onReset () {
      this.$refs.searchForm.resetFields()
      if (this.userInfo.crmRoleId !== 2) {
        this.formData.servicecenterId = this.userInfo.baseDataId
      }
      let params = {
        ...this.initSearchParams,
        ...this.formData
      }
      this.querySchoolData(params)
    },
    // 导出
    async onExport () {
      const { schoolIdList, servicecenterId } = this.formData
      let params = {
        servicecenterId: servicecenterId,
        schoolIds: schoolIdList && schoolIdList.length > 0 ? schoolIdList : [],
        cityCode: this.userInfo.cityCode
      }
      this.$message.info({ message: '正在导出excel，请勿关闭当前标签页', duration: 5000 })
      queryExportDateDicInfo(params).then(res => {
        (function getFilePath (id, that) {
          exportTable({ id, queryId: that.queryId, cityCode: this.userInfo.cityCode }).then(data => {
            if (data.filePath) {
              window.open(data.filePath)
              that.exportIng = false
            } else {
              setTimeout(() => {
                getFilePath(id, that)
              }, 2000)
            }
          }).catch(err => {
            that.exportIng = false
            that.$message.error({ message: '导出失败：' + err.message, duration: 5000 })
          })
        })(res.id, this)
      }).catch(err => {
        this.$message.error({ message: err.message, duration: 5000 })
      })
    },
    // 获取服务中心
    queryDeptByAreaCode (val) {
      let { cityCode, baseDataId, crmRoleId } = val
      getDeptByAreaCode({
        areaCode: cityCode
      }).then(data => {
        this.scList = data
        if (crmRoleId !== 2) {
          this.formData.servicecenterId = baseDataId
        } else {
          this.formData.servicecenterId = data[0].deptCode
        }
        this.querySchoolList(this.formData.servicecenterId)
        this.initSearchParams = { ...this.initSearchParams, cityCode, servicecenterId: this.formData.servicecenterId }
        this.querySchoolData(this.initSearchParams)
      }).catch(err => {
        this.$message.error('查询服务中心列表错误：' + err.message)
      })
    },
    // 获取学校字典
    querySchoolList (id) {
      const params = {
        cityCode: this.userInfo.cityCode,
        servicecenterId: id
      }
      this.queryAllSchool(params)
    },
    changeServicecenterId (id) {
      this.querySchoolList(id)
    }

  },
  watch: {
    userInfo: {
      handler (val) {
        if (val && val.cityCode) {
          this.queryDeptByAreaCode(val)
        }
      },
      immediate: true
    }
  }
}
</script>

<style scoped lang="less">
.dic-wrapper {
  width: 100%;
  background: #f5f7fa;
  padding: 10px;

  .title {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;

    .text {
      font-size: 20px;
      font-weight: 500;
    }
  }

  .search-wrapper {
    display: flex;
    justify-content: space-between;
    height: 80px;
    padding: 24px;
    background: #ffffff;
    border-radius: 8px;
    margin-bottom: 20px;
    .select_w {
      width: 150px;
    }
    .select_minW {
      min-width: 150px;
    }
  }
}
</style>
