import { queryGradeClassNumBySchoolId, querySchoolClassList, queryOperateLog } from '@/api/schoolDic'

export default {
  data () {
    return {
      gradeList: [],
      classList: [],
      gradeId: '',
      classIds: []
    }
  },
  methods: {
    setCurrentGradeId (gradeId) {
      this.gradeId = gradeId
      this.queryClassList()
    },
    async queryClassList () {
      try {
        let params = {
          cityCode: this.cityCode,
          schoolId: this.schoolId,
          gradeId: this.gradeId
        }
        const data = await querySchoolClassList(params)
        this.classList = data.map(c => {
          c.log = {}
          return c
        })
        if (this.classList.length) {
          this.classIds = [ this.classList[0].id ]
          if (this.source === 'edit') {
            for (const c of this.classList) {
              const logs = await queryOperateLog({
                cityCode: this.cityCode,
                schoolIds: c.id
              })
              if (logs.records && logs.records.length) {
                c.log = logs.records[0]
              }
            }
          }
        }
      } catch (e) {
        this.$Message.error({
          content: '查询班级信息错误：' + e.message,
          duration: 5
        })
      }
    },
    queryGradeClassNum () {
      this.classList = []
      this.gradeList = []
      queryGradeClassNumBySchoolId({
        cityCode: this.cityCode,
        schoolId: this.schoolId
      }).then(data => {
        this.gradeList = data
        if (data.length) {
          this.gradeId = data[0].gradeId
          if (this.source === 'edit') {
            this.queryClassList()
          }
        }
      }).catch(err => {
        this.$Message.error({
          content: '查询年级、班级个数错误：' + err.message,
          duration: 5
        })
      })
    }
  }
}
