<template>
  <div style="padding: 10px;width: 100%">
    <Row style="margin-bottom: 10px">
      城市：
      <Select v-model="cityCode" style="width: 200px" @on-change="cityChange">
        <Option v-for="city in cityList" :key="city.cityCode" :value="city.cityCode">{{ city.cityName }}</Option>
      </Select>
    </Row>
    <Table :columns="advLevelCols" :data="advLevelData" style="margin-bottom: 10px">
    </Table>
    <Table :columns="advCols" :data="advData">
    </Table>
  </div>
</template>
<script>
import { queryCityAdvisorConfig, updateOrSaveLevelConfig, getUsersLevelInfo, updateUserLevel } from '@/api/crm'
export default {
  name: 'ResourceConfig',
  data () {
    return {
      cityCode: '',
      cityList: [
        { cityName: '南京', cityCode: '025' },
        { cityName: '郑州', cityCode: '0371' },
        { cityName: '成都', cityCode: '028' },
        { cityName: '西安', cityCode: '029' },
        { cityName: '悦享读', cityCode: '02501' }
      ],
      advLevelCols: [
        { title: '顾问等级', key: 'level', align: 'center' },
        {
          title: '服务上限',
          key: 'resourceLimit',
          align: 'center',
          render: (h, params) => {
            const row = this.advLevelData[params.index]
            if (row.editing) {
              return h('InputNumber', {
                style: {
                  width: '80px'
                },
                props: {
                  value: row.resourceLimit
                },
                on: {
                  'on-change': data => {
                    if (/^[+]?(\d+)$/.test(data)) {
                      row.resourceLimit = data
                    } else {
                      this.$Message.warning({ content: '请输入正整数', duration: 5 })
                    }
                  }
                }
              })
            } else {
              return h('span', row.resourceLimit)
            }
          }
        },
        {
          title: '分配资源比例',
          key: 'ratio',
          align: 'center',
          render: (h, params) => {
            const row = this.advLevelData[params.index]
            if (row.editing) {
              return h('InputNumber', {
                style: {
                  width: '80px'
                },
                props: {
                  value: row.ratio,
                  autofocus: true
                },
                on: {
                  'on-change': data => {
                    if (data >= 0 && data <= 1) {
                      row.ratio = data
                    } else {
                      this.$Message.warning({ content: '请输入0到1之前的数字', duration: 5 })
                    }
                  }
                }
              })
            } else {
              return h('span', row.ratio)
            }
          }
        },
        {
          title: '操作',
          align: 'center',
          render: (h, params) => {
            const row = this.advLevelData[params.index]
            return h('Button', {
              props: {
                type: row.editing ? 'primary' : 'warning',
                size: 'small'
              },
              on: {
                click: () => {
                  if (row.editing) {
                    updateOrSaveLevelConfig(row).then(() => {
                      this.$Message.success('更新分校顾问配置成功')
                      this.$set(row, 'editing', false)
                    }).catch(err => {
                      this.$Message.error({ content: '更新分校顾问配置失败:' + err.message, duration: 5 })
                    })
                  } else {
                    this.$set(row, 'editing', true)
                  }
                }
              }
            }, row.editing ? '保存' : '编辑')
          }
        }
      ],
      advLevelData: [],
      advCols: [
        { title: '顾问姓名', key: 'userName', align: 'center' },
        { title: '员工编号', key: 'empNo', align: 'center' },
        {
          title: '等级',
          key: 'level',
          align: 'center',
          render: (h, params) => {
            const row = this.advData[params.index]
            if (row.editing) {
              return h('Select', {
                style: {
                  width: '80px'
                },
                props: {
                  value: row.level,
                  transfer: true
                },
                on: {
                  'on-change': data => {
                    row.level = data
                  }
                }
              }, this.levelList.map(l => {
                return h('Option', {
                  props: {
                    value: l
                  }
                }, l)
              }))
            } else {
              return h('span', row.level)
            }
          }
        },
        {
          title: '操作',
          align: 'center',
          render: (h, params) => {
            const row = this.advData[params.index]
            return h('Button', {
              props: {
                type: row.editing ? 'primary' : 'warning',
                size: 'small'
              },
              on: {
                click: () => {
                  if (row.editing) {
                    row.identityId = undefined
                    updateUserLevel(row).then(() => {
                      this.$Message.success('更新顾问级别成功')
                      this.$set(row, 'editing', false)
                    }).catch(err => {
                      this.$Message.error({ content: '更新顾问级别失败:' + err.message, duration: 5 })
                    })
                  } else {
                    this.$set(row, 'editing', true)
                  }
                }
              }
            }, row.editing ? '保存' : '编辑')
          }
        }
      ],
      advData: [],
      levelList: [
        'S',
        'A',
        'B',
        'C',
        'D'
      ]
    }
  },
  methods: {
    queryAdvLevelConfig () {
      queryCityAdvisorConfig({
        areaCode: this.cityCode
      }).then(data => {
        this.advLevelData = data
      }).catch(err => {
        this.$Message.error({ content: '查询分校顾问配置失败:' + err.message, duration: 5 })
      })
    },
    queryAdvConfig () {
      getUsersLevelInfo({
        areaCode: this.cityCode
      }).then(data => {
        this.advData = data
      }).catch(err => {
        this.$Message.error({ content: '查询顾问级别列表失败:' + err.message, duration: 5 })
      })
    },
    cityChange () {
      this.queryAdvLevelConfig()
      this.queryAdvConfig()
    }
  },
  mounted () {
    this.cityCode = this.cityList[0].cityCode
    this.cityChange()
  }
}
</script>

