import { queryDiagnosisRecordIdsByPyUid } from '@/api/diagnosis'
import { queryStudentDiagnosis, queryStudentTaskActivity, queryAppClassConsult, updateStudentStatus, completeCity, getOrderByTaskId } from '@/api/crm'
import { getStudentDetail } from '@/api/student'
import { getCityList } from '@/api/bd'
import { getAppointmentIdByPyUid } from '@/api/appointment'
import { getAppointmentApplyAndRecordRList } from '@/api/appointmentApply'
import { getStudentNeedFollowTask, addFollowRecord } from '@/api/crmStudent'
import { XesAnalyticsTrack } from '@/libs/tal-sensors'
import Cookie from 'js-cookie'
export default {
  data () {
    return {
      showBackupRecordModal: false, // 候补记录
      showNoPaymentModal: false, // 报名未缴费
      diagnosisModal: false, // 诊断报告
      showDiagnosisFllowModal: false, // 散诊任务跟进
      showAuditionFollowModal: false, // 试听任务跟进
      showAuditionApplyFollowModal: false, // 试听申请任务跟进
      keyActionModal: false, // 关键行为
      maintainRecordAddModal: false, // 添加维护
      stuDetail: {},
      followDiagnosisArr: [], // 需要跟进的散诊id
      followAuditionArr: [], // 需要跟进的试听id
      followApplyArr: [], // 需要跟进的申请试听id
      activeTabId: '', // 当前tab激活的name
      diagnosisData: [], // 诊断报告数据
      diagnosisColumns: [
        {
          type: 'index',
          width: 30,
          align: 'center'
        },
        {
          title: '城市',
          key: 'cityName',
          align: 'center'
        },
        {
          title: '年级',
          key: 'gradeName',
          align: 'center'
        },
        {
          title: '学科',
          key: 'subjectName',
          align: 'center'
        },
        {
          title: '学期',
          key: 'termName',
          align: 'center'
        },
        {
          title: '学员编号',
          key: 'stuCode',
          align: 'center',
          width: 100
        },
        {
          title: '学员姓名',
          key: 'stuName',
          align: 'center'
        },
        {
          title: '分数',
          key: 'score',
          align: 'center'
        },
        {
          title: '班次级别',
          key: 'levelDegreeName',
          align: 'center'
        },
        {
          title: '班次名称',
          key: 'levelName',
          align: 'center'
        },
        {
          title: '报告URL',
          slot: 'reportUrl',
          align: 'center',
          width: 130
        },
        {
          title: '成绩来源',
          key: 'sourceTypeName',
          align: 'center'
        },
        {
          title: '班次生效时间',
          key: 'effectiveClaLevelTime',
          align: 'center'
        },
        {
          title: '成绩是否有效',
          slot: 'isValid',
          align: 'center'
        },
        {
          title: '成绩是否用于报班',
          slot: 'forRegist',
          align: 'center'
        },
        {
          title: '作答时间',
          key: 'answerDateTime',
          align: 'center',
          width: 130
        }
      ],
      keyActionData: [], // 关键行为
      cubeCols: [
        { title: '活动名称', align: 'center', key: 'activityName' },
        { title: '活动类型', align: 'center', key: 'type' },
        { title: '活动URL', align: 'center', slot: 'url' }
      ],
      maintainRecordAddData: {}, // 添加维护
      appClassConsultContent: [],
      isShowAppConsultModal: false, // 查询APP课程咨询 弹窗
      isShowCitySelectModal: false, // 城市补全提醒分配 弹窗
      cityList: [],
      distributedCityCode: '', // 城市补全提醒分配的城市
      orderDetails: [] // 订单详情
    }
  },
  methods: {
    // 获取城市列表
    getCityList () {
      getCityList()
        .then(data => {
          this.cityList = data
          if (this.cityList.length) {
            this.distributedCityCode = data[0].id
          }
        })
        .catch(err => {
          this.$Message.warning({
            content: '获取所有城市失败:' + err.message,
            duration: 5
          })
        })
    },
    // 更新类型和列表数据
    refreshFullData () {
      setTimeout(() => {
        this.taskSummary(1)
        this.taskSummary(3)
        this.$refs.taskTable1.getTableData()
        this.$refs.taskTable2.getTableData()
      }, 1500)
    },
    // 处理操作项目
    handleAction ({ type, row }) {
      // console.log(row)
      let editName = ''
      switch (type) {
        case 1:
          // console.log('散诊跟进')
          editName = '散诊跟进'
          this.followDiagnosisHandle(row)
          break
        case 2:
          // console.log('试听跟进')
          editName = '试听跟进'
          this.followAuditionHandle(row)
          break
        case 3:
          // console.log('诊断报告')
          editName = '诊断报告'
          this.diagnosisHandle(row.taskList)
          break
        case 4:
          // console.log('报班信息')
          editName = '报班信息'
          this.classInfoHandle(row)
          break
        case 5:
          // console.log('候补记录')
          editName = '候补记录'
          this.backupRecordHandle(row)
          break
        case 6:
          // console.log('关键行为')
          editName = '关键行为'
          this.showKeyAction(row)
          break
        case 7:
          // 添加维护 单独埋点
          XesAnalyticsTrack('15-5006', {
            task_name: this.activeRecordTaskType ? this.activeRecordTaskType.taskName : '全部任务'
          })
          this.maintainRecordAdd(row)
          break
        case 8:
          // console.log('试听申请跟进')
          editName = '试听申请跟进'
          this.auditionApplyHandle(row)
          break
        case 9:
          // 查询APP课程咨询
          this.appClassConsult(row)
          break
        // 励步融合
        case 10:
          // 挂起
          this.holdOn(row, 1)
          break
        case 11:
          // 报名
          this.apply(row)
          break
        // 城市补全提醒
        case 12:
          // 无效
          this.holdOn(row, 3)
          break
        case 13:
          // 挂起
          this.holdOn(row, 1)
          break
        case 14:
          // 分配
          this.showDistributedModal(row)
          break

        case 15:
          // 订单详情
          this.getOrderByTaskId(row)
          break
      }

      if (editName) {
        // 埋点
        XesAnalyticsTrack('15-5007', {
          task_name: editName
        })
      }
    },
    // 查看订单详情
    async getOrderByTaskId (row) {
      console.log(row)
      let taskId = null
      row.taskList.forEach(item => {
        if (item.taskType === 48) taskId = item.taskId
      })
      const params = { taskId }
      if (!taskId) return
      try {
        const data = await getOrderByTaskId(params)
        this.orderDetails = data
        this.$refs.clueOrderInfo.show()
      } catch (error) {
        this.$Message.error({ content: '订单详情查看失败' + error.message, duration: 5 })
      }
    },
    async showDistributedModal (row) {
      this.isShowCitySelectModal = true
      this.stuDetail = row
      if (this.cityList.length === 0) {
        this.getCityList()
      }
    },
    // 城市补全提醒任务分配
    async distribute () {
      try {
        await completeCity({ pyUid: this.stuDetail.pyUid, cityCode: this.distributedCityCode, realName: this.stuDetail.stuRealName })
        this.refreshFullData()
        this.isShowCitySelectModal = false
        this.$Message.success({ content: '分配成功', duration: 5 })
      } catch (error) {
        this.$Message.error({ content: '分配失败' + error.message, duration: 5 })
      }
    },
    // 将学员挂起或置为无效
    async holdOn (params, status) {
      try {
        await updateStudentStatus({ pyUid: params.pyUid, status })
        this.refreshFullData()
        this.$Message.success({ content: '操作成功', duration: 5 })
      } catch (error) {
        this.$Message.error({ content: '操作失败', duration: 5 })
      }
    },
    // 报名
    apply (params) {
      this.$router.push({
        name: 'applyWorkbenchForMulti',
        query: {
          cityCode: params.areaCode,
          card: params.stuLoginName
          // tab: 'classRecord'
        },
        params: {
          id: params.stuLoginName
        }
      })
    },
    addSuccess (params) { // 添加维护记录方法
      let obj = params
      obj.identityId = ''
      obj.pyUid = this.stuDetail.pyUid
      addFollowRecord(obj).then(() => {
        this.maintainRecordAddModal = false
        this.$Message.success({
          content: '添加维护记录成功',
          duration: 5
        })
        this.$refs.addrecord.setValue()
        this.refreshFullData()
      }).catch(err => {
        this.$Message.error({
          content: '添加维护记录失败：' + err.message,
          duration: 5
        })
      })
    },
    maintainRecordAdd (row) {
      this.stuDetail = { ...row }
      this.maintainRecordAddModal = true
      getStudentNeedFollowTask({
        studentUid: row.pyUid
      }).then(data => {
        this.maintainRecordAddData = data
      }).catch(err => {
        this.$Message.error({
          content: '获取信息失败：' + err.message,
          duration: 5
        })
      })
    },
    // 关键行为
    showKeyAction (row) {
      this.stuDetail.cityCode = row.areaCode
      this.stuDetail.studentId = row.studentId

      queryStudentTaskActivity({
        studentId: row.studentId,
        pyUid: row.pyUid,
        uid: row.pyUid,
        mode: 4,
        cityCode: row.areaCode
      }).then(data => {
        this.keyActionData = data
        this.keyActionModal = true
      }).catch(err => {
        this.$Message.error('查询关键行为错误：' + err.message)
      })
    },
    // 试听 跟进维护成功 刷新当前跟进维护记录
    handleFollowAuditionRecordSuccess () {
      this.refreshFullData()
    },
    // 试听 tabs点击事件
    handleFollowAuditionTabsClick (name) {
      this.followAuditionArr.forEach((item) => {
        if (item.id === name) item.init = true
      })
    },
    // 散诊 跟进维护成功 刷新当前跟进维护记录
    handleFollowDiagnosisRecordSuccess () {
      this.refreshFullData()
    },
    // 散诊 tabs点击事件
    handleFollowDiagnosisTabsClick (name) {
      this.followDiagnosisArr.forEach((item) => {
        if (item.id === name) item.init = true
      })
    },
    // 散诊 跟进维护
    async followDiagnosisHandle (row) {
      this.stuDetail = {
        realName: row.stuRealName,
        gradeName: row.stuGradeName,
        card: row.stuLoginName,
        cityCode: row.areaCode,
        areaCode: row.areaCode,
        uid: row.pyUid
      }
      try {
        // 根据任务id拿散诊记录id
        const res = await queryDiagnosisRecordIdsByPyUid({ pyUid: row.pyUid })
        res.forEach((item, index) => {
          if (index === 0) {
            this.activeTabId = item.id
            item.init = true
          } else item.init = false
        })
        this.followDiagnosisArr = res
        this.showDiagnosisFllowModal = true
      } catch (error) {
        this.$Message.error({ content: error.message, duration: 5 })
      }
    },
    // 查询APP课程咨询
    async appClassConsult (row) {
      let taskId = []
      if (row.taskList && row.taskList.length > 0) {
        row.taskList.forEach(item => {
          if (item.taskType === 40) {
            taskId.push(item.taskId)
          }
        })
      }
      taskId = taskId.length > 0 && taskId.join(',')
      try {
        const reqParams = {
          taskId
        }
        const res = await queryAppClassConsult(reqParams)
        this.appClassConsultContent = res || []
        this.isShowAppConsultModal = true
      } catch (error) {
        this.isShowAppConsultModal = false
        this.$Message.error({ content: error.message, duration: 5 })
      }
    },
    // 诊断报告
    diagnosisHandle (list) {
      try {
        let taskList = []
        list.forEach((obj) => {
          taskList.push(obj.taskId)
        })
        this.diagnosisData = []
        this.$nextTick(async () => {
          const rlt = await queryStudentDiagnosis(taskList)
          this.diagnosisData = Object.freeze(rlt)
          this.diagnosisModal = true
        })
      } catch (err) {
        this.$Message.error({
          content: '获取诊断报告失败：' + err.message
        })
      }
    },
    // 报班信息
    classInfoHandle (stu) {
      const params = {
        cityCode: stu.areaCode,
        uid: stu.pyUid
      }
      getStudentDetail(params)
        .then((res) => {
          this.stuDetail = {
            cityCode: stu.cityCode,
            studentId: res.studentId,
            bindingCode: res.bindingCode
          }
          this.showNoPaymentModal = true
        })
        .catch((err) => {
          this.$Message.error({
            content: '获取学员信息失败: ' + err.message,
            duration: 5
          })
        })
    },
    // 候补记录
    backupRecordHandle (stu) {
      const params = {
        cityCode: stu.areaCode,
        uid: stu.pyUid
      }
      getStudentDetail(params)
        .then((res) => {
          this.stuDetail = {
            cityCode: stu.cityCode,
            studentId: res.studentId,
            bindingCode: res.bindingCode
          }
          this.showBackupRecordModal = true
        })
        .catch((err) => {
          this.$Message.error({
            content: '获取学员信息失败: ' + err.message,
            duration: 5
          })
        })
    },
    // 试听 跟进维护
    async followAuditionHandle (row) {
      this.stuDetail = {
        realName: row.stuRealName,
        gradeName: row.stuGradeName,
        card: row.stuLoginName,
        cityCode: row.areaCode,
        areaCode: row.areaCode,
        uid: row.pyUid
      }
      try {
        // 根据任务id拿试听记录id
        // operationType 1: 试听任务
        let res = await getAppointmentIdByPyUid({
          pyUid: row.pyUid,
          operationType: 1
        })
        res = res.map((item, index) => {
          if (index === 0) {
            this.activeTabId = item.id
            return Object.assign(item, { init: true })
          } else {
            return Object.assign(item, { init: false })
          }
        })
        if (!res || res.length === 0) {
          this.$Message.info({ content: '试听任务已全部处理' })
          return
        }
        this.followAuditionArr = res
        this.showAuditionFollowModal = true
      } catch (error) {
        this.$Message.error({ content: error.message, duration: 5 })
      }
    },
    // 试听申请 tabs点击事件
    handleFollowApplyTabsClick (name) {
      this.followApplyArr.forEach((item) => {
        if (item.id === name) item.init = true
      })
    },
    // 试听申请 跟进维护
    async auditionApplyHandle (row) {
      try {
        let res = await getAppointmentApplyAndRecordRList({
          pyUid: row.pyUid,
          empNo: JSON.parse(Cookie.get('columbusUserInfo')).id
        })
        res = res.appointmentList.map((item, index) => {
          if (index === 0) {
            this.activeTabId = item.id
            return Object.assign(item, { init: true })
          } else {
            return Object.assign(item, { init: false })
          }
        })
        if (!res || res.length === 0) {
          this.$Message.info({ content: '试听任务已全部处理' })
          return
        }
        this.followApplyArr = res
        this.showAuditionApplyFollowModal = true
      } catch (error) {
        this.$Message.error({ content: error.message, duration: 5 })
      }
    }
  }
}
