<template>
  <Layout id="school-table">
    <Row>
      <Table
        :row-class-name="rowClassName"
        disabled-hover
        :columns="columns"
        :data="schoolData.records"
        border
        :loading="schoolDataLoading"
        @on-expand="expendChange"
        class="tableStyle"
      ></Table>
    </Row>
    <Row type="flex" justify="end">
      <Page
        :total="schoolData.totalCount"
        :current="schoolData.pageNo"
        show-total
        show-elevator
        @on-change="changePage"
      ></Page>
    </Row>
  </Layout>
</template>
<script>
import gradeTable from './grade-table.vue'
import { mapActions, mapState } from 'vuex'
export default {
  components: { gradeTable },
  data () {
    return {
      loading: false,
      isOpen: false,
      currentClickItemIndex: -1,
      allClickItemIndex: [],
      columns: [
        {
          type: 'expand',
          width: 50,
          render: (h, { row }) => {
            return h(gradeTable, {
              props: {
                schoolId: row.schoolId
              }
            })
          }
        },
        {
          title: '学校',
          key: 'schoolName',
          width: 150,
          render: (h, { row }) => {
            return h('span', row.schoolName ? row.schoolName : '--')
          }
        },
        {
          title: '人数',
          key: 'totalPeople',
          align: 'right',
          width: 150,
          render: (h, { row }) => {
            return h('span', row.totalPeople ? row.totalPeople : '--')
          }
        },
        {
          title: '线索渗透',
          key: 'address',
          align: 'center',
          children: [
            {
              title: '人数',
              key: 'music',
              align: 'right',
              render: (h, { row }) => {
                return h('span', '--')
              }
            },
            {
              title: '渗透率',
              key: 'job',
              align: 'center',
              render: (h, { row }) => {
                return h('span', '--')
              }
            }
          ]
        },
        {
          title: '注册渗透',
          key: 'registerTotal',
          align: 'center',
          children: [
            {
              title: '人数',
              key: 'registerTotalPeople',
              align: 'right',
              render: (h, { row }) => {
                return h('span', row.registerTotalPeople ? row.registerTotalPeople : '--')
              }
            },
            {
              title: '渗透率',
              key: 'registerPermRate',
              align: 'center',
              render: (h, { row }) => {
                return h('span', row.registerPermRate ? row.registerPermRate : '--')
              }
            }
          ]
        },
        {
          title: '在读渗透',
          key: 'atSchoolTotal',
          align: 'center',
          children: [
            {
              title: '人数',
              key: 'atSchoolTotalPeople',
              align: 'right',
              render: (h, { row }) => {
                return h('span', row.atSchoolTotalPeople ? row.atSchoolTotalPeople : '--')
              }
            },
            {
              title: '渗透率',
              key: 'atSchoolPermRate',
              align: 'center',
              render: (h, { row }) => {
                return h('span', row.atSchoolPermRate ? row.atSchoolPermRate : '--')
              }
            }
          ]
        },
        {
          title: '班级KOL渗透',
          key: 'total',
          align: 'center',
          children: [
            {
              title: '班级数',
              key: 'totalClass',
              align: 'right',
              render: (h, { row }) => {
                return h('span', row.totalClass ? row.totalClass : '--')
              }
            },
            {
              title: 'KOL用户',
              key: 'totalKolPeople',
              align: 'center',
              render: (h, { row }) => {
                return h('span', row.totalKolPeople ? row.totalKolPeople : '--')
              }
            },
            {
              title: '渗透率',
              key: 'kolPermRate',
              align: 'center',
              render: (h, { row }) => {
                return h('span', row.kolPermRate ? row.kolPermRate : '--')
              }
            }
          ]
        }
      ]
    }
  },
  computed: {
    ...mapState('resource', ['schoolData', 'schoolDataSearch', 'schoolDataLoading', 'gradeData']),
    ...mapState('roleRelativeData', ['userInfo'])
  },
  methods: {
    ...mapActions('resource', ['querySchoolData', 'queryGradeData']),
    expendChange (expandedRows, isOpen) {
      this.currentClickItemIndex = this.schoolData.records.findIndex(item => item.schoolId === expandedRows.schoolId)
      if (isOpen) {
        this.allClickItemIndex = [...this.allClickItemIndex, this.currentClickItemIndex]
        // 优化请求 但是想到信息录入之后还是需要重新请求，因此未做优化
        // let storeSchoolIds = Object.keys(this.gradeData)
        // if (storeSchoolIds.indexOf(expandedRows.schoolId) === -1) {
        const params = {
          schoolId: expandedRows.schoolId,
          cityCode: this.userInfo.cityCode
        }
        this.queryGradeData(params)
        // }
      } else {
        this.allClickItemIndex = this.allClickItemIndex.filter(item => item !== this.currentClickItemIndex)
      }
      this.$nextTick(() => {
        this.rowClassName(expandedRows, this.currentClickItemIndex)
      })
    },
    changePage (num) {
      let params = {
        ...this.schoolDataSearch,
        pageNum: num
      }
      this.querySchoolData(params)
    },
    rowClassName (row, index) {
      if (this.allClickItemIndex.indexOf(index) !== -1) {
        return 'backcolor_nomal'
      } else {
        return 'backcolor_no'
      }
    }
  }
}
</script>

<style lang="less">
#school-table {
  background: #fff;
  padding: 20px;

  .tableStyle {
    border-radius: 5px;
    margin-bottom: 10px;
  }

  .ivu-table .backcolor_deep td {
    background-color: #e2e2e2;
  }

  .ivu-table .backcolor_deeper td {
    background-color: #d5d5d5;
  }

  .ivu-table .backcolor_nomal td {
    background-color: #f2f2f2;
  }

  .ivu-table .backcolor_no td {
    background-color: #fff;
  }

  td.ivu-table-expanded-cell {
    padding: 0px !important;
  }

  .ivu-table-row > td:first-child {
    border-right-width: 0px;
  }

  .ivu-table-border tr:first-child th:first-child {
    border-right-width: 0px;
  }

  .ivu-table-cell-expand i {
    color: #286cff !important;
  }
  .ivu-table-border th,
  .ivu-table-border td {
    padding: 0 10px;
  }
}
</style>
