  methods: {
    saveCityStatus () {
      if (this.cityStatus) {
        if (this.cityStatus === '2') {
          if (this.distributedCityCode) {
            this.distribute()
          } else {
            this.$Message.warning('请选择分配城市')
          }
        } else {
          this.holdOn()
        }
      } else {
        this.$Message.warning('请选择操作')
      }
    },
    // 将学员挂起或置为无效
    async holdOn () {
      try {
        await updateStudentStatus({ pyUid: this.row.uid, status: this.cityStatus })
        this.getTableData()
        this.editStuCityModal = false
        this.$Message.success({ content: '操作成功', duration: 5 })
      } catch (error) {
        this.$Message.error({ content: '操作失败', duration: 5 })
      }
    },
    // 城市补全提醒任务分配
    async distribute () {
      try {
        await completeCity({ pyUid: this.row.uid, cityCode: this.distributedCityCode, realName: this.row.stuRealName })
        this.getTableData()
        this.editStuCityModal = false
        this.$Message.success({ content: '分配成功', duration: 5 })
      } catch (error) {
        this.$Message.error({ content: '分配失败' + error.message, duration: 5 })
      }
    },
    // 获取学员电话
    getStuMobile (uid, index) {
      queryStudentPhoneNum({
        uid
      }).then(data => {
        this.$set(this.tableData[index], 'stuMobile', data)
        this.$set(this.tableData[index], 'eyeHide', true)
      }).catch(err => {
        this.$Message.error('获取学员电话失败: ' + err.message)
      })
    },
    // 点击自定义表头
    handleClickCustomHeader () {
      this.customHeaderShow = true
      let stuLevel = ''
      switch (this.tabIndex) {
        case 1: stuLevel = '纯新学员'; break
        case 2: stuLevel = '仅体验学员'; break
        case 3: stuLevel = '历史在读学员'; break
        case 4: stuLevel = '首缴在读学员'; break
        case 5: stuLevel = '多期在读学员'; break
      }
      // 埋点 - 自定义表头按钮点击
      XesAnalyticsTrack('click_eo4KKSXy', { tab_name: stuLevel })
    },
    // 没配关闭之后
    handleModalClose () {
      this.search()
      this.selectedCols = []
    },
    handleSelect (cols) {
      this.selectedCols = cols.map(c => c.uid)
    },
    search () {
      console.log('search')
      this.formData.pageNo = 1
      this.getTableData()
    },
    resetTableConfig () {
      Object.assign(this.formData, this.$options.data().formData)
    },
    handleSortChange (e) {
      for (let key in this.formData) { // 删除之前选中的排序
        if (key.indexOf('Order') !== -1 && this.formData[key]) {
          delete this.formData[key]
        }
      }
      this.formData[e.key + 'Order'] = e.order === 'normal' ? undefined : e.order
      this.formData.pageNo = 1
      this.getTableData()
    },
    // 获取年级
    async getSubjectList () {
      try {
        let data = await getSubjectList({
          cityCode: this.userInfo.cityCode
        })
        data = data.map((item) => {
          return { value: item.standardId, label: item.standardName }
        })
        // this.columnTemp.find(c => c.key === 'payFirstSubName').filters = Object.freeze(data) || []
        this.columnTemp.find(c => c.key === 'lastYearReadSubNames').filters = Object.freeze(data) || []
        this.columnTemp.find(c => c.key === 'curReadSubNames').filters = Object.freeze(data) || []
      } catch (error) {
        this.$Message.error({
          content: '获取学科列表失败: ' + error.message
        })
      }
    },
    /**
     * 初始分组表头 传入分组穿梭框组件
     */
    initGroupHeaders () {
      this.groupHeaders = this.groupHeaders.map(gh => {
        gh.children = this.columnTemp.filter(ct => ct.parentKey === gh.key)
        return gh
      })
    },
    /**
     * 初始化表头
     */
    initTableHeaders () {
      queryCustomHeader({
        empNo: this.userInfo.empNo,
        tabId: this.tabIndex === 6 ? 0 : this.tabIndex
      }).then(data => {
        if (data && data.length) {
          this.defaultColumns = data.map(d => this.columnTemp.find(ct => ct.key === d.id))
        } else {
          // 资源预警自定义表头和全部资源使用同一个
          let tabIndex = this.tabIndex === 6 ? 0 : this.tabIndex
          this.defaultColumns = this.columnTempList[tabIndex].map(c => this.columnTemp.find(ct => ct.key === c.key))
        }
        console.log('columnes', this.defaultColumns)
        this.columns = this.preColumns.concat(this.defaultColumns)
        console.log('columnes', this.columns)
      }).catch(err => {
        this.$Message.error('查询表头设置失败：' + err.message)
      })
    },
    // 设置励步学员相关状态
    async setStudentTMKStatus (tableData) {
      if (tableData.length <= 0) return
      try {
        const studentUidList = tableData.map(item => item.uid)
        const data = await getCRMStudents({ cityCode: this.userInfo.cityCode, studentUidList })
        this.tableData.forEach((item, index) => {
          item.serviceCenterName = data[index].serviceCenterName
          item.serviceCenterId = data[index].serviceCenterId
          item.status = data[index].status
          item.statusName = data[index].statusName
          this.$set(tableData, index, item)
        })
      } catch (error) {
        this.$Message.error({ content: '获取TMK学员信息失败', duration: 5 })
      }
    },
    async setStudentFollowUpStatus (tableData) {
      if (tableData.length <= 0) return
      try {
        const studentUidList = tableData.map(item => item.uid)
        const data = await getStuStatus({ cityCode: this.userInfo.cityCode, studentUidList })
        this.tableData.forEach((item, index) => {
          item.followUpStatusName = data[index].statusName
          item.followUpStatus = data[index].status
          this.$set(tableData, index, item)
        })
      } catch (error) {
        this.$Message.error({ content: '获取学员跟进状态失败', duration: 5 })
      }
    },
    async getTableData () {
      try {
        this.formData.crmRoleId = this.userInfo.crmRoleId
        this.formData.crmDeptId = this.userInfo.crmDeptId
        this.formData.baseDataId = this.userInfo.baseDataId
        const params = Object.assign({}, this.searchParams, this.formData, { card: this.searchParams.stuLoginName })
        params.belongIdentityId = params.belongIdentityIdByStop || params.belongIdentityIdByPerson
        // 资源预警自定义表头和全部资源使用同一个
        let stuLevel = ''
        switch (this.tabIndex) {
          case 1: stuLevel = '纯新学员'; break
          case 2: stuLevel = '仅体验学员'; break
          case 3: stuLevel = '历史在读学员'; break
          case 4: stuLevel = '首缴在读学员'; break
          case 5: stuLevel = '多期在读学员'; break
        }
        params.stuLevel = stuLevel
        this.tableLoading = true
        const data = await getCRMStudentList(params)
        this.tableData = data.crmStudentRespList || []
        this.totalCount = data.totalCount
        await this.setStudentTMKStatus(this.tableData)
        await this.setStudentFollowUpStatus(this.tableData)
      } catch (err) {
        this.$Message.error('查询资源列表失败：' + err.message)
      } finally {
        this.tableLoading = false
      }
    },
    changePage (num) {
      this.formData.pageNo = num
      this.getTableData()
    },
    pageSizeChange (size) {
      this.formData.pageNo = 1
      this.formData.pageSize = size
      this.getTableData()
    },
    /**
     * 自定义表头确认保存
     * @param val
     */
    confirmHeaderConfig (val) {
      if (val && val.length) {
        const idNameRespList = val.map(v => {
          return {
            id: v.key,
            name: v.title
          }
        })
        setCustomHeader({
          empNo: this.userInfo.empNo,
          tabId: this.tabIndex,
          idNameRespList
        }).then(() => {
          this.$Message.success('设置表头成功')
          this.customHeaderShow = false
          this.transferLoading = false
          this.defaultColumns = idNameRespList.map(d => this.columnTemp.find(ct => ct.key === d.id))
          this.columns = this.preColumns.concat(this.defaultColumns)
        }).catch(err => {
          this.$Message.error('设置表头失败：' + err.message)
        })
      }
    },
    /****
     * button_type: 埋点区分全部选中还是部分选中
     * 0 选中 1全部
     */
    handleDistr (button_type) {
      let stuLevel = ''
      switch (this.tabIndex) {
        case 1: stuLevel = '纯新学员'; break
        case 2: stuLevel = '仅体验学员'; break
        case 3: stuLevel = '历史在读学员'; break
        case 4: stuLevel = '首缴在读学员'; break
        case 5: stuLevel = '多期在读学员'; break
      }
      // 点击分配按钮事件 埋点
      XesAnalyticsTrack('click_mzH4fJXt', { tab_name: stuLevel, button_type })
      this.$refs['ResourceDstributeModalRef'].open()
    }
    // getStatusEnums () {
    //   getStatusEnum({ cityCode: this.userInfo.cityCode }).then(data => {
    //     this.statusEnums = data
    //   }).catch(err => {
    //     this.$Message.error('获取获取学员状态枚举失败：' + err.message)
    //   })
    // }
  },
  async mounted () {
    this.initGroupHeaders()
    getCityList().then(data => {
      this.cityList = data
    }).catch(err => {
      this.$Message.error('获取城市失败：' + err.message)
    })
    if (this.userInfo.cityCode) {
      this.getSubjectList()
      this.getTableData()
      // this.getStatusEnums()
    }
  }
}
