<script>
import resourceTable from '@/components/resource/resourceTable.vue'
import DispatchDialog from '_c/resource/DispatchDialog.vue'
import ResourceRecordSearchDialog from '_c/resource/resourceRecordSearchDialog.vue'
import {
  queryConsultantsByIdentity,
  queryResourceCondition,
  queryDeptListByDeptType,
  getCRMStudentList,
  queryResignConsultantsByIdentity
} from '../../../api/crm'
// import Cookie from 'js-cookie'
// const urls = {
//   getStuList: '/xinxin/i/resource/list',
//   getStuStatics: '/xinxin/i/stu/getStudentStatistics',
//   getStuCondition: '/xinxin/i/stu/getStudentCondition',
//   getDepartByType: '/xinxin/i/depart/getDepartByType',
//   getDepartChilds: '/xinxin/i/depart/children',
//   getqueryUserList: '/xinxin/i/user/queryUserListByServiceCenter',
//   getTags: '/xinxin/i/tag/system'
// }
export default {
  name: 'resourceList',
  components: {
    resourceTable,
    DispatchDialog,
    ResourceRecordSearchDialog
  },
  data () {
    return {
      pickerOption: {
        disabledDate (time) {
          return time.getTime() > Date.now()
        }
      },
      tableData: [],
      overallStat: {},
      consultantLoading: false,
      consultantLoaded: false,
      resignIdentityLoading: false,
      resignIdentityLoaded: false,
      condition: {
        // cityList: [],
        belongStatusList: [],
        belongTypeList: [],
        customActiveStatusList: [],
        distributeStatusList: [],
        readStatusList: [],
        gradeList: [],
        consultantList: [],
        deptList: [],
        resignIdentity: []
      },
      channel: '',
      curriculumTime: [],
      form: {
        startTime: '',
        endTime: '',
        activeStatus: '',
        allotStatus: '',
        followStatus: '',
        belongIdentityId: '',
        readStatus: '',
        areaCode: '',
        gradeId: [],
        tagId: '',
        belongStatus: '',
        bindStatus: '',
        belongDeptType: '',
        belongDeptId: '',
        userTag: [],
        pageNo: 1,
        pageSize: 20,
        belongIdentityIdByStop: '',
        belongIdentityIdByPerson: '',
        resourceWarning: ''
      },
      fastSearchType: 'stuMobile',
      fastSearch: '',
      deptList: [],
      registerTime: '',
      // belongFromTime: '',
      // followLastestTime: '',
      pageSize: 20,
      pageNo: 1,
      total: 0,
      flag: '',
      loading: false,
      tags: [],
      bindConsults: [],
      flagShow: false,
      disShow: false,
      disPersonShow: false,
      // empNo: JSON.parse(Cookie.get('columbusUserInfo')).id
      resourceWarningList: []
    }
  },
  computed: {
    identityInfo () {
      let roleRelativeData = this.$store.state.roleRelativeData
      // let activeIdentityId = roleRelativeData.roleList.activeIdentityId
      // let list = roleRelativeData.roleList.list
      // if (list) {
      //   let activeItem = list.filter(item => {
      //     return item.identityId === activeIdentityId
      //   })
      //   let res = {
      //     departId: activeItem[0].departId,
      //     departName: activeItem[0].departName,
      //     crmRoleId: activeItem[0].crmRoleId,
      //     roleName: activeItem[0].roleName
      //   }
      //   return res
      // } else {
      //   return {}
      // }
      return roleRelativeData.userInfo
    },
    crmRoleId () {
      return this.identityInfo.crmRoleId
    },
    roleName () {
      return this.identityInfo.roleName
    },
    departId () {
      return this.identityInfo.crmDeptId
    },
    departName () {
      return this.identityInfo.crmDepartName
    },
    roleInfo () {
      return this.$store.state.roleRelativeData.roleInfo
    },
    userInfo () {
      return this.$store.state.roleRelativeData.userInfo
    }
  },
  watch: {
    fastSearch (val, old) {
      this.form[this.fastSearchType] = val
    },
    fastSearchType (val, old) {
      this.form[old] = ''
      this.form[val] = this.fastSearch
    },
    departId () {
      this.flagShow = this.ifShow()
    }
  },
  mounted () {
    // this.getDepartList(false)
    // let lastVisitedTime = window.localStorage.getItem('lastVisitedTime')
    // console.log(lastVisitedTime)
    // this.flagShow = this.ifShow()
    // // 获取在读年级
    // this.queryStandardGradeListHandler()
    // this.queryRegisterCityListHandler()
    this.search()
    this.queryResourceConditionHandler()
    // this.queryHandler()
  },
  beforeRouteLeave (to, from, next) {
    let trackId = null
    let button_name = ''
    // 创建群发
    if (to.name === 'apppushmsg') {
      trackId = '15-5127'
      button_name = '资源列表创建群发按钮'
      // 群发历史
    } else if (to.name === 'pushmsglist') {
      trackId = '15-5126'
      button_name = '资源列表群发历史'
    }
    if (trackId) {
      XesAnalytics.track(trackId, {
        button_name
      })
    }
    next()
  },
  methods: {
    // 搜索埋点
    searchTrack (formData) {
      XesAnalytics.track('15-5020', {
        query_json: formData
      })
    },
    async queryHandler (type) {
      this.disPersonShow = true
      if (type && !this.consultantLoaded) {
        try {
          this.consultantLoading = true
          const data = await queryConsultantsByIdentity()
          this.condition.consultantList = Object.freeze(data) || []
          this.consultantLoaded = true
          this.consultantLoading = false
        } catch (err) {
          this.$Message.error({
            content: '获取绑定人失败：' + err.message
          })
        }
      }
    },

    changeStatus (type) {
      if (type === '') {
        this.disShow = false
        this.disPersonShow = false
      }
    },
    async queryResignIdentity (type) {
      this.disShow = true
      if (type && !this.resignIdentityLoaded) {
        try {
          this.resignIdentitying = true
          const data = await queryResignConsultantsByIdentity()
          this.condition.resignIdentity =
            Object.freeze(
              data.map((item) => {
                return { ...item, userName: item.userName + '(已停用)' }
              })
            ) || []
          this.resignIdentityLoaded = true
          this.resignIdentityLoading = false
        } catch (err) {
          this.$Message.error({
            content: '获取停用/离职顾问失败：' + err.message
          })
        }
      }
    },
    async queryResourceConditionHandler () {
      try {
        const data = await queryResourceCondition({
          // empNo: this.empNo
        })
        // this.condition.cityList = Object.freeze(data.cityList) || []
        this.condition.belongStatusList =
          Object.freeze(data.belongStatusList) || []
        this.condition.belongTypeList = Object.freeze(data.belongTypeList) || []
        this.condition.customActiveStatusList =
          Object.freeze(data.customActiveStatusList) || []
        this.condition.distributeStatusList =
          Object.freeze(data.distributeStatusList) || []
        this.condition.readStatusList = Object.freeze(data.readStatusList) || []
        this.condition.gradeList = Object.freeze(data.gradeList) || []
        this.condition.userTagList = Object.freeze(data.userTagList) || []
      } catch (err) {
        this.$Message.error({
          content: '获取筛选条件失败：' + err.message
        })
      }
    },
    handleSizeChange (val) {
      this.pageSize = val
      this.pageNo = 1
      this.form.pageSize = val
      this.form.pageNo = 1
      this.getResourceList()
    },
    handleCurrentChange (val) {
      this.pageNo = val
      this.form.pageNo = val
      this.getResourceList()
    },
    search () {
      this.pageNo = 1
      let form = { ...this.form }
      form.startTime =
        this.registerTime && this.registerTime[0]
          ? this.registerTime[0] / 1000
          : ''
      form.endTime =
        this.registerTime && this.registerTime[1]
          ? this.registerTime[1] / 1000 + 86399
          : ''
      if (this.flagShow) {
        form.channel = this.channel
        form.curriculumTime = this.curriculumTime
      }
      form.stuMobile = ''
      form.stuRealName = ''
      form.card = ''
      form[this.fastSearchType] = this.fastSearch
      // form.empNo = this.empNo
      form.pageNo = this.pageNo
      form.pageSize = this.pageSize
      form.isDeactivate = form.belongDeptId === 1 ? 1 : 0
      form.belongIdentityId = this.disShow
        ? form.belongIdentityIdByStop
        : form.belongIdentityIdByPerson

      form.resourceWarningType = form.resourceWarning.split('-')[0]
      form.resourceWarningId = form.resourceWarning.split('-')[1]
      form.crmRoleId = this.identityInfo.crmRoleId
      form.crmDeptId = this.identityInfo.crmDeptId
      form.baseDataId = this.identityInfo.baseDataId

      this.form = { ...form }
      delete form.belongIdentityIdByStop
      delete form.belongIdentityIdByPerson
      this.getResourceList(form)
      // 搜索埋点
      this.searchTrack(form)
    },
    async getResourceList (_form) {
      if (!_form) _form = this.form
      this.loading = true
      try {
        const data = await getCRMStudentList(_form)
        this.tableData = Object.freeze(data.crmStudentRespList) || []
        this.total = data.totalCount
      } catch (err) {
        this.$Message.error({
          content: '获取资源列表失败：' + err.message
        })
      } finally {
        this.loading = false
      }
    },
    async getDepartList () {
      this.form.belongDeptId = ''
      if (this.form.belongDeptType === '' || this.form.belongDeptType === 0) {
        return
      }
      try {
        const data = await queryDeptListByDeptType({
          type: this.form.belongDeptType
        })
        let deptAllList =
          data &&
          data.map((item) => {
            return item.status === 2
              ? { ...item, deptName: item.deptName + ' (已停用)' }
              : item
          }) // 停用数据
        this.condition.deptList = deptAllList
      } catch (err) {
        this.$Message.error({
          content: '获取归属失败：' + err.message
        })
      }
    },
    sortchange (sortProps) {
      this.form[sortProps.prop] = sortProps.order
      this.form.pageNo = 1
      this.getResourceList()
    },
    runEnv () {
      // 运行环境判断
      if (window) {
        const location = window.location.hostname
        let departId = 0
        if (
          location.indexOf('.dev.') > -1 ||
          process.env.NODE_ENV === 'development'
        ) {
          // hj = 'dev'
          departId = 707
        } else if (location.indexOf('.qa.') > -1) {
          // hj = 'beta'
          departId = 2343523
        } else {
          // hj = 'prod'
          departId = 937
        }
        return departId
      }
    },
    ifShow () {
      if (this.departId === this.runEnv()) {
        return true
      } else {
        return false
      }
    }
  }
}
</script>

