<template>
  <el-select
    size="small"
    v-model="grade"
    placeholder="请选择"
    @change="changGrade">
    <el-option
      v-for="item in gradeList"
      :key="item.id"
      :label="item.name"
      :value="String(item.id)"/>
  </el-select>
</template>
<script>
import { queryStandardGradeList } from '../../api/bd'
export default {
  props: {
    hasAll: {
      type: Boolean,
      default: false
    },
    initValue: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      gradeList: [],
      grade: ''
    }
  },
  watch: {
    initValue (val) {
      this.grade = val
    }
  },
  mounted () {
    this.getGradeList()
  },
  methods: {
    changGrade (val) {
      this.$emit('getGrade', val)
    },
    async getGradeList () {
      try {
        let data = await queryStandardGradeList()
        if (data && data.length > 0 && this.hasAll) {
          data = [{ id: '', name: '全部' }].concat(data)
        }
        this.gradeList = Object.freeze(data) || []
      } catch (err) {
        this.$Message.error({
          content: '获取权限列表失败：' + err.message
        })
      }
    }
  }
}
</script>

