<template>
   <div>
    <Modal
      title="资源分配"
      v-model="dialogVisible"
      :closable="false"
      :mask-closable="false"
      width="960"
      class="resource-distribute"
      >
      <Tabs class="tabs_list" :animated="false" name="resource" v-model="activeTabName">
        <TabPane label="资源分配" name="resourceDistr">
          <Form class="form-container" :model="formData" :label-width="90" >
           <FormItem label="资源人数" prop="gender" >
             <span style="transform: translate(0, 5px);">{{ pyUids.length || fullNum }} 人</span>
           </FormItem>
           <FormItem label="分配模式" prop="gender">
              <RadioGroup v-model="formData.modal">
                <Tooltip :transfer="true" placement="top"  max-width="250" :content="tipText">
                  <Radio label="0">百分比分配</Radio>
                </Tooltip>
                <Radio label="1">数量分配</Radio>
              </RadioGroup>
           </FormItem>
           <FormItem label="分配详情" prop="gender">
            <div class="dist_detail">
              <div class="table_header">
                <div>
                  <ResourceDistributeForm :ref="item.ref"
                    v-for="(item, index) in addForm" :key="index"
                    :distributeType="Number(formData.modal)"
                    :acceptType="item.acceptType"
                    :isLast="index === addForm.length - 1"
                    :leftNum="item.leftNum" @delete="handleDelete(index)" @change="handleNumChange" />
                </div>
                <div v-if="!isDistributeOver && addForm.length < 10">
                  <Dropdown trigger="click" @on-click="handleDistributeDetail" transfer>
                    <Button type="primary" ghost icon='md-add'>添加</Button>
                      <DropdownMenu slot="list" transfer>
                        <DropdownItem name="1">城市</DropdownItem>
                        <DropdownItem name="2">服务中心</DropdownItem>
                        <DropdownItem name="3">顾问</DropdownItem>
                      </DropdownMenu>
                  </Dropdown>
                </div>
              </div>
            </div>
           </FormItem>
           <Alert type="warning" show-icon>确定分配后，操作无法撤销，请仔细检查分配信息</Alert>
          </Form>
        </TabPane>
        <TabPane label="分配记录" name="distributeRecords">
           <ResourceRecordSearch ref="rrs" />
        </TabPane>
      </Tabs>
      <div slot="footer">
        <Button @click="handleClose" :loading="loading">取 消</Button>
        <Button v-show="activeTabName === 'resourceDistr'" type="primary" @click="handleSubmit" :disabled="addForm.length === 0" :loading="loading">确 定</Button>
      </div>
    </Modal>
    <Modal
        title="确认分配"
        v-model="comfirmDist"
        :closable="false"
        ok-text="确认分配"
        @on-ok="handleConfirm"
        >
        <Row type="flex" align="middle" v-for="(item,index) in confirmParams" :key="index" style="margin-bottom: 10px;">
          <Col :span="3" class="f-14">接收方：</Col>
          <Col :span="8" v-if="item.type === 1">{{ item.cityName }}</Col>
          <Col :span="8" v-if="item.type === 2">{{ item.centerName }}</Col>
          <Col :span="8" v-if="item.type === 3">{{ item.adviserName }}</Col>
          <Col :span="3" v-if="formData.modal === '0'">{{  item.rate * 100  }}%</Col>
          <Col :span="3">{{  item.dispatchCount }}人</Col>
        </Row>
    </Modal>
   </div>
</template>

<script>
import ResourceRecordSearch from './resource-record-search.vue'
import { mapState } from 'vuex'
import ResourceDistributeForm from './resource-distribute-form.vue'
import { batchDispatchStudent, specifyDispatchCount } from '@/api/crm'
import { XesAnalyticsTrack } from '@/libs/tal-sensors'
export default {
  name: 'resourceDistributeModal',
  components: {
    ResourceRecordSearch,
    ResourceDistributeForm
  },
  provide () {
    return { parent: this }
  },
  props: {
    pyUids: {
      type: Array,
      default: () => []
    },
    searchParams: {
      type: Object,
      default: () => {
        return {}
      }
    },
    tabIndex: {
      type: Number,
      default: 0
    },
    fullNum: {
      type: Number,
      default: 0
    }
  },
  watch: {
    'formData.modal': {
      deep: true,
      handler () {
        this.handleNumChange()
        this.addForm.forEach((item) => {
          // 切换模式，清空分配数据
          if (this.$refs[item.ref]) {
            this.$refs[item.ref][0].formData.num = null
            item.leftNum =
              this.formData.modal === '0' ? 100 : (this.pyUids.length || this.fullNum)
          }
        })
      }
    },
    activeTabName (tabName) {
      if (tabName === 'distributeRecords') {
        this.$refs.rrs.getLogList(null, 1)
      }
    }
  },
  data () {
    return {
      activeTabName: 'resourceDistr',
      formData: {
        modal: '1'
      },
      detailForm: false,
      areaList: [],
      cityList: [],
      serverList: [],
      adviserList: [],
      // distDetailFormList: [],
      // roleMap: {
      //   0: { city: 'none', area: 'none', center: 'none', person: 'none' }, // 顾问
      //   1: { city: 'none', area: 'none', center: 'none', person: 'hidden' }, // 服务中心
      //   2: { city: 'none', area: 'hidden', center: 'hidden', person: 'hidden' }, // 城市
      // },
      // currentRole: '',
      dialogVisible: false,
      addForm: [],
      resetNum: 100,
      currentIndex: '',
      comfirmDist: false,
      isDistributeOver: false, // 资源是否分配完
      loading: false,
      confirmParams: [], // 确认提示内容
      tipText:
        '"百分比分配"模式下，计算数据向上取整，可能会造成最后接收方，接收数量比预期少。例如：总计 5个学员待分配，分别为：50%，25%，25%，分配三个接收方，则三个接收方，接收资源数，分别为：3，2，0个资源。',
      isSuccess: false // 是否分配成功
    }
  },
  computed: {
    ...mapState('roleRelativeData', ['userInfo']),
    areaCode () {
      return function (cityId) {
        const city = this.cityList.find((c) => String(c.id) === cityId)
        if (city) {
          return city.deptCode
        }
        return ''
      }
    }
  },
  methods: {
    distributeOver () {
      // 最多10个接收方

      let leftNum = 0
      // 百分比分配
      if (this.formData.modal === '0') {
        leftNum = 100
        // 刷领分配
      } else {
        leftNum = this.pyUids.length || this.fullNum
      }
      this.addForm.forEach((item) => {
        if (this.$refs[item.ref]) {
          leftNum -= this.$refs[item.ref][0].formData.num
        }
      })
      console.log('left 判断是佛超出限制', this.formData.modal, this.pyUids.length, leftNum)
      this.isDistributeOver = leftNum <= 0
    },
    // 子组件分配量变更
    handleNumChange () {
      this.distributeOver()
      const leftNum = this.getLeftNum()
      if (this.addForm.length) {
        if (leftNum > 0) {
          this.addForm[this.addForm.length - 1].leftNum = leftNum
        } else {
          this.addForm[this.addForm.length - 1].leftNum = 0
        }
        const lastAddForm = this.addForm[this.addForm.length - 1]
        this.$refs[lastAddForm.ref][0].validateField()
      }
    },
    // 获取剩余分配量
    getLeftNum (modal) {
      let leftNum = 0
      // 百分比分配
      if (this.formData.modal === '0' || modal === '0') {
        leftNum = 100
        // 刷领分配
      } else {
        leftNum = this.pyUids.length || this.fullNum
      }

      this.addForm.forEach((item, index) => {
        if (index !== this.addForm.length - 1) {
          leftNum -= this.$refs[item.ref][0].formData.num
        }
      })
      return leftNum < 0 ? 0 : leftNum
    },
    // handleTabClick (tabName) {
    //   // 为什么要用vuex?
    //   if (tabName === 'distributeRecords') {
    //     this.$store.commit('resource/setIdentity', 'records')
    //   } else {
    //     this.$store.commit('resource/setIdentity', '')
    //   }
    // },
    handleDistributeDetail (val) {
      // this.detailForm = true
      // this.currentRole = val
      // this.distDetailFormList = [...this.distDetailFormList, this.roleMap[val]]
      this.addForm.push({
        acceptType: Number(val),
        leftNum: this.getLeftNum(),
        ref: 'ref' + Date.now()
      })
    },
    // 移除分配
    handleDelete (i) {
      this.addForm.splice(i, 1)
      this.distributeOver()
    },
    async handleConfirm () {
      try {
        this.formData.crmRoleId = this.userInfo.crmRoleId
        this.formData.crmDeptId = this.userInfo.crmDeptId
        this.formData.baseDataId = this.userInfo.baseDataId
        const params = Object.assign({}, this.searchParams, this.formData, { card: this.searchParams.stuLoginName })
        params.belongIdentityId = params.belongIdentityIdByStop || params.belongIdentityIdByPerson
        // 资源预警自定义表头和全部资源使用同一个
        let stuLevel = ''
        switch (this.tabIndex) {
          case 1: stuLevel = '纯新学员'; break
          case 2: stuLevel = '仅体验学员'; break
          case 3: stuLevel = '历史在读学员'; break
          case 4: stuLevel = '首缴在读学员'; break
          case 5: stuLevel = '多期在读学员'; break
        }
        params.stuLevel = stuLevel
        this.loading = true
        const params2 = {
          targetList: this.confirmParams,
          cityCode: this.userInfo.cityCode,
          areaCode: this.userInfo.cityCode,
          operateEmpNo: this.userInfo.empNo,
          pyUidList: this.pyUids,
          ...params
        }
        await batchDispatchStudent(params2)
        this.$Message.success('分配成功')
        this.$emit('success')
        this.isSuccess = true
        this.activeTabName = 'distributeRecords'
      } catch (error) {
        this.$Message.error({
          content: '资源分配失败: ' + error.message,
          duration: 5
        })
      }
      this.loading = false
      // 埋点 分配
      XesAnalyticsTrack('click_eDGdU5nk', {
        num: this.addForm.length,
        module_name: this.formData.modal === '0' ? '百分比' : '数量',
        tab_name: stuLevel,
        user_num: this.pyUids.length,
        button_type: this.pyUids.length ? 0 : 1 // 0选中分配 1全部分配
      })
    },
    async handleSubmit () {
      let valid = true
      let targetList = []
      for (let i = 0; i < this.addForm.length; i++) {
        const item = this.addForm[i]
        const child = this.$refs[item.ref][0]
        let valid1 = await child.validate()
        if (!valid1) {
          valid = valid1
        }

        let acceptType = child.acceptType
        let num = child.formData.num
        let targetId
        switch (acceptType) {
          case 3:
            targetId = child.formData.adviserId
            break
          case 2:
            targetId = child.formData.centerId
            break
          case 1:
            targetId = child.formData.cityId
            break
        }

        // 数量
        if (child.distributeType === 1) {
          targetList.push({
            targetId,
            dispatchCount: num,
            type: acceptType,
            distributeType: child.distributeType,
            cityName: child.cityName,
            centerName: child.centerName,
            adviserName: child.adviserName
          })
          // 百分比
        } else {
          targetList.push({
            targetId,
            rate: (num / 100).toFixed(2),
            type: acceptType,
            distributeType: child.distributeType,
            cityName: child.cityName,
            centerName: child.centerName,
            adviserName: child.adviserName
          })
        }
      }

      if (valid) {
        const params = {
          targetList,
          cityCode: this.userInfo.cityCode,
          areaCode: this.userInfo.cityCode,
          operateEmpNo: this.userInfo.empNo,
          pyUidList: this.pyUids
        }
        try {
          const data = await specifyDispatchCount(params)
          this.comfirmDist = true
          this.confirmParams = data.targetList
        } catch (error) {
          this.$Message.error({
            content: '分配失败: ' + error.message,
            duration: 5
          })
        }
      }
    },
    changeNum (e, item, index) {
      this.currentIndex = index
    },
    open () {
      this.dialogVisible = true
      this.isSuccess = false
      this.isDistributeOver = false
    },
    handleClose () {
      this.addForm = []
      // this.distDetailFormList = []
      this.dialogVisible = false
      if (this.isSuccess) {
        this.$emit('close')
      }
      Object.assign(this.$data, this.$options.data())
    }
  }
}
</script>

<style scoped lang="less">
.tabs_list {
  overflow-y: scroll;
  .dist_detail {
    padding-bottom: 25px;
    .table_header {
      span.first_label,
      span.last_label {
        font-size: 12px;
        line-height: 22px;
        margin-right: 16px;
        margin-top: 5px;
      }
      .detail_form {
        display: flex;
        border-bottom: 1px solid #f0f0f0;
        padding: 0px 0 25px 0;
        margin-bottom: 25px;
        div.dist_detail_item {
          /deep/.ivu-form-item-content {
            width: 110px;
            flex: 1;
          }
        }
      }
    }
  }
  .icon_delete {
    font-size: 14px;
    cursor: pointer;
    display: inline;
  }

  /deep/ .ivu-form .ivu-form-item-label {
    padding: 10px 32px 10px 0;
    font-size: 14px;
    transform: translate(0, -2px);
  }

  /deep/ .ivu-tabs-bar {
    border-bottom: none;
  }
}

.resource-distribute {
  /deep/ .ivu-modal-body {
    max-height: 500px;
    overflow: auto;
  }
}

.f-14 {
  font-size: 14px;
}
</style>
