              var result = '' + parseInt(secondTime) + '秒'
              if (minuteTime > 0) {
                result = '' + parseInt(minuteTime) + '分' + result
              }
              if (hourTime > 0) {
                result = '' + parseInt(hourTime) + '小时' + result
              }
              return result
            }
            let responseTime = '--秒'
            if (params.row.avgReactionCost) {
              responseTime = secToTimestr(params.row.avgReactionCost)
            } else {
              responseTime = '--秒'
            }
            return h(
              'p',
              responseTime
            )
          }
        }
      ],
      stuGradeIdList: [], // 年级列表
      dateOptions: {
        shortcuts: [
          {
            text: '近一周',
            value () {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              return [start, end]
            }
          },
          {
            text: '近一个月',
            value () {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              return [start, end]
            }
          },
          {
            text: '近三个月',
            value () {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
              return [start, end]
            }
          }
        ]
      },
      dateRange: [] // 维护时间
    }
  },
  mounted () {
    if (this.tableType === 1) {
      this.columns = [...this.columns1]
    } else if (this.tableType === 2) {
      this.columns = [...this.columns2]
    } else if (this.tableType === 3) {
      this.columns = [...this.columns3]
    } else if (this.tableType === 4) {
      this.columns = [...this.columns4]
    }

    if (this.tableType === 1 || this.tableType === 2) {
      this.type = 1 // 待维护
    } else if (this.tableType === 3 || this.tableType === 4) {
      this.type = 3 // 已维护
    }

    if (this.type === 1) {
      this.getGradeList()
    } else {
      this.initDate()
    }
  },
  methods: {
    callShow (cityCode) { // 外呼组件是否灰度显示
      const callGrayCity = this.$store.state.app.callGrayCity
      if ((!callGrayCity || !callGrayCity.length || callGrayCity.includes(cityCode)) && this.$store.state.roleRelativeData.roleIds.includes(this.$store.state.roleRelativeData.userInfo.crmRoleId)) {
        return true
      }
      return false
    },
    // 初始化维护时间 近7天
    initDate () {
      const end = new Date()
      const start = new Date()
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
      this.dateRange = [start, end]
      this.followStartDate = dateFormat(start, 'yyyy-MM-dd') + ' 00:00:00'

      this.followEndDate = dateFormat(end, 'yyyy-MM-dd') + ' 23:59:59'
    },
    // 维护时间筛选监听
    handleDateRangeChange (data) {
      const [start, end] = data
      this.followStartDate = start
      this.followEndDate = end
      if (start) {
        this.followStartDate = start + ' 00:00:00'
      }
      if (end) {
        this.followEndDate = end + ' 23:59:59'
      }

      XesAnalyticsTrack('15-5008', {
        task_name: data
      })

      this.getTableData()
    },
    // 获取年级
    async getGradeList () {
      try {
        let data = await queryStandardGradeList()
        data = [ { value: 99, label: '未填写' } ].concat(data.map((item) => {
          return { value: item.id, label: item.name }
        }))
        this.columns1[6].filters = Object.freeze(data) || []
        this.columns2[6].filters = Object.freeze(data) || []
      } catch (error) {
        this.$Message.error({
          content: '获取年级列表失败: ' + error.message
        })
      }
    },
    // 操作项目聚合
    aggrActions (row) {
      const actions = []
      if (row.diagnosisTaskContains) {
        actions.push({ value: 1, label: '散诊跟进' })
      }
      if (row.auditionsTaskContains) {
        actions.push({ value: 2, label: '试听跟进' })
      }
      if (row.isStudentDiagnosis) {
        actions.push({ value: 3, label: '诊断报告' })
      }
      if (row.isStuHasReg) {
        actions.push({ value: 4, label: '报班信息' })
      }
      if (row.isStuHasWait) {
        actions.push({ value: 5, label: '候补记录' })
      }
      if (row.isStuFollowUp) {
        actions.push({ value: 6, label: '关键行为' })
      }
      if (row.isAddSave || this.tableType === 3) {
        actions.push({ value: 7, label: '添加维护' })
      }
      if (row.auditionApplyFollow) {
        actions.push({ value: 8, label: '试听申请跟进' })
      }
      if (row.isQueryAppClassConsult) {
        actions.push({ value: 9, label: '查询APP课程咨询' })
      }
      // 励步融合
      if (row.isLibu === 1) {
        if (row.stuStatus === 0) {
          actions.push({ value: 10, label: '挂起' })
          actions.push({ value: 11, label: '报名' })
        } else if (row.stuStatus === 1) {
          actions.push({ value: 11, label: '报名' })
        }
      }
      // 城市补全提醒
      if (row.isCityComplete) {
        // stuStatus 0 待维护 1 挂起 2 已维护 3无效
        if (row.stuStatus === 0) {
          actions.push({ value: 12, label: '无效' })
          actions.push({ value: 13, label: '挂起' })
          actions.push({ value: 14, label: '分配' })
        }

        if (row.stuStatus === 1) {
          actions.push({ value: 12, label: '无效' })
          actions.push({ value: 14, label: '分配' })
        }

        if (row.stuStatus === 3) {
          actions.push({ value: 13, label: '挂起' })
          actions.push({ value: 14, label: '分配' })
        }
      }

      if (row.isOrderExchange) {
        actions.push({ value: 15, label: '订单详情' })
      }
      return actions
    },
    resetTableConfig () {
      this.tableConfig.pageSize = 10
      this.tableConfig.pageNo = 1
    },
    // 搜索
    search () {
      this.resetTableConfig()
      this.getTableData()
    },
    // 处理排序
    handleSortChange (e) {
      // 处理排序问题处理排序问题处理排序问题处理排序问题处理排序问题处理排序问题 出参入参一致
      this[e.key + 'Order'] = e.order
      this.resetTableConfig()
      this.getTableData()
    },
    changePage (num) {
      this.tableConfig.pageNo = num
      this.getTableData()
    },
    pageSizeChange (size) {
      this.tableConfig.pageNo = 1
      this.tableConfig.pageSize = size
      this.getTableData()
    },
    async getTableData (taskType) {
      const userInfo = this.$store.state.roleRelativeData.userInfo
      if (taskType) {
        this.taskType = taskType
        this.tableConfig.pageNo = 1
        this.tableConfig.pageSize = 10
        this.tableConfig.hasNextPage = false
      }
      if (taskType === 0) {
        this.taskType = null
        this.tableConfig.pageNo = 1
        this.tableConfig.pageSize = 10
        this.tableConfig.hasNextPage = false
      }

      const params = {
        cityCode: userInfo.cityCode,
        crmDeptId: userInfo.crmDeptId,
        crmRoleId: userInfo.crmRoleId,
        [this.selectValue]: this.inputValue, // 学员姓名、手机号、学员编号
        taskType: this.taskType,
        type: this.type, // 1待维护，2预约，3已维护
        stuGradeIdList: this.stuGradeIdList || [], // 年级
        followNumOrder: this.followNumOrder, // 维护次数排序
        taskTimeLastOrder: this.taskTimeLastOrder, // 最长任务时长排序
        followStartDate: this.followStartDate, // 维护时间
        followEndDate: this.followEndDate, // 维护时间
        ...this.tableConfig
      }

      try {
        this.tableConfig.tableLoading = true
        const data = await advisorTaskList(params)
        this.tableConfig.totalCount = data.totalCount
        this.tableConfig.hasNextPage = data.hasNextPage
        data.records.forEach((item) => {
          item.actions = this.aggrActions(item) || []
          if (item.taskTimeLast >= 70 * 60 * 60) {
            item.taskTimeLast = '超过70小时'
          } else if (item.taskTimeLast) {
            item.taskTimeLast = secToTimestr(item.taskTimeLast)
          } else {
            item.taskTimeLast = '--'
          }

          let taskTypeStr = ''

          if (item.taskList && item.taskList.length > 0) {
            taskTypeStr = item.taskList
              .reduce((pre, cur) => {
                return pre + cur.taskName + '、'
              }, taskTypeStr)
              .slice(0, -1)
          }

          item.taskTypeStr = taskTypeStr
        })
        this.table = data.records
      } catch (error) {
        const name = this.type === 1 ? '获取待维护任务失败' : '获取维护记录失败'
        this.$Message.error({
          content: `${name}: ${error.message}`,
          duration: 5
        })
      }

      setTimeout(() => {
        this.tableConfig.tableLoading = false
      }, 500)
    }
  }
}
</script>

