<template>
  <div class="h-scroll">
    <div class="arrow left"
         :class="{'disabled': scrollLeft === 0 || fullChildrenWidth <= scrollBoxWidth}"
         @click="scroll('left')">
      <Icon type="ios-arrow-back" />
    </div>
    <div class="arrow right"
         :class="{'disabled': scrollLeft === fullChildrenWidth - scrollBoxWidth || fullChildrenWidth <= scrollBoxWidth}"
         @click="scroll('right')">
      <Icon type="ios-arrow-forward" />
    </div>
    <div class="scroll-box" ref="scrollBox"><slot></slot></div>
  </div>
</template>

<script>
import { on, off } from '@/libs/util'
export default {
  name: 'HScroll',
  props: {
    gutter: {
      // 卡片间距
      type: Number,
      default: 16
    }
  },
  provide () {
    return { ScrollInstance: this }
  },
  data () {
    return {
      scrollBoxWidth: 0, // 滚动盒子的宽度
      fullChildrenWidth: 0, // 子元素宽度之和
      scrollLeft: 0 // 滚动的距离
    }
  },
  mounted () {
    this.scrollBoxWidth = 0
    this.fullChildrenWidth = 0
    on(window, 'resize', this.updateView)
  },
  activated () {
    this.refresh()
  },
  beforeDestroy () {
    off(window, 'resize', this.updateView)
  },
  methods: {
    refresh () {
      this.$nextTick(() => {
        // 处理隐藏情况下宽度归零的情况
        const scrollBoxEl = this.$refs.scrollBox
        this.scrollBoxWidth = scrollBoxEl.clientWidth
        // console.log('更新宽度', this.scrollBoxWidth)
        this.$refs.scrollBox.scrollLeft = this.scrollLeft
      })
    },
    updateView (width) {
      const scrollBoxEl = this.$refs.scrollBox
      this.scrollBoxWidth = scrollBoxEl.clientWidth
      if (width && typeof width === 'number') {
        this.fullChildrenWidth += width + this.gutter
      }
      // console.log('scrollBox宽度', this.scrollBoxWidth)
    },
    scroll (direction) {
      if (direction === 'left') {
        this.$refs.scrollBox.scrollLeft -= this.scrollBoxWidth
      } else if (direction === 'right') {
        this.$refs.scrollBox.scrollLeft += this.scrollBoxWidth
      }
      this.scrollLeft = this.$refs.scrollBox.scrollLeft
      // console.log('scroll', direction, this.scrollLeft)
    }
  }
}
</script>

<style lang="less" scoped>
.h-scroll {
  overflow-x: hidden;
  overflow-y: visible;
  padding: 0 26px;
  box-sizing: border-box;
  position: relative;
  min-height: 20px;

  // 为了让取消置顶可见
  margin-top: -12px;

  .arrow {
    cursor: pointer;
    position: absolute;
    transform: translate(0, -50%);
    top: 50%;
    font-size: 20px;
    color: rgba(0, 0, 0, 0.8);

    &.left {
      left: 0;
    }

    &.right {
      right: 0;
    }

    &.disabled {
      color: rgba(0, 0, 0, 0.3) !important;
    }
  }

  .scroll-box {
    z-index: 1;
    width: 100%;
    overflow-x: hidden;
    white-space: nowrap;
    // 为了让取消置顶可见
    padding: 24px 0;
  }
}
</style>
