<template>
   <div>
     <Form inline :label-width="70">
       <FormItem label="年级">
         <Select v-model="gradeId" @on-change="setCurrentGradeId">
           <Option v-for="grade in gradeList" :value="grade.gradeId" :key="grade.gradeId">{{grade.gradeName}}</Option>
         </Select>
       </FormItem>
       <FormItem label="班级">
         <Select v-model="classIds" multiple filterable>
           <Option v-for="clazz in classList" :value="clazz.id" :key="clazz.id">{{ clazz.className }}</Option>
         </Select>
       </FormItem>
       <FormItem label="修改时间">
         <DatePicker @on-change="timeChange" type="daterange" separator=" ~ " show-week-numbers placement="bottom-end" placeholder="请选择" transfer style="width: 200px"></DatePicker>
       </FormItem>
       <FormItem>
         <Button @click="search" type="primary">查询</Button>
       </FormItem>
     </Form>
     <template v-if="tableData.length">
       <Timeline style="max-height: 600px;overflow-y: auto;">
         <TimelineItem v-for="log in tableData" :key="log.id">
           <Icon type="ios-radio-button-on" slot="dot" color="#3b7eff"/>
           <div class="record-bar">
             <div class="left">
               <div class="icon" :class="icons[log.operateType]"></div>
               <span style="margin-top: 5px;">{{ log.operateName }}</span>
             </div>
             <div class="right">
               <span style="color: rgba(0, 0, 0, 0.45);font-size: 12px;line-height: 24px;">{{ log.createTime }}</span>
               <span>{{ log.message }}</span>
             </div>
           </div>
         </TimelineItem>
       </Timeline>
       <div style="margin: 10px;" :class="{ 'page-fix': tableData.length < 3 }">
         <div style="text-align: right;">
           <Page
             :total="totalCount"
             :page-size="pageSize"
             :current.sync="pageNo"
             show-elevator
             show-sizer
             show-total
             @on-page-size-change="pageSizeChange"
             @on-change="changePage"></Page>
         </div>
       </div>
     </template>
     <span v-else style="font-size: 14px; color: rgba(0, 0, 0, 0.25);">暂无数据</span>
   </div>
</template>

<script>
import RecordMixin from './record-mixin'
import { queryOperateLog } from '@/api/schoolDic'
export default {
  name: 'RecordLog',
  mixins: [ RecordMixin ],
  props: {
    schoolId: String,
    cityCode: String
  },
  data () {
    return {
      source: 'log',
      tableData: [],
      editTimeRange: [],
      icons: {
        modify: 'edit',
        add: 'add'
      },
      startTime: '',
      endTime: '',
      pageSize: 10,
      pageNo: 1,
      totalCount: 0
    }
  },
  watch: {
    classIds (n, o) {
      if (n.length === 0 && o.length > 0) {
        this.classIds = [...o]
        this.$Message.warning({
          content: '请至少选择一个班级',
          duration: 5
        })
      }
    }
  },
  methods: {
    queryLogs () {
      queryOperateLog({
        cityCode: this.cityCode,
        schoolIds: this.classIds.join(','),
        startTime: this.startTime,
        endTime: this.endTime,
        pageNo: this.pageNo,
        pageSize: this.pageSize
      }).then(data => {
        this.tableData = data.records
        this.totalCount = data.totalCount
      }).catch(err => {
        this.$Message.error({
          content: '查询维护记录失败：' + err.message,
          duration: 5
        })
      })
    },
    search () {
      this.pageNo = 1
      this.queryLogs()
    },
    timeChange (val) {
      this.startTime = val[0] + ' 00:00:00'
      this.endTime = val[1] + ' 00:00:00'
    },
    /**
     * 明细翻页
     * @param index
     */
    changePage (index) {
      this.pageNo = index
      this.queryLogs()
    },
    /**
     * 变更pageSize
     * @param size
     */
    pageSizeChange (size) {
      this.pageNo = 1
      this.pageSize = size
      this.queryLogs()
    }
  }
}
</script>

