  .record-bar {
    display: flex;
    align-items: center;
    height: 76px;
    background: #fafafa;
    border-radius: 4px;
    font-size: 14px;

    .left {
      width: 96px;
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      border-right: 1px solid #ebedf1;
      .icon {
        width: 24px;
        height: 24px;
      }
      .add {
        background: url("../../../assets/images/add-gray.svg");
      }
      .edit {
        background: url("../../../assets/images/edit-gray.svg");
      }
    }

    .icon {
      font-size: 20px;
      color: rgba(0, 0, 0, 0.45);
    }

    .right {
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding-left: 24px;
    }
  }
</style>
