<template>
  <el-select
    size="small"
    v-model="grade"
    placeholder="请选择"
    @change="changTaskType">
    <el-option
      v-for="item in typeList"
      :key="item.typeId"
      :label="item.typeName"
      :value="String(item.typeId)"/>
</el-select></template>
<script>
import { advisorTaskTypes } from '../../api/crm'
export default {
  props: {
    hasAll: {
      type: Boolean,
      default: false
    },
    initValue: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      typeList: [],
      grade: ''
    }
  },
  watch: {
    initValue (val) {
      this.grade = val
    }
  },
  mounted () {
    this.getTypeList()
  },
  methods: {
    changTaskType (val) {
      this.$emit('getTaskType', val)
    },
    async getTypeList () {
      let data = await advisorTaskTypes()
      if (data && data.length > 0 && this.hasAll) {
        data = [{ typeId: '', typeName: '全部' }].concat(data)
      }
      this.typeList = Object.freeze(data) || []
    }
  }
}
</script>

