      :footer-hide="true"
    >
      <TalBackupRecordsModal
        v-if="showBackupRecordModal"
        :studentDetail="stuInfo"
        :isTaskMode="true"
        @close="showBackupRecordModal = false"
      ></TalBackupRecordsModal>
    </Modal>
    <!-- 散诊任务跟进 -->
    <Modal
      v-model="showDiagnosisFllowModal"
