    >
      <Tabs :animated="false" v-if="showDiagnosisFllowModal" v-model="activeTabId" @on-click="handleFollowDiagnosisTabsClick">
        <TabPane :label="'预约记录' + (index + 1)" :name="item.id"
            v-for="(item, index) in followDiagnosisArr"
            :key="index" :index="index">
          <FllowDiagnosisRecord
            :ref="item.id"
            v-if="item.init"
