            :studentDetail="stuDetail"
            @success="handleFollowDiagnosisRecordSuccess(item.id)" @close="showDiagnosisFllowModal=false"></FllowDiagnosisRecord>
        </TabPane>
      </Tabs>
    </Modal>
    <!-- 试听任务跟进 -->
    <Modal
      v-model="showAuditionFollowModal"
      title="试听任务跟进"
      width="760"
      :mask-closable="false"
      :footer-hide="true"
    >
      <Tabs :animated="false" v-if="showAuditionFollowModal" v-model="activeTabId" @on-click="handleFollowAuditionTabsClick">
        <TabPane :label="'预约记录' + (index + 1)" :name="item.id"
          v-for="(item, index) in followAuditionArr"
          :key="index" :index="index">
          <FollowRecord
            :ref="item.id"
            v-if="item.init"
            :appointmentApplyId="item.appointmentApplyId"
            :appointmentId="item.id"
            :operationType="1"
            :studentDetail="stuDetail"
            @success="handleFollowAuditionRecordSuccess(item.id)"
            @close="showAuditionFollowModal=false">
          </FollowRecord>
        </TabPane>
      </Tabs>
    </Modal>
    <!-- 试听申请跟进 -->
      <Modal
        v-model="showAuditionApplyFollowModal"
        title="试听申请任务跟进"
        width="760"
        :mask-closable="false"
        :footer-hide="true"
      >
      <Tabs :animated="false" v-if="showAuditionApplyFollowModal" v-model="activeTabId" @on-click="handleFollowApplyTabsClick">
        <TabPane :label="'试听申请' + (index + 1)" :name="item.id"
          v-for="(item, index) in followApplyArr"
          :key="index" :index="index">
          <AuditionFollowRecord
            :ref="item.id"
            v-if="item.init"
            :appointmentApplyId="item.appointmentApplyId"
            :appointmentId="item.id"
            :operationType="1"
            :studentDetail="stuDetail"
            :appointmentList="item"
            @success="handleFollowAuditionRecordSuccess">
          </AuditionFollowRecord>
        </TabPane>
      </Tabs>
      </Modal>
  </Layout>
</template>
<script>
import radioTaskType from '_c/mytask/radioTaskType'
import radioGrade from '_c/mytask/radioGrade'
import followRecordTable from '_c/mytask/consultTask/followRecordTable'
import bookTaskTable from '_c/mytask/consultTask/bookTaskTable'
import toFollowTaskTable from '_c/mytask/consultTask/toFollowTaskTable'
import GiftTaskTable from '_c/mytask/consultTask/giftTaskTable'
import FllowDiagnosisRecord from '../../operative/scatteredDiagnosisMgt/fllowDiagnosisRecord'
import {
  advisorTaskList,
  advisorPlanList,
  advisorFollowDetailList,
  queryStudentDiagnosis,
  queryGiftActivityList
} from '../../../api/crm'
import localStorage from '@/common/utils/localstorage'
import { getStudentDetail } from '@/api/student'
import { queryDiagnosisRecordIdsByPyUid } from '../../../api/diagnosis'
import { getAppointmentIdByPyUid } from '@/api/appointment'
import { getAppointmentApplyAndRecordRList } from '@/api/appointmentApply'
import FollowRecord from '_c/appointment/followRecord.vue'
import AuditionFollowRecord from '_c/appointment/auditionFollowRecord.vue'
import Cookie from 'js-cookie'

const formatTime = (date, sign = '.') => {
  const year = date.getFullYear()
  const month = date.getMonth() + 1
  const day = date.getDate()
  const hour = date.getHours()
  const minute = date.getMinutes()
  const second = date.getSeconds()

  return (
    [year, month, day].map(formatNumber).join(sign) +
    ' ' +
    [hour, minute, second].map(formatNumber).join(':')
  )
}

const formatNumber = (n) => {
  n = n.toString()
  return n[1] ? n : '0' + n
}

const formatStamp = (stamp, sing) => {
  if (!stamp) return ''
  const date = new Date(Number(stamp * 1000))
  return formatTime(date, sing)
}

const secToTimestr = (time) => {
  var secondTime = parseInt(time)
  var minuteTime = 0
  var hourTime = 0
  if (secondTime > 60) {
    minuteTime = parseInt(secondTime / 60)
    secondTime = parseInt(secondTime % 60)
    if (minuteTime > 60) {
      hourTime = parseInt(minuteTime / 60)
      minuteTime = parseInt(minuteTime % 60)
    }
  }
  var result = '' + parseInt(secondTime) + '秒'
  if (minuteTime > 0) {
    result = '' + parseInt(minuteTime) + '分' + result
  }
  if (hourTime > 0) {
    result = '' + parseInt(hourTime) + '小时' + result
  }
  return result
}
function formatTableData (rawTableData, type) {
  if (Number(type) === 1) {
    for (let item of rawTableData) {
      let allTask = ''
      let taskNum = 0
      let earlistPopTime = 0
      let reactionStartTime = 0
      if (item.taskList) {
        item.taskList.forEach((taskItem, index) => {
          allTask += taskItem.taskName + ','
          taskNum++
          if (earlistPopTime === 0) {
            earlistPopTime = taskItem.createTime
          } else if (earlistPopTime > taskItem.createTime) {
            earlistPopTime = taskItem.createTime
          }
          if (reactionStartTime === 0) {
            reactionStartTime = taskItem.reactionStartTime
          } else if (reactionStartTime > taskItem.reactionStartTime) {
            reactionStartTime = taskItem.reactionStartTime
          }
        })
      }
      item.allTask = allTask.substr(0, allTask.length - 1)
      item.taskNum = taskNum
      item.earlistPopTime = formatStamp(earlistPopTime, '-')
      item.reactionStartTime = formatStamp(reactionStartTime, '-')
    }
  } else if (Number(type) === 2) {
    for (let item of rawTableData) {
      item.planTimeStr = formatStamp(item.planTime, '-')
    }
  } else if (Number(type) === 3) {
    for (let item of rawTableData) {
      let allTask = ''
      if (item.taskList) {
        item.taskList.forEach((taskItem, index) => {
          allTask += taskItem.taskName + ','
        })
      }
      item.allTask = allTask.substr(0, allTask.length - 1)
      item.firstStartTimeStr = formatStamp(item.firstStartTime, '-')
      item.createTimeStr = formatStamp(item.createTime, '-')
      item.avgReactionCostStr = secToTimestr(item.avgReactionCost)
    }
  }
  return rawTableData
}

// 任务状态：1待维护、2挂起、3关闭、4无效
export default {
  name: 'consultstask',
  components: {
    radioTaskType,
    radioGrade,
    toFollowTaskTable,
    followRecordTable,
    bookTaskTable,
    FllowDiagnosisRecord,
    FollowRecord,
    GiftTaskTable,
    AuditionFollowRecord
  },
  data () {
    return {
      input: '',
      activeName: '0',
      tableListZero: [],
      tableListOne: [],
      tableListTwo: [],
      tableListThree: [],
      pageNo: 1,
      select: 'stuMobile',
      pageSize: 40,
      totalArr: {
        0: 0,
        1: 0,
        2: 0,
        3: 0
      },
      params: {
        type: 1,
        taskType: '',
        stuGradeId: '',
        stuLoginName: '',
        stuMobile: '',
        stuRealName: '',
        pageSize: 40,
        pageNo: 1
      },
      searching: false,
      diagnosisModal: false,
      diagnosisColumns: [
        {
          type: 'index',
          width: 30,
          align: 'center'
        },
        {
          title: '城市',
          key: 'cityName',
          align: 'center'
        },
        {
          title: '年级',
          key: 'gradeName',
          align: 'center'
        },
        {
          title: '学科',
          key: 'subjectName',
          align: 'center'
        },
        {
          title: '学期',
          key: 'termName',
          align: 'center'
        },
        {
          title: '学员编号',
          key: 'stuCode',
          align: 'center',
          width: 100
        },
        {
          title: '学员姓名',
          key: 'stuName',
          align: 'center'
        },
        {
          title: '分数',
          key: 'score',
          align: 'center'
        },
        {
          title: '班次级别',
          key: 'levelDegreeName',
          align: 'center'
        },
        {
          title: '班次名称',
          key: 'levelName',
          align: 'center'
        },
        {
          title: '报告URL',
          slot: 'reportUrl',
          align: 'center',
          width: 130
        },
        {
          title: '成绩来源',
          key: 'sourceTypeName',
          align: 'center'
        },
        {
          title: '班次生效时间',
          key: 'effectiveClaLevelTime',
          align: 'center'
        },
        {
          title: '成绩是否有效',
          slot: 'isValid',
          align: 'center'
        },
        {
          title: '成绩是否用于报班',
          slot: 'forRegist',
          align: 'center'
        },
        {
          title: '作答时间',
          key: 'answerDateTime',
          align: 'center',
          width: 130
        }
      ],
      diagnosisData: [],
      stuInfo: {
        studentId: '',
        cityCode: '',
        bindingCode: ''
      },
      showBackupRecordModal: false, // 候补记录
      showNoPaymentModal: false, // 报名未缴费
      showDiagnosisFllowModal: false, // 散诊任务跟进
      showAuditionFollowModal: false, // 试听任务跟进
      showAuditionApplyFollowModal: false, // 试听申请任务跟进
      stuDetail: {},
      followDiagnosisArr: [], // 需要跟进的散诊id
      followAuditionArr: [], // 需要跟进的试听id
      followApplyArr: [], // 需要跟进的试听申请id
      activeTabId: '' // 当前tab激活的name
    }
  },
  mounted () {
    this.search()
  },
  methods: {
    // 试听 + 试听申请 跟进维护成功 刷新当前跟进维护记录
    handleFollowAuditionRecordSuccess (refName) {
      this.$refs[refName][0].refresh()
    },
    // 试听 tabs点击事件
    handleFollowAuditionTabsClick (name) {
      this.followAuditionArr.forEach((item) => {
        if (item.id === name) item.init = true
      })
    },
    // 试听 跟进维护
    async followAuditionHandle (row) {
      this.stuDetail = {
        realName: row.stuRealName,
        gradeName: row.stuGradeName,
        card: row.stuLoginName,
        cityCode: row.areaCode,
        areaCode: row.areaCode,
        uid: row.stuUid
      }
      try {
        // 根据任务id拿试听记录id
        // operationType 1: 试听任务
        let res = await getAppointmentIdByPyUid({
          pyUid: row.stuUid,
          operationType: 1
        })
        res = res.map((item, index) => {
          if (index === 0) {
            this.activeTabId = item.id
            return Object.assign(item, { init: true })
          } else {
            return Object.assign(item, { init: false })
          }
        })
        if (!res || res.length === 0) {
          this.$Message.info({ content: '试听任务已全部处理' })
          return
        }
        this.followAuditionArr = res
        this.showAuditionFollowModal = true
      } catch (error) {
        this.$Message.error({ content: error.message, duration: 5 })
      }
    },
    // 试听申请 tabs点击事件
    handleFollowApplyTabsClick (name) {
      this.followApplyArr.forEach((item) => {
        if (item.id === name) item.init = true
      })
    },
    // 试听申请 跟进维护
    async auditionApplyHandle (row) {
      try {
        let res = await getAppointmentApplyAndRecordRList({
          pyUid: row.stuUid,
          empNo: JSON.parse(Cookie.get('columbusUserInfo')).id
        })
        res = res.appointmentList.map((item, index) => {
          if (index === 0) {
            this.activeTabId = item.id
            return Object.assign(item, { init: true })
          } else {
            return Object.assign(item, { init: false })
          }
        })
        if (!res || res.length === 0) {
          this.$Message.info({ content: '试听任务已全部处理' })
          return
        }
        this.followApplyArr = res
        this.showAuditionApplyFollowModal = true
      } catch (error) {
        this.$Message.error({ content: error.message, duration: 5 })
      }
    },
    // 散诊 跟进维护成功 刷新当前跟进维护记录
    handleFollowDiagnosisRecordSuccess (refName) {
      this.$refs[refName][0].refresh()
    },
    // 散诊 tabs点击事件
    handleFollowDiagnosisTabsClick (name) {
      this.followDiagnosisArr.forEach((item) => {
        if (item.id === name) item.init = true
      })
    },
    // 散诊 跟进维护
    async followDiagnosisHandle (row) {
      this.stuDetail = {
        realName: row.stuRealName,
        gradeName: row.stuGradeName,
        card: row.stuLoginName,
        cityCode: row.areaCode,
        areaCode: row.areaCode,
        uid: row.stuUid
      }
      try {
        // 根据任务id拿散诊记录id
        const res = await queryDiagnosisRecordIdsByPyUid({ pyUid: row.stuUid })
        res.forEach((item, index) => {
          if (index === 0) {
            this.activeTabId = item.id
            item.init = true
          } else item.init = false
        })
        this.followDiagnosisArr = res
        this.showDiagnosisFllowModal = true
      } catch (error) {
        this.$Message.error({ content: error.message, duration: 5 })
      }
    },
    // 报班信息
    classInfoHandle (stu) {
      const params = {
        cityCode: stu.areaCode,
        uid: stu.stuUid
      }
      getStudentDetail(params)
        .then((res) => {
          this.stuInfo = {
            cityCode: stu.cityCode,
            studentId: res.studentId,
            bindingCode: res.bindingCode
          }
          this.showNoPaymentModal = true
        })
        .catch((err) => {
          this.$Message.error({
            content: '获取学员信息失败: ' + err.message,
            duration: 5
          })
        })
    },
    // 候补记录
    backupRecordHandle (stu) {
      const params = {
        cityCode: stu.areaCode,
        uid: stu.stuUid
      }
      getStudentDetail(params)
        .then((res) => {
          this.stuInfo = {
            cityCode: stu.cityCode,
            studentId: res.studentId,
            bindingCode: res.bindingCode
          }
          this.showBackupRecordModal = true
        })
        .catch((err) => {
          this.$Message.error({
            content: '获取学员信息失败: ' + err.message,
            duration: 5
          })
        })
    },
    search () {
      this.searching = true
      this.params['stuMobile'] = ''
      this.params['stuRealName'] = ''
      this.params['stuLoginName'] = ''
      this.params[this.select] = this.input
      if (this.activeName === '0') {
        this.queryGiftActivityListHandler(this.params)
      } else if (this.activeName === '1') {
        this.params.type = 1
        this.advisorTaskListHandler(this.params)
      } else if (this.activeName === '2') {
        this.params.type = 2
        this.advisorPlanListHandler(this.params)
      } else if (this.activeName === '3') {
        this.params.type = 3
        this.advisorFollowDetailListHandler(this.params)
      }
    },
    async queryGiftActivityListHandler (req) {
      try {
        const userInfo = this.$store.state.roleRelativeData.userInfo
        req.stuName = req.stuRealName
        req.areaCode = userInfo.cityCode
        req.deptCode = userInfo.baseDataId
        const data = await queryGiftActivityList(req)
        this.tableListZero = Object.freeze(data.records) || []
        this.totalArr['0'] = data.totalCount
      } catch (err) {
        this.$Message.error({
          content: '获取新店礼品任务失败：' + err.message
        })
      } finally {
        this.searching = false
      }
    },
    async advisorTaskListHandler (req) {
      try {
        const data = await advisorTaskList(req)
        this.tableListOne =
          Object.freeze(formatTableData(data.list, this.params.type)) || []
        this.totalArr['1'] = data.total
        let timestamp = parseInt(Date.now() / 1000)
        let lastVisitedTime = {
          mytaskNewstudent: timestamp,
          mytaskConsultstask: timestamp
        }
        localStorage.set('lastVisitedTime', lastVisitedTime)
      } catch (err) {
        this.$Message.error({
          content: '获取待维护列表失败：' + err.message
        })
      } finally {
        this.searching = false
      }
    },
    async advisorPlanListHandler (req) {
      try {
        const data = await advisorPlanList(req)
        this.tableListTwo =
          Object.freeze(formatTableData(data.list, this.params.type)) || []
        this.totalArr['2'] = data.total
      } catch (err) {
        this.$Message.error({
          content: '获取预约任务列表失败：' + err.message
        })
      } finally {
        this.searching = false
      }
    },
    async advisorFollowDetailListHandler (req) {
      try {
        const data = await advisorFollowDetailList(req)
        this.tableListThree =
          Object.freeze(formatTableData(data.list, this.params.type)) || []
        this.totalArr['3'] = data.total
      } catch (err) {
        this.$Message.error({
          content: '获取维护记录列表失败：' + err.message
        })
      } finally {
        this.searching = false
      }
    },
    getTaskType (val) {
      this.params.taskType = val
    },
    getGrade (val) {
      this.params.stuGradeId = val
    },
    selectTab (type) {
      this.pageNo = 1
      this.pageSize = 40
      for (let i in this.totalArr) {
        this.totalArr[i] = 0
      }
      this.tableListOne = []
      this.tableListTwo = []
      this.tableListThree = []
      this.params.pageNo = 1
      this.params.pageSize = 40
      this.params.taskType = ''
      this.params.stuGradeId = ''
      this.params.type = parseInt(type)
      this.input = ''
      this.select = 'stuMobile'
      this.search()
    },
    handleSizeChange (val) {
      this.params.pageSize = val
      this.params.pageNo = 1
      this.pageSize = val
      this.pageNo = 1
      this.search()
    },
    handleCurrentChange (val) {
      this.params.pageNo = val
      this.pageNo = val
      this.search()
    },
    diagnosisHandle (list) {
      try {
        let taskList = []
        list.forEach((obj) => {
          taskList.push(obj.taskId)
        })
        this.diagnosisData = []
        this.$nextTick(async () => {
          const rlt = await queryStudentDiagnosis(taskList)
          this.diagnosisData = Object.freeze(rlt)
          this.diagnosisModal = true
        })
      } catch (err) {
        this.$Message.error({
          content: '获取诊断报告失败：' + err.message
        })
      } finally {
      }
    }
  }
}
</script>

