<template>
    <div class="content_box">
      <Tabs class="tabs-list" :animated="false" name="resource" v-model="activeTabName" @on-click="handleTabClick">
        <TabPane label="学员活动轨迹" name="activeLine">
          <Table border :height="activeLineData&&activeLineData.length>0?'290':'100'"   :columns="activeLineColumns" :data="activeLineData">
            <template slot="actionLabel" slot-scope="{row}">
              <div>
                <Tag class="tagStyle"  :color="activeLineColorMap[row.actionLabel]" > {{row.actionLabel}}</Tag>
              </div>
            </template>
          </Table>
        </TabPane>
        <TabPane label="顾问沟通记录" name="talkRecords">
           <Table border  :height="talkRecordsData&&talkRecordsData.length>0?'290':'100'"  :columns="talkRecordsColumns" :data="talkRecordsData">
             <template slot="labelStatus" slot-scope="{row}">
              <div>
                <Tag v-if="row.isEffectiveCommunication==1" class="tagStyle"  color="green" >有效沟通</Tag>
                <Tag v-if="row.followTypeName" class="tagStyle" color="blue" >{{row.followTypeName}}</Tag>
                <Tag class="tagStyle"  color="red" > {{'客户分级'+row.customerLevel}}</Tag>
              </div>
            </template>
            <template slot="suggRecords" slot-scope="{row}">
              <div>
                <span>{{`意向教师:${row.intentedTeacher?row.intentedTeacher:''};意向学科:${row.intendedSubject?row.intendedSubject:''};意向班级:${row.intendedClassType?row.intendedClassType:''}`}}</span>
              </div>
            </template>
           </Table>
        </TabPane>

      </Tabs>

    </div>
</template>

<script>
import {
  queryStudentActions,
  getAdvisorFollowRecord
} from '@/api/crm'
import { XesAnalyticsTrack } from '@/libs/tal-sensors'
import { mapState } from 'vuex'
export default {
  props: ['row', 'tabIndex'],
  computed: {
    ...mapState('roleRelativeData', ['userInfo'])
  },
  data () {
    return {
      activeTabName: 'activeLine',
      activeLineColumns: [
        {
          title: '时间',
          key: 'actionDate',
          width: 210
        },
        {
          title: '活动轨迹',
          key: 'action',
          width: 160
        },
        {
          title: '标签状态',
          key: 'actionLabel',
          slot: 'actionLabel',
          width: 150
        },
        {
          title: '活跃详情',
          key: 'actionDetail'
        }
      ],
      activeLineData: [],
      talkRecordsColumns: [
        {
          title: '时间',
          key: 'createTime',
          width: 210
        },
        {
          title: '沟通主题',
          key: 'taskNames',
          width: 160
        },
        {
          title: '标签状态',
          key: 'labelStatus',
          slot: 'labelStatus',
          width: 300
        },
        {
          title: '意向记录',
          key: 'suggRecords',
          slot: 'suggRecords'
        },
        {
          title: '沟通内容',
          key: 'content'
        }
      ],
      talkRecordsData: [],
      activeLineColorMap: {
        '活跃行为': 'green',
        '预警行为': 'red'
      },
      talkRecordsColorMap: {
        0: 'green',
        1: 'red'
      },
      talkRecordsTextMap: {
        0: '有效沟通',
        1: '客户分级A'
      },
      errText: {
        'activeLine': '学员活动轨迹',
        'talkRecords': '顾问沟通记录'
      },
      activeLine: [],
      talkRecords: []
    }
  },
  created () {

  },
  mounted () {
    this.queryActionTableData()
  },
  methods: {
    handleTabClick (val) {
      if (val === 'activeLine') {
        this.queryActionTableData()
      } else {
        this.queryTalkTableData()
      }
    },
    queryActionTableData () {
      let { cityCode } = this.userInfo
      let { uid } = this.row
      let params = {
        uid,
        areaCode: cityCode,
        cityCode
      }
      queryStudentActions(params).then(res => {
        this.activeLineData = res
      }).catch(err => {
        this.$Message.error({
          content: `获取${this.errText[this.activeTabName]}:${err.message}`
        })
      })

      let stuLevel = ''
      switch (this.tabIndex) {
        case 1: stuLevel = '纯新学员'; break
        case 2: stuLevel = '仅体验学员'; break
        case 3: stuLevel = '历史在读学员'; break
        case 4: stuLevel = '首缴在读学员'; break
        case 5: stuLevel = '多期在读学员'; break
      }
      // 埋点 - 手风琴弹窗tab
      XesAnalyticsTrack('click_6AGxVIVp', { tab_name: stuLevel, subtab_name: '行为轨迹' })
    },
    queryTalkTableData () {
      let dateArr = this.getDate()
      let { cityCode } = this.userInfo
      let { uid } = this.row
      let params = {
        pyUid: uid,
        cityCode,
        startTime: dateArr[0],
        endTime: dateArr[1]
      }
      getAdvisorFollowRecord(params).then(res => {
        this.talkRecordsData = res
      }).catch(err => {
        this.$Message.error({
          content: `获取${this.errText[this.activeTabName]}:${err.message}`
        })
      })

      let stuLevel = ''
      switch (this.tabIndex) {
        case 1: stuLevel = '纯新学员'; break
        case 2: stuLevel = '仅体验学员'; break
        case 3: stuLevel = '历史在读学员'; break
        case 4: stuLevel = '首缴在读学员'; break
        case 5: stuLevel = '多期在读学员'; break
      }
      // 埋点 - 手风琴弹窗tab
      XesAnalyticsTrack('click_6AGxVIVp', { tab_name: stuLevel, subtab_name: '沟通记录' })
    },
    getDate () {
      var myDate = new Date()
      var date1 = new Date(myDate.getTime())
      let now = date1.getFullYear() + '-' + ((date1.getMonth() + 1) < 10 ? '0' + (date1.getMonth() + 1) : (date1.getMonth() + 1)) + '-' + (date1.getDate() < 10 ? '0' + date1.getDate() : date1.getDate()) + ' ' + (date1.getHours() < 10 ? '0' + date1.getHours() : date1.getHours()) + ':' + (date1.getMinutes() < 10 ? '0' + date1.getMinutes() : date1.getMinutes()) + ':' + (date1.getSeconds() < 10 ? '0' + date1.getSeconds() : date1.getSeconds())
      var date2 = new Date(myDate.getTime() - (7 * 24 * 60 * 60 * 1000))
      let before_7 = date2.getFullYear() + '-' + ((date2.getMonth() + 1) < 10 ? '0' + (date2.getMonth() + 1) : (date2.getMonth() + 1)) + '-' + (date2.getDate() < 10 ? '0' + date2.getDate() : date2.getDate()) + ' ' + (date2.getHours() < 10 ? '0' + date2.getHours() : date2.getHours()) + ':' + (date2.getMinutes() < 10 ? '0' + date2.getMinutes() : date2.getMinutes()) + ':' + (date2.getSeconds() < 10 ? '0' + date2.getSeconds() : date2.getSeconds())
      return [before_7, now]
    }
  }
}
</script>

