<template>
  <div>
    <Row type="flex">
      <Col span="8"><span>学员姓名: {{ appointmentList.stuName || '--' }}</span></Col>
      <Col span="8"><span>年级: {{ appointmentList.gradeName || '--' }}</span></Col>
      <Col span="8"><span>学员编号: {{ appointmentList.stuCode || '--' }}</span></Col>
    </Row>
    <br>
    <Row type="flex">
      <Col span="8"><span>申请时间: {{ appointmentList.createTime || '--' }}</span></Col>
      <Col span="8"><span>推荐顾问: {{ appointmentList.maintainerName || '--' }}</span></Col>
      <Col span="8"><span>推荐学员: {{ appointmentList.referrerStuName || '--' }}</span></Col>
    </Row>
    <br>
    <Row type="flex">
      <Col span="8"><span>意向服务中心: {{ appointmentList.intendedServiceCenter || '--' }}</span></Col>
      <Col span="8"><span>年份: {{ appointmentList.year || '--' }}</span></Col>
      <Col span="8"><span>年级: {{ appointmentList.gradeName || '--' }}</span></Col>
    </Row>
    <br>
    <Row type="flex">
      <Col span="8"><span>学科: {{ appointmentList.subject || '--' }}</span></Col>
      <Col span="8"><span>授课类型: {{ appointmentList.classType || '--' }}</span></Col>
      <Col span="8"><span>期望试听时间: {{ appointmentList.subTime || '--' }}</span></Col>
    </Row>
    <br>
    <!-- 修改申请状态 -->
    <ChangeApplyStatus v-if="changeApplyStatusModal.show"
      :applyChangedRecord="appointmentList.recordList"
      :applyInfo="appointmentList"
      @success="$emit('success')"
    />
  </div>
</template>

<script>
import { queryFollowDetail } from '@/api/appointment'
import TaskMaintainMixin from '@/view/resource/advisor-workbench/task-maintain-mixin'
import ChangeApplyStatus from './change-apply-status'
export default {
  name: 'AuditionFollowRecord', // 跟进记录
  mixins: [TaskMaintainMixin],
  components: {
    ChangeApplyStatus
  },
  props: {
    // cityCode / areaId / uid / realName / stuCard
    studentDetail: {
      type: Object,
      default: () => {
        return {}
      }
    },
    operationType: {
      // 跟进类型 {1: 试听跟进}
      type: Number,
      default: null
    },
    appointmentList: {
      type: Object,
      default: () => {
        return {}
      }
    },
    applyId: {
      type: String,
      default: ''
    },
    preApplyStatus: {
      type: Number
    }
  },
  data () {
    return {
      followList: [], // 跟进记录
      taskResp: [], // 待维护任务
      changeApplyStatusModal: {
        show: true
      },
      attendStatus: {
        // 是否考勤：1 准时上课， 2迟到， 0缺勤
        0: '缺勤',
        1: '准时上课',
        2: '迟到'
      },
      classTypeMap: {
        1: '在线',
        2: '双师',
        4: '面授'
      },
      appointmentApply: null, // 试听申请记录
      loaded: false,
      applyStatusMap: { // 一级跟进状态
        0: '未跟进',
        1: '已预约',
        2: '跟进结束',
        3: '跟进中'
      },
      applySecondStatusMap: { // 二级跟进状态
        1: '继续跟进',
        2: '未接通',
        3: '无意向',
        4: '已用其他账号预约',
        5: '已转化无需预约',
        6: '无匹配课程'
      }
    }
  },
  methods: {
    refresh () {
      this.queryFollowDetail()
    },
    queryFollowDetail () {
      const params = {
        appointmentId: this.appointmentId,
        cityCode: this.studentDetail.cityCode,
        operationType: this.operationType,
        appointmentApplyId: this.appointmentApplyId
      }
      queryFollowDetail(params)
        .then((res) => {
          this.followList = Object.freeze(res.followList) || []
          this.appointmentResp = Object.freeze(res.appointmentResp) || {}
          this.taskResp = Object.freeze(res.taskResp) || []
          this.appointmentApply = Object.freeze(res.appointmentApply) || null
          this.loaded = true
        })
        .catch((err) => {
          this.loaded = true
          this.$Message.error({ content: err.message, duration: 5 })
        })
    }
  }
}
</script>

