<template>
  <div class="wrapper">
    <Form inline :label-width="40">
      <FormItem label="学校">
        <Select v-model="schoolId" @on-change="schoolChange">
          <Option v-for="school in schoolList" :key="school.schoolId" :value="school.schoolId">
            {{ school.schoolName }}
          </Option>
        </Select>
      </FormItem>
    </Form>
    <Tabs value="name1" @on-click="tabChange">
      <TabPane label="信息录入" name="name1">
        <RecordEdit ref="tab1" :city-code="cityCode" :school-id="schoolId"></RecordEdit>
      </TabPane>
      <TabPane label="维护记录" name="name2">
        <RecordLog ref="tab2" :city-code="cityCode" :school-id="schoolId"></RecordLog>
      </TabPane>
    </Tabs>
  </div>
</template>

<script>
import RecordEdit from './record-edit'
import RecordLog from './record-log'
export default {
  name: 'record',
  components: {
    RecordEdit,
    RecordLog
  },
  props: {
    schoolList: {
      type: Array,
      default: () => {
        return []
      }
    },
    cityCode: String
  },
  data () {
    return {
      schoolId: ''
    }
  },
  methods: {
    init () {
      this.$nextTick(() => {
        this.$refs.tab1.queryGradeClassNum()
        this.$refs.tab2.queryGradeClassNum()
      })
    },
    schoolChange (val) {
      this.schoolId = val
      this.init()
    },
    tabChange (val) {
      if (val === 'name2') {
        this.$refs.tab2.queryClassList()
      }
    }
  },
  mounted () {
    if (this.schoolList.length) {
      this.schoolId = this.schoolList[0].schoolId
      this.init()
    }
  }
}
</script>

<style scoped lang="less">
  .wrapper {
    height:100%;
  }

</style>
