<template>
  <Layout id="grade-table">
    <Row>
      <Table
        :show-header="false"
        :row-class-name="rowClassName"
        :columns="columns"
        :data="tableList"
        no-data-text="暂无数据"
        disabled-hover
        @on-expand="expendChange"
      ></Table>
    </Row>
  </Layout>
</template>
<script>
import classTable from './class-table.vue'
import { mapActions, mapState } from 'vuex'
export default {
  name: 'gradeTable',
  components: { classTable },
  props: ['schoolId'],
  data () {
    return {
      columns: [
        {
          type: 'expand',
          width: 50,
          render: (h, { row }) => {
            return h(classTable, {
              props: {
                schoolId: row.schoolId,
                gradeId: row.gradeId,
                grageName: row.gradeName
              }
            })
          }
        },
        {
          title: '学校',
          key: 'gradeName',
          width: 150,
          render: (h, { row }) => {
            return h('span', row.gradeName ? row.gradeName : '--')
          }
        },
        {
          title: '人数',
          key: 'totalPeople',
          align: 'right',
          width: 150,
          render: (h, { row }) => {
            return h('span', row.totalPeople ? row.totalPeople : '--')
          }
        },
        {
          title: '线索渗透',
          key: 'address',
          align: 'center',
          children: [
            {
              title: '人数',
              key: 'music',
              align: 'right',
              render: (h, { row }) => {
                return h('span', '--')
              }
            },
            {
              title: '渗透率',
              key: 'job',
              align: 'center',
              render: (h, { row }) => {
                return h('span', '--')
              }
            }
          ]
        },
        {
          title: '注册渗透',
          key: 'registerTotal',
          align: 'center',
          children: [
            {
              title: '人数',
              key: 'registerTotalPeople',
              align: 'right',
              render: (h, { row }) => {
                return h('span', row.registerTotalPeople ? row.registerTotalPeople : '--')
              }
            },
            {
              title: '渗透率',
              key: 'registerPermRate',
              align: 'center',
              render: (h, { row }) => {
                return h('span', row.registerPermRate ? row.registerPermRate : '--')
              }
            }
          ]
        },
        {
          title: '在读渗透',
          key: 'atSchoolTotal',
          align: 'center',
          children: [
            {
              title: '人数',
              key: 'atSchoolTotalPeople',
              align: 'right',
              render: (h, { row }) => {
                return h('span', row.atSchoolTotalPeople ? row.atSchoolTotalPeople : '--')
              }
            },
            {
              title: '渗透率',
              key: 'atSchoolPermRate',
              align: 'center',
              render: (h, { row }) => {
                return h('span', row.atSchoolPermRate ? row.atSchoolPermRate : '--')
              }
            }
          ]
        },
        {
          title: '班级KOL渗透',
          key: 'total',
          align: 'center',
          children: [
            {
              title: '班级数',
              key: 'totalClass',
              align: 'right',
              render: (h, { row }) => {
                return h('span', row.totalClass ? row.totalClass : '--')
              }
            },
            {
              title: 'KOL用户',
              key: 'totalKolPeople',
              align: 'center',
              render: (h, { row }) => {
                return h('span', row.totalKolPeople ? row.totalKolPeople : '--')
              }
            },
            {
              title: '渗透率',
              key: 'kolPermRate',
              align: 'center',
              render: (h, { row }) => {
                return h('span', row.kolPermRate ? row.kolPermRate : '--')
              }
            }
          ]
        }
      ],
      tableList: []
    }
  },
  computed: {
    ...mapState('resource', ['gradeData', 'gradeDataLoading', 'gradeDataSearch', 'classData']),
    ...mapState('roleRelativeData', ['userInfo'])
  },
  methods: {
    ...mapActions('resource', ['queryClassData']),
    expendChange (expandedRows, isOpen) {
      if (isOpen) {
        // let storeSchoolIdsAndGradeIds = Object.keys(this.classData)
        // if (storeSchoolIdsAndGradeIds.indexOf(expandedRows.schoolId + expandedRows.gradeId) === -1) {
        const params = {
          schoolId: expandedRows.schoolId,
          gradeId: expandedRows.gradeId,
          cityCode: this.userInfo.cityCode
        }
        this.queryClassData(params)
        // }
      }
      // 无论打开还是关闭都清除编辑状态
      let key = expandedRows.schoolId + expandedRows.gradeId
      this.classData[key] && (this.classData[key] = this.classData[key].map(item => {
        item.isEdit && delete item.isEdit
        return item
      }))
    },
    rowClassName () {
      return 'backcolor_deep'
    }
  },
  watch: {
    gradeDataLoading: {
      handler () {
        let tableList = this.gradeData[this.schoolId]
        this.tableList = tableList && tableList.length > 0 ? tableList : []
      },
      immediate: true
    }
  }
}
</script>

<style lang="less">
#grade-table {
  .ivu-table-cell-expand {
    margin-left: 20px;
  }
}
</style>
