<template>
  <div>
    <section class="trees">
      <section class="tree-left">
        <h3>{{ sourceLabel }}</h3>
        <section class="tree" >
          <div class="input-wrapper">
            <Input suffix="ios-search" @on-change="textChange"></Input>
          </div>
          <ul class="level-1">
            <li v-for="(level1, level1idx) in treeData" :key="level1.key">
              <span class="hover pointer item caret" :class="{ expanded: level1.expanded }" @click="nodeClickHandle(level1, 'level1')">{{level1.title}}</span>
              <ul class="level-2">
                <li class="hover-bg" v-for="level2 in level1.children" :key="level2.key">
                  <span class="hover pointer checkbox item" :class="{ sel: level2.checked }" @click="nodeClickHandle(level2, 'level2', level1idx)">{{level2.title}}</span>
                </li>
              </ul>
            </li>
          </ul>
        </section>
      </section>
      <section class="tree-arrow">
      </section>
      <section class="tree-right">
        <h3>{{ targetLabel }}（{{selectedTree.length}}/ {{ maxLength }}）</h3>
        <section class="tree">
          <ul>
            <li class="hover pointer hover-bg" v-for="item in selectedTree" :class="{ 'disable': item.disableCancel}" :key="item.key">
              <div>{{item.title}}</div>
              <div v-if="!item.disableCancel" class="icon-close" @click="removeSelTreeHandle(item)"></div>
            </li>
          </ul>
        </section>
      </section>
    </section>
    <div style="margin-top: 16px;text-align: right">
      <Button @click="$emit('cancel')">取  消</Button>
      <Button type="primary" @click="confirm" :loading="loading">确  定</Button>
    </div>
  </div>
</template>

<script>
import { deepCopy } from 'view-design/src/utils/assist'
export default {
  name: 'TreeTransfer',
  props: {
    sourceLabel: {
      type: String,
      default: 'Source'
    },
    targetLabel: {
      type: String,
      default: 'Target'
    },
    maxLength: {
      type: Number,
      default: 10
    },
    groupList: {
      type: Array,
      default: () => {
        return []
      }
    },
    defaultSelected: {
      type: Array,
      default: () => {
        return []
      }
    },
    loading: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      selectedTree: [],
      treeData: []
    }
  },
  methods: {
    async textChange ({ target }) {
      await this.init()
      if (target.value) {
        // 过滤
        this.treeData = this.treeData.filter(t => (t.title.indexOf(target.value) !== -1 || t.children.find(c => c.title.indexOf(target.value) !== -1))).map(obj => {
          obj.children = obj.children.filter(c => c.title.indexOf(target.value) !== -1)
          obj.expanded = true
          return obj
        })
      }
    },
    // 点击节点1级展开 2级选中或取消选中，并为2级增加 索引 方便回显和取消
    async nodeClickHandle (data, type, level1Idx) {
      if (type === 'level1') {
        const d = this.treeData.map(obj => {
          if (obj.key === data.key) {
            obj.expanded = !obj.expanded
          }
          return obj
        })
        this.treeData = d
      } else if (type === 'level2') {
        if (data.checked) {
          return
        }
        this.treeData[level1Idx].children = this.level2BuildHandle(data, level1Idx)
        this.emitSelTreeHandle()
      }
    },
    level2BuildHandle (data, level1Idx) {
      return this.treeData[level1Idx].children.map(obj => {
        if (obj.key === data.key) {
          const p = {
            ...data,
            checked: !data.checked,
            parentKey: this.treeData[level1Idx].key
          }
          if (this.selectedTree.length < this.maxLength) {
            if (!data.checked) {
              this.selectedTree.push(p)
            } else {
              this.selectedTree = this.selectedTree.filter(o => o.key !== data.key)
            }
            return p
          }
          if (this.selectedTree.length >= this.maxLength) {
            let pp = this.selectedTree.filter(o => o.key === data.key)
            if (pp.length > 0) {
              this.selectedTree = this.selectedTree.filter(o => o.key !== data.key)
              return p
            } else {
              this.$Message.warning(`最多添加${this.maxLength}条`)
            }
          }
          return data
        }
        return obj
      })
    },
    // 通过2级数据中的索引 来达到取消的目的
    removeSelTreeHandle (item) {
      if (item.disableCancel) {
        return this.$Message.warning('不允许取消')
      }
      const lv1Idx = this.treeData.findIndex(lv1 => lv1.key === item.parentKey)
      const lv2Idx = this.treeData[lv1Idx].children.findIndex(lv2 => lv2.key === item.key)
      this.treeData[lv1Idx].children[lv2Idx] = {
        ...this.treeData[lv1Idx].children[lv2Idx],
        checked: false
      }
      this.selectedTree = this.selectedTree.filter(o => o.key !== item.key)
      this.emitSelTreeHandle()
    },
    emitSelTreeHandle () {
      this.$emit('links', this.selectedTree)
    },
    async init () {
      return new Promise(resolve => {
        this.$nextTick(() => {
          this.treeData = deepCopy(this.groupList)
          this.treeData[0].expanded = true
          if (!this.selectedTree.length) {
            this.selectedTree = deepCopy(this.defaultSelected)
          }
          this.selectedTree.forEach(item => {
            const lv1Idx = this.treeData.findIndex(lv1 => lv1.key === item.parentKey)
            const lv2Idx = this.treeData[lv1Idx].children.findIndex(lv2 => lv2.key === item.key)
            this.treeData[lv1Idx].children[lv2Idx] = {
              ...this.treeData[lv1Idx].children[lv2Idx],
              checked: true
            }
          })
          resolve()
        })
      })
    },
    confirm () {
      this.$emit('update:loading', true)
      this.$emit('ok', this.selectedTree)
    }
  },
  mounted () {
    this.init()
  }
}
</script>

