<template>
  <Layout id="class-table">
    <Row>
      <Table
        :row-class-name="rowClassName"
        disabled-hover
        :columns="columns"
        :data="tableList"
        no-data-text="暂无数据"
      >
      </Table>
    </Row>
  </Layout>
</template>
<script>
import Vue from 'vue'
import { editSchoolClass } from '@/api/schoolDic'
import { mapActions, mapState } from 'vuex'
export default {
  name: 'classTable',
  props: ['schoolId', 'gradeId', 'grageName'],
  data () {
    return {
      columns: [
        {
          type: 'expand',
          width: 50
        },
        {
          title: '学校',
          key: 'schoolClassName',
          tooltip: true,
          width: 150,
          render: (h, { row }) => {
            return h('span', row.schoolClassName ? row.schoolClassName : '--')
          }
        },
        {
          title: '人数',
          key: 'totalPeople',
          width: 150,
          align: 'right',
          render: (h, { row, columns, index }) => {
            if (row.isEdit) {
              return h('div', [h('InputNumber', {
                style: {
                  width: '70px'
                },
                props: {
                  controlsPosition: 'right',
                  size: 'small',
                  value: Number(row.totalPeople),
                  activeChange: false
                },
                on: {
                  input: (val) => {
                    // this.tableList[index].totalPeople = val
                    Vue.set(this.tableList, index, { ...row, totalPeople: val })
                  }
                }
              }), h('span', {
                class: 'button_round button_save',
                domProps: {
                  innerHTML: "<i class='icon el-icon-close point'></i>"
                },
                on: {
                  click: () => {
                    this.onClose(row, index)
                  }
                }
              }), h('span', {
                class: 'button_round button_close',
                domProps: {
                  innerHTML: "<i class='el-icon-check point' ></i>"
                },
                on: {
                  click: () => {
                    this.onSave(row, index)
                  }
                }
              })
              ])
            } else {
              return h('div', [h('span', {
                style: {
                  height: '15px',
                  lineHeight: '15px',
                  display: 'inline-block'
                }
              }, row.totalPeople), h('span', {
                style: {
                  fontSize: '12px',
                  display: 'inline-block',
                  marginLeft: '10px',
                  width: '15px',
                  height: '21px',
                  cursor: 'pointer',
                  verticalAlign: 'text-bottom',
                  background: 'url(' + require('../../../assets/images/archives/edit-black.svg') + ') no-repeat top center'

                },
                on: {
                  click: () => {
                    this.isEdit(row, index, true)
                  }
                }
              })]
              )
            }
          }
        },
        {
          title: '线索渗透',
          key: 'address',
          align: 'center',
          children: [
            {
              title: '人数',
              key: 'music',
              align: 'right',
              render: (h, { row }) => {
                return h('span', '--')
              }
            },
            {
              title: '渗透率',
              key: 'job',
              align: 'center',
              render: (h, { row }) => {
                return h('span', '--')
              }
            }
          ]
        },
        {
          title: '注册渗透',
          key: 'registerTotal',
          align: 'center',
          children: [
            {
              title: '人数',
              key: 'registerTotalPeople',
              align: 'right',
              render: (h, { row, columns, index }) => {
                return h('router-link',
                  {
                    style: {
                      color: '#276BFC',
                      cursor: 'pointer'
                    },
                    props: {
                      to: {
                        path: '/resource/classRoster',
                        query: {
                          atSchoolTotalPeople: row.atSchoolTotalPeople,
                          totalPeople: row.totalPeople,
                          registerTotalPeople: row.registerTotalPeople,
                          schoolId: row.schoolId,
                          gradeId: row.gradeId,
                          classOrder: row.classOrder,
                          areaCode: row.schoolCode,
                          schoolName: row.schoolName,
                          schoolClassName: row.schoolClassName,
                          grageName: this.grageName
                        }
                      }
                    }
                  },
                  row.registerTotalPeople ? row.registerTotalPeople : '--'
                )
              }
            },
            {
              title: '渗透率',
              key: 'registerPermRate',
              align: 'center',
              render: (h, { row }) => {
                return h('span', row.registerPermRate ? row.registerPermRate : '--')
              }
            }
          ]
        },
        {
          title: '在读渗透',
          key: 'atSchoolTotal',
          align: 'center',
          children: [
            {
              title: '人数',
              key: 'atSchoolTotalPeople',
              align: 'right',
              render: (h, { row }) => {
                return h('span', row.atSchoolTotalPeople ? row.atSchoolTotalPeople : '--')
              }
            },
            {
              title: '渗透率',
              key: 'atSchoolPermRate',
              align: 'center',
              render: (h, { row }) => {
                return h('span', row.atSchoolPermRate ? row.atSchoolPermRate : '--')
              }
            }
          ]
        },
        {
          title: '班级KOL渗透',
          key: 'total',
          align: 'center',
          children: [
            {
              title: '班级数',
              key: 'totalClass',
              align: 'right',
              render: (h, { row }) => {
                return h('span', row.totalClass ? row.totalClass : '--')
              }
            },
            {
              title: 'KOL用户',
              key: 'totalKolPeople',
              align: 'center',
              render: (h, { row }) => {
                return h('span', row.totalKolPeople ? row.totalKolPeople : '--')
              }
            },
            {
              title: '渗透率',
              key: 'kolPermRate',
              align: 'center',
              render: (h, { row }) => {
                return h('span', row.kolPermRate ? row.kolPermRate : '--')
              }
            }
          ]
        }
      ],
      tableList: []
    }
  },
  computed: {
    ...mapState('resource', ['classData', 'classDataLoading', 'classDataSearch']),
    ...mapState('roleRelativeData', ['userInfo'])
  },
  methods: {
    ...mapActions('resource', ['queryClassData']),
    rowClassName ({ row, index }) {
      return 'backcolor_deeper'
    },
    isEdit (row, index, status) {
      Vue.set(this.tableList, index, { ...row, isEdit: status })
    },
    onSave (row, index) {
      if (row.hasOwnProperty('totalPeople') && (row.totalPeople === null || row.totalPeople === undefined)) {
        this.$Message.error('保存失败，人数不能为空')
        return
      }
      if (row.totalPeople < 0) {
        this.$Message.error('保存失败，人数不能小于0人')
        return
      }
      if (row.totalPeople > 200) {
        this.$Message.error('保存失败，超出最多限额人数200人')
        return
      }
      let params = {
        classOrder: row.classOrder,
        className: row.schoolClassName,
        studentCount: row.totalPeople,
        id: row.schoolClassId,
        cityCode: this.userInfo.cityCode
      }
      editSchoolClass(params).then(res => {
        if (res) {
          this.$Message.success('保存成功')
          this.isEdit(row, index, false)
          this.queryClassData(this.classDataSearch)
        }
      }).catch(err => {
        this.$Message.error('保存失败' + err.message)
        this.queryClassData(this.classDataSearch)
      })
    },
    onClose (row, index) {
      this.isEdit(row, index, false)
      this.queryClassData(this.classDataSearch)
    }
  },
  watch: {
    classDataLoading: {
      handler () {
        let tableList = this.classData[this.schoolId + this.gradeId]
        this.tableList = tableList && tableList.length > 0 ? tableList : []
      },
      immediate: true
    }
  }
}
</script>

<style lang="less">
#class-table {
  .point {
    cursor: pointer;
  }

  th {
    display: none !important;
  }

  .ivu-table-cell-expand {
    margin-left: 40px;
    visibility: hidden;
  }

  .ivu-table-row > td:first-child {
    border-right-width: 0px;
  }

  .button_round {
    font-size: 12px;
    color: #fff;
    margin-left: 2px;
    width: 20px;
    height: 20px;
    padding: 0 2px;
    border-radius: 20px;
  }

  .button_save {
    background-color: #d34b36;
    margin: 0 5px;
  }

  .button_close {
    background-color: #5ec134;
  }
}
</style>
