<template>
  <Layout id="class-roster-table">
    <Row>
      <Table
        :columns="columns"
        :data="classRoster.records"
        class="tableStyle"
        :loading="classRosteLoading"
      >
      </Table>
    </Row>
    <Row type="flex" justify="end">
      <Page
        :total="classRoster.totalCount"
        :current="classRoster.pageNo"
        show-total
        show-elevator
        @on-change="changePage"
      ></Page>
    </Row>
  </Layout>
</template>
<script>
import Vue from 'vue'
import { decodePhone } from 'talApi/student'
import { mapState, mapActions } from 'vuex'
import CustomerMixin from '@/components/mixin/customerDetail'
export default {
  name: 'classRoster',
  mixins: [CustomerMixin],
  data () {
    return {
      loading: false,
      tabelList: [],
      dic: {
        ifKol: {
          1: '否',
          0: '是'
        },
        preAt: {
          1: '在读',
          0: '未在读'
        }
      },
      columns: [
        {
          key: 'id',
          title: '序号',
          align: 'center'
        },
        {
          title: '学员',
          key: 'name',
          render: (h, { row }) => {
            return h('a', {
              on: {
                click: () => {
                  const stu = {
                    card: row.card,
                    cityCode: this.userInfo.cityCode
                  }
                  this.goCustomerDetail(stu)
                }
              }
            }, row.name || '--')
          }
        },
        {
          title: '手机号',
          key: 'tel',
          width: 150,
          render: (h, { row, columns, index }) => {
            return h('tal-decode-text', {
              props: {
                encodeText: row.tel,
                type: 'phone',
                uid: row.id
              }
            })
          }
        },
        {
          title: '注册日期',
          key: 'regDate',
          width: 180,
          render: (h, { row }) => {
            return h('span', row.regDate || '--')
          }
        },
        {
          title: '是否KOL',
          key: 'ifKol',
          render: (h, { row }) => {
            return h('span', this.dic['ifKol'][row.ifKol] || '--')
          }
        },
        {
          title: '当期在读',
          key: 'currAt',
          align: 'right',
          render: (h, { row }) => {
            return h('span', this.dic['ifKol'][row.currAt] || '--')
          }
        },
        {
          title: '上期在读',
          key: 'preAt',
          align: 'right',
          render: (h, { row }) => {
            return h('span', this.dic['preAt'][row.preAt] || '--')
          }
        },
        {
          title: '上上期在读',
          key: 'prePreAt',
          align: 'right',
          render: (h, { row }) => {
            return h('span', this.dic['preAt'][row.prePreAt] || '--')
          }
        },
        {
          title: '累计老带新量',
          key: 'total',
          align: 'right',
          render: (h, { row }) => {
            return h('span', row.total || '--')
          }
        }
      ]
    }
  },
  computed: {
    ...mapState('resource', ['classRoster', 'classRosterSearch', 'classRosteLoading']),
    ...mapState('roleRelativeData', ['userInfo'])
  },
  methods: {
    ...mapActions('resource', ['queryClassRoster']),
    changePage (num) {
      let params = {
        ...this.classRosterSearch,
        pageNum: num
      }
      this.queryClassRoster(params)
    },
    // 显示隐藏手机号
    async toggleMobile (item, index) {
      item.isNotFullPhone = !item.isNotFullPhone
      if (!item.tel) return
      const params = {
        uid: item.id,
        encodePhone: item.tel
      }
      try {
        const res = await decodePhone(params)
        Vue.set(this.classRoster.records, index, { ...item, fullTel: res })
      } catch (error) {
        this.$Message.error({ content: '手机号解码失败:' + error.message, duration: 5 })
      }
    }
  },
  watch: {
    '$route': {
      handler (val) {
        if (val.name === 'classRoster') {
          const { areaCode, schoolId, classOrder, gradeId } = val.query
          let params = {
            pageSize: 10,
            pageNo: 1,
            schoolId,
            classOrder,
            areaCode,
            gradeId,
            cityCode: this.userInfo.cityCode
          }
          this.queryClassRoster(params)
        }
      },
      immediate: true

    }
  }

}
</script>

<style lang="less">
#class-roster-table {
  background-color: #fff;
  padding: 20px;

  .tableStyle {
    border-radius: 5px;
    margin-bottom: 10px;
  }
  .ivu-table-header th,
  .ivu-table-body td {
    padding: 0 10px;
  }
}
</style>
